import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import { monitor, myToast } from '../../utils';
import moment from 'moment';
import classNames from 'classnames';
import styles from './result.less';
import * as tools from '../../utils/tools';
import { appConfig } from '../../config';
import { checkUserAuth } from '../../components';

const wxImg = appConfig.resources.defaultWX;

/**
 * 约课结果页
 */
@inject(({ resultBookStore }) => {
  return {
    course: resultBookStore.course,
    week: resultBookStore.week,

    getTeacherQRCode: resultBookStore.getTeacherQRCode,
    getCourseAbouts: resultBookStore.getCourseAbouts
  };
})
@observer
@checkUserAuth({ support: 'all', mode: 'base' })
export default class Result extends Component {
  constructor(props) {
    super(props);
    this.allow = true;
  }

  componentDidMount() {
    monitor.log('', '预约结果页面浏览');
    this.loadData();
  }

  loadData = () => {
    this.props.getTeacherQRCode();
    this.props.getCourseAbouts();
  };

  copy = (text, result) => {
    monitor.log('', '点击复制微信号');
    if (!this.allow) return;
    this.allow = false;
    if (result) {
      myToast.success('复制微信号成功', '1.5');
      setTimeout(() => {
        this.allow = true;
      }, 1500);
    } else {
      myToast.success('复制微信号失败', '1.5');
      setTimeout(() => {
        this.allow = true;
      }, 1500);
    }
  };

  render() {
    const { course, week } = this.props;
    return (
      <div className={styles.page}>
        <div className={classNames(styles.part, styles.part1)}>
          <div className={styles.title}>
            <span className={styles.icon}>01</span>预约成功
          </div>
          <div className={styles.con}>
            <p>
              <span>课程内容：</span>
              {course.courseName ? course.courseName : '北美外教直播课（适合4-12岁）'}
            </p>
            <p className={styles.timetitle}>
              <span>上课时间：</span>
            </p>
            <p className={styles.allTime}>
              {course.courseTime ? (
                course.courseTime.map((times, index) => {
                  const weekNum = moment(times).format(`e`);
                  return (
                    <span key={`${index}-day`} className={styles.time}>
                      {moment(times).format(`YYYY年MM月DD日 (周${week[weekNum]}) HH:mm`)}
                    </span>
                  );
                })
              ) : (
                <span className={styles.time}>无</span>
              )}
            </p>

            <p className={styles.wxchat}>
              <span>
                关注<span className={styles.text}>【魔力耳朵】</span>服务号继续约课或调整上课时间！
              </span>
            </p>
          </div>
        </div>
        <div className={classNames(styles.part, styles.part2)}>
          <div className={styles.title}>
            <span className={styles.icon}>02</span>添加课程老师微信
          </div>
          <div className={styles.con}>
            <p>提供个性化反馈</p>
            <p>解答学习问题</p>
            <div className={styles.wxIcon}>
              <img src={course.teacherQR ? course.teacherQR : wxImg} alt="无图片" />
            </div>
            <div className={styles.wxNo}>
              <span>微信号：</span>
              <span className={styles.wxText}>{course.wxNo ? course.wxNo : `mmears215`}</span>
            </div>
            {!tools.isWeiXin() ? (
              <CopyToClipboard text={course.wxNo || 'mmears215'} onCopy={this.copy}>
                <div className={styles.copy}>点击复制微信号</div>
              </CopyToClipboard>
            ) : (
              ''
            )}
          </div>
        </div>
        <div className={classNames(styles.part, styles.part3)}>
          <div className={styles.title}>
            <span className={styles.icon}>03</span>下载上课设备
          </div>
          <div className={styles.con}>
            <p>用电脑上课</p>
            <p>
              访问魔力耳朵官网<span>“mmears.com”</span>下载客户端
            </p>
            <p>用iPad上课</p>
            <p>
              打开App store 搜索<span>“魔力耳朵”</span>下载客户端
            </p>
          </div>
        </div>
      </div>
    );
  }
}
