import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import queryString from 'query-string';

import { checkUserAuth, DocumentTitle, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';

import { userService } from '../../service';
import { message, localStore, myToast, monitor, tools, logger } from '../../utils';
import { REISTER_RESULT_CODE } from '../../constants';
import { appConfig } from '../../config';
import styles from './register.less';

// 资源
const { regProtocolUrl } = appConfig.resources;

const phoneReg = /^1\d{10}$/;
const codeReg = /^[0-9]*$/;
const maxNumber = 60;

// 二级目录
const basename = appConfig.basename;
const dataType = 'funnel';
/**
 * 注册页面
 */
@connBasicLayout()
@checkUserAuth({ support: 'all', mode: 'base' })
class Register extends PureComponent {
  constructor(props) {
    super(props);

    this.urlParams = queryString.parse(location.search);
    this.rk = this.urlParams && this.urlParams.rk;
    this.rf = this.urlParams && this.urlParams.rf;
    this.act = this.urlParams && this.urlParams.act;
    this.allow = true; // 请接口状态
    this.state = {
      loading: false,
      codeState: false, // 获取验证码按钮状态
      captchaVisible: false, // 是否显示图形验证码
      sendMsgText: '获取验证码', // 获取短信按钮文本
      countDown: 0, // 发送验证码倒计时
      phone: '',
      code: '',
      password: '',
      icode: '' // 图形验证码
    };

    // 简单节流
    this.throttleHandleRegister = tools.throttle(this.handleRegister, 1000);
  }

  componentDidMount() {
    logger.info({
      message: '注册页面入口',
      dataType,
      rk: this.rk,
      rf: this.rf,
      act: this.act
    });
    // monitor.logMoreParam('', '注册页面入口');
  }

  componentWillUnmount() {
    clearInterval(this.countTimer);
  }

  handleInputChange = (key, e) => {
    this.setState({
      [key]: e.target.value
    });
  };

  /**
   * 点击注册
   */
  handleRegister = async () => {
    if (!this.allow) return false;
    const { phone, code } = this.state;
    if (!this.verifyData(phone, code)) {
      return false;
    }

    this.allow = false;
    logger.info({
      message: '验证码-点击注册按钮',
      dataType
    });
    // monitor.logMoreParam('', '注册-点击注册按钮');
    try {
      const result = await userService.register(phone, code);

      this.allow = true;
      if (result.code.toUpperCase() === 'OK') {
        localStore.setToken(result.data.userId, result.data.passToken);
        // monitor.log(phone, '注册成功');
        logger.info({
          message: '注册-注册成功',
          dataType,
          rk: this.rk,
          rf: this.rf,
          act: this.act
        });
        // monitor.logMoreParam(phone, '注册-注册成功');
        monitor.login();
        this.registerSucceed();
      } else {
        message.error(result.msg || '注册失败');
        monitor.log(phone, `注册失败 ${result.msg}`);
        // this.setState({countDown: 0});
      }
    } catch (error) {
      this.allow = true;
      message.error(error.msg || '注册失败');
      // monitor.log(phone, `注册失败 ${JSON.stringify(error)}`);
      monitor.logMoreParam(phone, `注册-注册失败 ${JSON.stringify(error)}`);
      // this.setState({countDown: 0});
    }
  };

  /**
   * 发送验证码短信
   */
  handleSendCode = () => {
    monitor.logMoreParam('', '验证码-点击获取验证码');
    if (!this.allow) return false;
    let { phone, captchaVisible, icode } = this.state;
    if (captchaVisible && !icode) {
      message.info('请输入验证码');
      return false;
    }
    this.setState({ loading: true });

    this.allow = false;
    userService
      .sendMessageCode(phone, icode)
      .then((data) => {
        this.allow = true;
        myToast.success('发送成功', 1500);
        logger.info({
          message: '验证码-获取验证码成功',
          dataType
        });
        // monitor.logMoreParam('', '验证码-获取验证码成功');
        this.startCountDown();
      })
      .catch((error) => {
        this.allow = true;
        let { loading } = this.state;
        loading = false;
        // 需要图片验证码验证
        switch (error.code) {
          // 手机号已经注册
          case REISTER_RESULT_CODE.PHONE_ALEARDY_REGISTED:
            captchaVisible = false;
            message.info(error.msg);
            break;
          // 需要输入图文验证码
          case REISTER_RESULT_CODE.INVALID_CODE:
            if (captchaVisible) {
              message.info('验证码错误');
            } else {
              captchaVisible = true;
            }
            break;
          default:
            message.error(error.msg || `获取验证码失败 ${error.msg}`);
            monitor.logMoreParam('', '验证码-获取验证码失败');
            break;
        }
        this.setState(
          {
            loading,
            captchaVisible
          },
          this.handleRefreshCode
        );
      });
  };

  /**
   * 开始验证码倒计时
   */
  startCountDown = () => {
    this.setState({
      loading: false,
      modalKey: '',
      countDown: maxNumber,
      sendMsgText: '秒后重新获取',
      errortip: (
        <small>
          短信已经发送<span>30</span>分钟内有效
        </small>
      )
    });

    // 倒计时
    this.countTimer = setInterval(() => {
      const { countDown } = this.state;
      if (countDown === 1) {
        clearInterval(this.countTimer);
      }
      this.setState({
        countDown: countDown - 1
      });
    }, 1000);
  };

  /**
   * 注册成功后执行
   */
  registerSucceed = () => {
    // 注册成功后清楚rf码
    localStore.remove('rf');

    let { callback } = this.urlParams;
    let isBooking = false;
    if (callback) {
      const { url } = queryString.parseUrl(callback);
      const { pathname } = tools.parseUrl(url);
      const reg = ['user/booking', 'user/bonus', 'user/home', 'user/special_booking'];
      isBooking = reg.includes(pathname);
    }

    setTimeout(() => {
      if (callback && !isBooking) {
        callback = decodeURIComponent(callback);
        window.location.replace(callback);
      } else {
        window.location.replace(`${basename}/survey?${tools.getSearchParam(window.location.href)}`);
      }
    }, 300);
  };

  /**
   * 保存前校验用户输入
   */
  verifyData = (phone, code) => {
    if (!phone) {
      message.info('手机号不能为空');
      return false;
    }
    if (!phoneReg.test(phone)) {
      message.info('请输入正确的手机号');
      return false;
    }
    if (!code) {
      message.info('请输入正确验证码');
      return false;
    }
    if (!codeReg.test(code)) {
      message.info('验证码为数字格式');
      return false;
    }
    return true;
  };

  handleHideModal = () => {
    this.setState({
      modalKey: ''
    });
  };

  /**
   * 刷新图片验证码
   */
  handleRefreshCode = () => {
    const { captchaVisible } = this.state;
    if (captchaVisible) {
      this.refs.captchaImg.src = '/captcha/get?t=' + Math.random();
    }
  };

  /**
   * render发送验证码按钮
   */
  renderSendMsgBtn = () => {
    const { phone, countDown } = this.state;
    const sendMsgText = countDown < 1 ? '获取验证码' : '秒后重新获取';
    if (phoneReg.test(phone) && countDown < 1) {
      return (
        <div className={classNames(styles.sendMsgBtn, styles.active)} onClick={this.handleSendCode}>
          {sendMsgText}
        </div>
      );
    }
    return (
      <div className={styles.sendMsgBtn}>{countDown ? countDown + sendMsgText : sendMsgText}</div>
    );
  };

  /**
   * render图片验证码
   */
  renderCaptchaCode = () => {
    const { icode, captchaVisible } = this.state;
    if (!captchaVisible) return null;
    return (
      <div className={classNames(styles.setItem, styles.icodeWrap, 'clearfix')}>
        <label />
        <input
          type="text"
          maxLength="4"
          value={icode}
          onChange={this.handleInputChange.bind(null, 'icode')}
          placeholder="请输入图片验证码"
          autoComplete="off"
        />
        <div className={styles['captchaImg']} onClick={this.handleRefreshCode}>
          <img src="" ref="captchaImg" alt="" />
        </div>
      </div>
    );
  };

  handleFocus = (ref) => () => {
    ref === 'phone'
      ? monitor.logMoreParam('', '注册-点击输入手机号码')
      : monitor.logMoreParam('', '注册-点击输入密码');
  };

  render() {
    const { phone, code, countDown } = this.state;
    return (
      <div className={styles.regWrapper}>
        <header className={styles.header}>
          <div className={styles.brand}></div>
        </header>
        <div className={styles.container}>
          <div className={styles.content}>
            <div className={styles.section}>
              <div className={classNames(styles.setItem, styles.phoneWarp, 'clearfix')}>
                <label></label>
                <input
                  type="tel"
                  readOnly={countDown < 1 ? false : true}
                  maxLength="11"
                  value={phone}
                  onChange={this.handleInputChange.bind(null, 'phone')}
                  placeholder="手机号码"
                  autoComplete="off"
                  onFocus={this.handleFocus('phone')}
                />
              </div>
              {this.renderCaptchaCode()}
              <div className={classNames(styles.setItem, styles.codeWrap, 'clearfix')}>
                <label />
                <input
                  type="text"
                  maxLength="4"
                  value={code}
                  pattern="[0-9]*"
                  onChange={this.handleInputChange.bind(null, 'code')}
                  placeholder="验证码"
                  autoComplete="off"
                />
                {this.renderSendMsgBtn()}
              </div>
            </div>
            <div className={styles.function}>
              <div className={styles.submitBtn} onClick={this.throttleHandleRegister}>
                免费领取198元学习大礼包
              </div>
              <p className={styles.loginLink}>
                <Link to={`/login?${tools.getSearchParam(window.location.href)}`}>
                  已有账号，<span style={{ color: '#FED501' }}>直接登录</span>
                </Link>
              </p>
            </div>
          </div>
          <div className={styles.footer}>
            <p>
              注册即视为同意
              <a href={regProtocolUrl}>《魔力耳朵用户注册协议》</a>
            </p>
          </div>
        </div>
        <DocumentTitle title="注册" />
        <WechatConfig />
      </div>
    );
  }
}

export default Register;
