import {observable, action} from 'mobx';
import {forwardingService} from '../service';
import {SCREENSHOT_RECORD} from '../constants';
import {message, monitor} from '../utils';

class ForwardingStore {
    // 是否可以上传朋友圈截图，及上传后的审核状态
    @observable shareInfo = {
        show: '', // 是否能上传朋友圈截图的限制，‘error’接口错误，noCourse本月未上课，noPay未付费用户，OK可正常上传。
        timelineImg: null, // 上传的朋友圈截图
        status: null, // 审核进度
        refuseReason: null, // 审核失败原因
        magicCoinAmount: 0, // 审核成功添加的魔力币
    };
    // 模态框及上传进度
    @observable state = {
        modal: '', // 控制模态框
        progress: 0, // 上传进度
    };
    // 更改模态框
    @action showModal = (modal) => {
        if (modal === 'uploading') {
            this.modifyProgress(0);
        }
        this.state.modal = modal;
    }
    // 修改上传进度
    @action modifyProgress = (value) => {
        this.state.progress = value;
    }
    // 进入页面后初始化数据
    @action initData = async () => {
        try {
            // message.showLoading();
            let res = await forwardingService.getSubmissionRecord();
            let {timelineImg, status, refuseReason, magicCoinAmount} = res.data || {};
            let show = 'OK';
            if (typeof res.data === 'string') {
                show = SCREENSHOT_RECORD[res.data];
            }
            this.shareInfo = {
                ...this.shareInfo,
                show,
                timelineImg: timelineImg,
                status,
                refuseReason,
                magicCoinAmount,

            };
            message.closeLoading();

        } catch (error) {
            console.log(error);
            message.closeLoading();
            if (error.code === 'LOGIN_REQUIRED') return;
            this.shareInfo.show = 'error';
            monitor.log('', `去约课接口报错：${error && error.msg}`);
        }
    }
}
export default new ForwardingStore();
