import React from 'react';
import Hammer from 'react-hammerjs';
import classNames from 'classnames';
import { observer, inject } from 'mobx-react';
import html2canvas from 'html2canvas';
import qs from 'query-string';
import WX from 'weixin-js-sdk';
import IScroll from 'iscroll';

import { checkUserAuth, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';

import { resourceService } from '../../service';
import CpModal from './cp-modal';
import { tools, message } from '../../utils/';
import styles from './index.less';
import monitor from '../../utils/monitor';
import * as wechatUtils from '../../utils/wechat-utils';
import { appConfig } from '../../config';

import tipsSrc from '../../public/images/tips.png';

const SPEED = 2;

/**
 * @description 推广链接
 */
let $x = 0;
let $y = 0;

@inject(({ globalStore }) => ({
  promotion_file: globalStore.promotion_file
}))
@observer
@connBasicLayout()
@checkUserAuth({ mode: 'base' })
class PromotionDetail extends React.Component {
  state = {
    currentX: 0,
    currentY: 0,
    isFirstUpload: false,
    modalKey: 'tips',
    poster: '', // 用户上传的照片
    realImg: '',
    templateList: [], // 海报模板列表
    currentBgIndex: 0,
    overLoaded: true
  };

  componentDidMount() {
    monitor.log('', '浏览制作海报页');
    tools.setTitle('个性化海报');

    // 加载海报模板数据
    this.loadPosterData();
  }

  componentWillUnmount() {
    $x = 0;
    $y = 0;
  }

  /**
   * 加载海报模板列表数据
   */
  loadPosterData = async () => {
    const { promotion_file } = this.props;
    if (!promotion_file) {
      // 刷新后跳转到海报合成入口页
      window.location.replace(`${appConfig.basename}/promotion`);
      return;
    }
    try {
      // 获取海报模版列表数据
      const templateList = await resourceService.getFramePosterList();
      this.swipper = new IScroll(`.${styles.swipper}`, {
        scrollX: true,
        scrollY: false,
        bindToWrapper: true,
        tap: true,
        click: true
      });
      const { current = 1 } = qs.parse(location.search);
      // 图片转成blob
      const blobUrl = await tools.getImageBlob(promotion_file);
      this.setState({
        templateList,
        currentBgIndex: isNaN(Number(current)) ? 1 : Number(current),
        poster: blobUrl
      });
      this.initPoster();
    } catch (error) {
      console.error(error);
    }
  };

  /**
   * @description 隐藏或显示modal
   * @param value key值
   * @return {Function}
   */
  handleSetModalKey = (value = '') => (event) => {
    this.setState({
      modalKey: value
    });
    if (event) {
      event.stopPropagation && event.stopPropagation();
      event.nativeEvent && event.nativeEvent.stopImmediatePropagation();
    }
  };

  // 初始化海报元素 和 宽高
  initPoster = () => {
    if (this.$el) return;
    this.$el = document.querySelector('#poster');
    if (this.$el) {
      this.setState({
        overLoaded: false,
        width: this.$el.offsetWidth || 300,
        height: this.$el.offsetHeight
      });
    }
  };

  handleOnPan = (event) => {
    if (event.maxPointers > 1) return;
    if (event.type === 'panend') {
      $x = this.state.currentX;
      $y = this.state.currentY;
      return;
    }

    if (event.type === 'pan' || event.type === 'panstart') {
      this.setState({
        currentX: $x + event.deltaX,
        currentY: $y + event.deltaY
      });
    }
    return false;
  };

  handlePinch = (e) => {
    e.preventDefault();
    this.initPoster();
    const width = this.$el.offsetWidth;
    const height = this.$el.offsetHeight;
    const rale = width / height;

    if (e.type === 'pinchin') {
      this.setState((state) => ({
        currentX: state.currentX + SPEED / 2,
        currentY: state.currentY + SPEED / 2,
        width: width - SPEED,
        height: `${(width - SPEED) / rale}px`
      }));
    }

    if (e.type === 'pinchout') {
      this.setState((state) => ({
        currentX: state.currentX - SPEED / 2,
        currentY: state.currentY - SPEED / 2,
        width: width + SPEED,
        height: `${(width + SPEED) / rale}px`
      }));
    }
    return false;
  };

  handleUpload = async () => {
    const context = this;
    monitor.log('', '点击换张图片');
    if (this.state.isFirstUpload) {
      this.setState({
        isFirstUpload: false
      });
      this.handleSetModalKey('tips')();
    }

    wechatUtils.chooseImage({
      callback(files) {
        WX.getLocalImgData({
          localId: files[0],
          async success(res) {
            let localData = res.localData;

            if (window.__wxjs_is_wkwebview) {
              // IOS
              localData = localData.replace('jgp', 'jpeg');
            } else {
              // 安卓
              localData = 'data:image/jpeg;base64,' + localData.replace(/\n/g, '');
            }

            const { url } = await resourceService.choiceImageUpload(localData);
            const isEntry = !!url;
            // 本地图片上传服务端响应
            if (isEntry) {
              context.setState({
                poster: url
              });
            }
          }
        });
      }
    });
  };

  renderModal = () => {
    const { modalKey, overLoaded } = this.state;
    switch (modalKey) {
      case 'loading': {
        return (
          <div className={styles.loading}>
            <div>
              <img src={tipsSrc} alt="" />
              <p>海报生成中...</p>
            </div>
          </div>
        );
      }
      case 'tips': {
        return (
          <div className={styles.tips}>
            <div className={styles.mask} />
            <div className={styles.content}>
              <img src={tipsSrc} alt="" />
              <p>将图片放大或缩小并移动到合适位置</p>
              <button
                disabled={overLoaded}
                className={styles.makeBtn}
                onClick={() => {
                  monitor.log('', '点击我知道了');
                  this.handleSetModalKey()();
                }}
              >
                我知道了
              </button>
            </div>
          </div>
        );
      }
      case 'cp': {
        return <CpModal onCancel={this.handleSetModalKey()} />;
      }
      default: {
        return null;
      }
    }
  };

  /**
   * @description 显示分享
   */
  renderShareContent = () => {
    const { realImg } = this.state;
    return (
      <div className={styles['share-content']}>
        <img src={realImg} alt="" />
        <div className={styles.tip}>长按保存到手机相册</div>
        <button className={styles.makeBtn} onClick={this.handleSetModalKey('cp')}>
          获取分享内容发朋友圈
        </button>
        {this.renderModal()}
      </div>
    );
  };

  /**
   * @description 发送数据到后台 获取合成后的图片 注释
   */
  handleSubmit = async () => {
    const { currentBgIndex, templateList } = this.state;
    const order = templateList[currentBgIndex] && templateList[currentBgIndex]['order'];
    monitor.log('', `合成海报, 点击生成海报按钮, currentBgIndex=${currentBgIndex} order=${order}`);
    const el = document.querySelector(`.${styles.ownerWrapper}`);
    const frameImgEl = el.children[1];
    this.handleSetModalKey('loading')();

    try {
      monitor.log('', '合成海报交互接口获取模版签名资源');
      let { url, msg } = await resourceService.getImgByOrder(order);
      monitor.log('', `合成海报后台响应: ${JSON.stringify(url)}`);
      const isEntry = !!url;
      if (isEntry) {
        url = url + '?v=' + Number(Date.now());
        // 图片url转成blob格式
        const blobUrl = await tools.getImageBlob(url);
        // 资源地址替换
        frameImgEl.src = blobUrl;
        // 将容器dom使用canvas画出来
        monitor.log('', '开始 html2canvas');
        const canvas = await html2canvas(el, {
          useCORS: true,
          logging: true,
          backgroundColor: 'transparent'
        });
        // // !!!这里需要二次调用 html2canvas, IOS低版本合成有兼容问题, 比如IOS10
        // // !!!二次调用后就可以解决, 很诡异的问题
        // canvas = await html2canvas(el, {
        //     useCORS: true,
        //     logging: true,
        //     backgroundColor: 'transparent'
        // });
        const realImg = canvas.toDataURL('image/png');
        monitor.log('', `结束 html2canvas`);
        // // 将canvas 转换为base64 并且同步状态
        this.setState(
          {
            realImg
          },
          () => {
            window.scrollTo(0, 0); // 生成海报后 滚动条归0
          }
        );
      } else {
        message.error(msg);
        monitor.log('', `合成海报后台响应失败: ${JSON.stringify(msg)}`);
      }
    } catch (error) {
      message.error(error.msg || '网络错误');
      console.error(error);
      monitor.log('', `合成海报失败: ${JSON.stringify(error)}`);
    }

    // 关闭加载loading
    this.handleSetModalKey()();
  };

  /**
   * @description 点击切换海报
   * @param key
   */
  handleTogglePoster = (key) => {
    const { currentBgIndex } = this.state;
    if (key === currentBgIndex) {
      return;
    }

    monitor.log('', '切换海报模板');
    this.setState({ currentBgIndex: key });
  };

  // 阻止默认事件
  stopImmediatePropagation = (event) => {
    event.stopPropagation && event.stopPropagation();
    event.nativeEvent.stopImmediatePropagation();
    return false;
  };

  render() {
    const { currentX, currentY, width, templateList, realImg, currentBgIndex, poster } = this.state;
    if (realImg) {
      return this.renderShareContent();
    }
    const styleSheet = {
      left: `${currentX}px`,
      top: `${currentY}px`,
      width: `${width}px`
    };
    return (
      <div className={styles.promotion}>
        {this.renderModal()}
        <Hammer
          options={{
            recognizers: {
              pinch: {
                enable: true,
                threshold: 0
              },
              pan: { threshold: 0 }
            }
          }}
          onPanStart={this.handleOnPan}
          onPanEnd={this.handleOnPan}
          onPan={this.handleOnPan}
          onPanCancel={this.handleOnPan}
          onPinch={this.handlePinch}
          onPinchIn={this.handlePinch}
          onPinchOut={this.handlePinch}
        >
          <div
            className={classNames(styles.ownerWrapper, styles.switch)}
            onClick={this.stopImmediatePropagation}
          >
            <div className={styles.defaultBg} />
            <img
              className={styles.poster_bg}
              onClick={this.stopImmediatePropagation}
              src={templateList[currentBgIndex] && templateList[currentBgIndex]['url']}
              alt=""
              crossOrigin="anonymous"
            />
            <div className={styles.wrapper}>
              <img
                src={poster}
                alt=""
                id="poster"
                onClick={this.stopImmediatePropagation}
                style={styleSheet}
                className={styles.poster}
                crossOrigin="anonymous"
              />
            </div>
          </div>
        </Hammer>
        <div className={styles.swipper}>
          <div>
            {templateList.map((v, k) => (
              <div key={v['order']} onClick={this.handleTogglePoster.bind(null, k)}>
                <img
                  src={v['url']}
                  alt=""
                  className={classNames('lazyload', { [styles.active]: currentBgIndex === k })}
                />
              </div>
            ))}
          </div>
        </div>
        <div className={styles.footer}>
          <div className={styles.changeWrapper}>
            <button
              className={classNames(styles.makeBtn, styles.disabled)}
              onClick={this.handleUpload}
            >
              换张照片
            </button>
          </div>
          <button className={styles.makeBtn} onClick={this.handleSubmit}>
            生成海报
          </button>
        </div>
        <WechatConfig />
      </div>
    );
  }
}

export default PromotionDetail;
