import React from 'react';
import propTypes from 'prop-types';
import classNames from 'classnames';

import styles from './index.less';

/**
 * @description 按钮样式的radio
 */
export default class RadioBtnGroup extends React.PureComponent {
    static propTypes = {
        name: propTypes.string,
        onChange: propTypes.func,
        value: propTypes.string,
        dataSource: propTypes.array,
        itemClass: propTypes.string,
        className: propTypes.string,
    };

    static defaultProps = {
        onChange: (event) => {
            console.log('onchange', event);
        },
    };

    state = {

    };

    handleChange = (value) => {
        this.props.onChange(value.value, value);
    };

    render() {
        const {dataSource, itemClass, value, className} = this.props;
        return (
            <div className={classNames(styles.radioBtnGroup, className)}>
                {
                    dataSource.map(v => (
                        <div
                            key={v.value}
                            className={classNames(styles.radioItem, itemClass, {
                                [styles.checked]: v.value === value
                            })}
                            onClick={this.handleChange.bind(null, v)}
                        >
                            <p>{v.name}</p>
                        </div>
                    ))
                }
            </div>
        );
    }
}
