import {observable, action} from 'mobx';

import {BONUS_DEFAULT_TRADE_TYPE} from '../constants';
import {bonusService} from '../service';

// 魔力币状态
class BonusStore {
    @observable bonusInfo = { // 魔力币 基本信息
        bonusPoint: 0, // 正常魔力币数量
        bonusFreezePoint: 0, // 冻结魔力币数量
        bonusIncome: 0, // 收入
        bonusExpend: 0, // 支出
    }

    @observable param = { // 获取正常魔力币数量接口 请求参数
        tradeType: [BONUS_DEFAULT_TRADE_TYPE], // 交易类型 默认全部
        tradeDate: '', // 全部交易时间 默认为空
        pageIndex: 1, // 当前列表页码 默认第一页
        pageSize: 10, // 每页条数 默认每页10条
    };

    // 获取正常魔力币的数量
    @action getBonusPoint = async () => {
        try {
            const {balance: bonusPoint, frozen: bonusFreezePoint} = await bonusService.getBonusCount();

            // 同步正常魔力币数量
            this.bonusInfo = {...this.bonusInfo, bonusPoint, bonusFreezePoint};
        } catch (e) {
            console.error(e);
        }
    }

    // 获取冻结魔力币的数量
    // @action getBonusFreezePoint = async () => {
    //     try {
    //         const bonusFreezePoint = await bonusService.getBonusFreezePoint();

    //         // 同步冻结魔力币数量
    //         this.bonusInfo = {...this.bonusInfo, bonusFreezePoint};
    //     } catch (e) {
    //         console.error(e);
    //     }
    // }

    // 获取正常魔力币列表数据
    @action getBonusPointList = async (tradeType, tradeDate, pageIndex, pageSize) => {
        const {tradeType: defaultTradeType, pageIndex: defaultPageIndex, pageSize: defaultPageSize} = this.param;

        // 查询参数
        this.param = {
            tradeType: tradeType ? tradeType : defaultTradeType,
            tradeDate: tradeDate,
            pageIndex: pageIndex ? pageIndex : defaultPageIndex,
            pageSize: pageSize ? pageSize : defaultPageSize,
        };

        try {
            const {income: bonusIncome, expend: bonusExpend, bonusResultDetailDtos: {records = [], total = 0}} = await bonusService.getBonusLogList(this.param);
            // 同步魔力收支数据状态
            this.bonusInfo = {...this.bonusInfo, bonusIncome, bonusExpend};

            return Promise.resolve({data: records, total});
        } catch (e) {
            return Promise.reject(e);
        }
    }

    // 获取冻结魔力币的数量
    @action getBonusFreezePointList = async (pageIndex = 1, pageSize = 10) => {

        try {
            const {records = [], total = 0} = await bonusService.getBonusFreezeLogList(pageIndex, pageSize);

            return Promise.resolve({data: records, total});
        } catch (e) {
            return Promise.reject(e);
        }
    }
}

export default new BonusStore();
