import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import queryString from 'query-string';
import classNames from 'classnames';

import {localStore, tools} from '../../utils';
import layoutStyles from './layout.less';

/**
 * 默认属性
 * @type {{useFooter: boolean, backgdName: string}}
 */
const defaultOptions = {
    useFooter: false, // 默认不需要footer
    backgdName: '', // 全局样式类名称
};

/**
 * 使用母版页
 * @param options
 * @returns {function(*): InnerBasicLayout}
 */
export default function connBasicLayout(options) {

    // 处理参数
    options = options || {};
    options = {
        ...defaultOptions,
        ...options,
    };

    return (WrappedComponent) => {
        @withRouter
        class InnerBasicLayout extends Component {
            constructor(props) {
                super(props);

                this.urlParams = queryString.parse(window.location.search);

                // 小程序跳转过来携带的wxtoken
                const {miniWxToken = ''} = this.urlParams;

                console.log('miniWxToken>>', miniWxToken);
                if (miniWxToken) {
                    localStore.setWechatToken(miniWxToken);
                }
            }

            componentDidMount() {
                this.setRecommend();
                if (tools.isWeiXin() && tools.isIOS() && !document.body.focusin) {
                    document.body.addEventListener('focusin', this.handleIOSFocusIn);
                    document.body.addEventListener('focusout', this.handleIOSKeyboard);
                }
            }

            componentWillUnmount() {
                if (tools.isWeiXin() && tools.isIOS() && document.body.focusin) {
                    document.body.removeEventListener('focusin', this.handleIOSFocusIn);
                    document.body.removeEventListener('focusout', this.handleIOSKeyboard);
                }
                if (this.time) {
                    clearTimeout(this.time);
                }
            }

            /**
             * 解决IOS软键盘呼出之后, 按钮事件失效
             */
            handleIOSKeyboard = () => {
                let scrollTop = parseInt(document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop, 10); // 获取滚动条值当前值
                this.time = setTimeout(() => {
                    window.scrollTo(0, scrollTop);
                }, 100);
            };

            handleIOSFocusIn = () => {
                if (this.time) {
                    clearTimeout(this.time);
                }
            };

            /**
             * 设置推荐码
             */
            setRecommend = () => {
                const {rf} = this.urlParams;
                if (rf) {
                    localStore.set('rf', rf);
                }
            };

            render() {
                // 如果使用母版页的底部导航
                const {useFooter = false, backgdName} = options;
                if (useFooter) {
                    return (
                        <div id="layout" className={classNames(layoutStyles.useFooterlayout, layoutStyles[backgdName])}>
                            <WrappedComponent {...this.props}/>
                        </div>
                    );
                }

                return (
                    <div className={layoutStyles.noFooterLayout}>
                        <WrappedComponent {...this.props}/>
                    </div>
                );
            }
        }

        return InnerBasicLayout;
    };
}
