import jsCookies from 'js-cookie';

import { fetchUtils, localStore } from '../utils';
import { appConfig } from '../config';

const baseDomain = appConfig.domain.baseDomain;
const mobileDomain = appConfig.domain.mobileDomain;

/**
 * 登录接口
 * @param {*} phone
 * @param {*} password
 */
export const login = (phone, password) => {
  const url = baseDomain.concat('/usercenter/login');
  const params = {
    phone,
    password
  };
  return fetchUtils.post(url, params, { includeToken: false }).then((res) => res.data);
};

/**
 * @description 清除cookie
 */
export const clearCookie = () => {
  const url = mobileDomain.concat('/id/clearCookie');
  return fetchUtils.get(url, {}, { successCode: 0 });
};

/**
 * 退出登录
 * @returns {*|Promise<any>}
 */
export const logout = () => {
  const url = baseDomain.concat('/usercenter/logout');
  return fetchUtils.post(url);
};

/**
 * 当微信token存在时, 根据微信token换用户token
 * @returns {*|Promise<T>}
 */
export const getUserTokenByWechatToken = () => {
  const url = baseDomain.concat('/usercenter/getUserToken');
  return fetchUtils.get(url, null, { includeToken: false }).then((res) => res.data);
};

/**
 * 注册接口
 * @param {*} phone
 * @param {*} code
 */
export const register = (phone, code) => {
  const url = baseDomain.concat('/usercenter/register/phone');
  const rf = localStore.get('rf');
  const params = {
    phone,
    code
  };
  if (rf) params.rf = rf;
  return fetchUtils.post(url, params, { includeToken: false });
};

/**
 * 发送短信验证码
 * @param {*} phone
 * @param {*} vcode
 */
export const sendMessageCode = (phone, icode) => {
  const url = baseDomain.concat('/usercenter/register/code');
  const ick = jsCookies.get('ick');
  const params = {
    phone,
    icode,
    ick
  };
  return fetchUtils.post(url, params, { includeToken: false }).then((res) => res.data);
};

