import React from 'react';
import {Carousel} from 'antd-mobile';
import PropTypes from 'prop-types';
import {Player, BigPlayButton} from 'video-react';

import {tools} from '../../utils';

import playIcon from '../../public/images/special/video-play.png';
import closeVideoIcon from '../../public/images/special/close.png';

import styles from './special-detail.less';
import 'video-react/dist/video-react.css'; // import css

/**
 * Banner 组件
 * @param data
 */
class Banner extends React.PureComponent {
    static propTypes = {
        data: PropTypes.array
    };

    static defaultProps = {
        data: []
    };

    constructor(props) {
        super(props);
        this.state = {
            loading: false, // 加载loading
            autoPlay: true,
            videoUrl: '',
            showVideo: false
        };
    }

    /**
     * @description 播放视频
     */
    playVideo = (url) => {
        this.setState({
            showVideo: true,
            videoUrl: url,
        });
    };

    /**
     * @description 关闭视频
     */
    closeVideo = () => {
        this.setState({
            showVideo: false,
            videoUrl: ''
        });
    };

    /**
     * @description 渲染视频模态框
     * @return {*}
     */
    renderVideo = () => {
        const {showVideo, videoUrl} = this.state;
        if (showVideo) {
            return <div className={styles.videoWrapper}>
                <div className={styles.video}>
                    <Player
                        autoPlay
                        poster={tools.getOssVideoPoster(videoUrl)}
                        src={videoUrl}
                    >
                        <BigPlayButton position="center"/>
                    </Player>
                    <div className={styles.close} onClick={this.closeVideo}>
                        <img src={closeVideoIcon} alt={'close'}/>
                    </div>
                </div>
            </div>;
        }
        return null;
    };

    /**
     * 次级渲染banner图片
     */
    renderBannersImg = () => {
        // banner列表数据
        let {data} = this.props;
        return data.map((banner, key) => {
            let element = null;
            switch (banner.type.toUpperCase()) {
                case 'VIDEO':
                    element = (<div
                        key={`banner-${key}`}
                        className={styles.bannerItem}
                        onClick={this.playVideo.bind(null, banner.url)}
                    >
                        <img
                            className={styles.poster}
                            alt={'poster'}
                            src={tools.getOssVideoPoster(banner.url)}
                        />
                        <img className={styles.play} src={playIcon} alt={''}/>
                    </div>);
                    break;
                default:
                    element = <div
                        key={`banner-${key}`}
                        className={styles.bannerItem}
                    >
                        <img
                            key={`banner-${key}`}
                            alt={'banner'}
                            className={styles.poster}
                            src={banner.url}
                        />
                    </div>;
                    break;
            }
            return element;
        });
    };

    /**
     * 渲染banner
     */
    render() {
        const {loading, autoPlay} = this.state;
        if (loading) {
            return (<div className={styles.containers}>
                <div className={styles['m-load2']}>
                    <div className={styles['line']}>
                        <div/>
                        <div/>
                        <div/>
                        <div/>
                        <div/>
                        <div/>
                    </div>
                    <div className={styles['circlebg']}/>
                </div>
            </div>);
        }

        // 渲染banner
        return (
            <div className={styles['special-banner']}>
                {this.renderVideo()}
                <Carousel
                    autoplay={autoPlay}
                    infinite
                    speed={500}
                    autoplayInterval={4000}
                    dotStyle={{
                        'width': '0.266667rem',
                        'height': '0.106667rem',
                        'borderRadius': '0.053333rem',
                        'background': '#fff'
                    }}
                    dotActiveStyle={{
                        'width': '0.533333rem',
                        'height': '0.106667rem',
                        'borderRadius': '0.053333rem',
                        'background': '#FECC49'
                    }}
                >
                    {this.renderBannersImg()}
                </Carousel>
            </div>
        );
    }

}

export default Banner;
