import React from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import classNames from 'classnames';

import {message} from '../../utils';


import styles from './index.less';

/**
 * @description 复制模板提示框
 */
export default class CpModal extends React.Component {
    state = {
        textIndex: 0,
        list: [
            `想学好英语，有啥窍门吗？想英语成绩突飞猛进，有啥窍门吗？想送孩子出国，带他去国外旅游，让他和外国人聊天，有啥学英语的捷径吗？都没有！只有尽早学，熟悉语境，克服恐惧，像学中文一样多说多听多练。现在我家孩子在魔力耳朵学，1对4小班，效果好性价比超高，扫码能领纯北美的外教少儿英语课让孩子试试，也许就入门了呢。`,
            `孩子在魔力耳朵已经学习了十多节课了，感觉孩子变化还是挺大的，从刚开始的有压力不敢张口去说，到现在已经对上课没有压力了而且口语也有了明显的提升，相信并感谢魔力耳朵的每一位老师，有朋友想试听的可以扫下面的二维码领取，差不多1个月就可以看到孩子的变化了。`,
            `加入魔力耳朵已经一个多月时间了，宝贝每个小阶段的进步我都可以通过中教的反馈可以看到。从第一节课陌生的上课形式、老师、和纯英文的环境懵懵的听不懂，宝贝是有点排斥的，跟他很认真的做了思想工作宝贝才愿意试一试。经过十几节课，上课过程中老师不停的鼓励和表扬，让宝贝越来越有自信并且喜欢主动用英文抢答老师提的问题，也找到了自己喜欢的老师，现在越来越喜欢上英语课了，棒棒哒！我们继续保持，加油宝贝！扫描下面二维码可以领取一节体验课，免费试听！`,
            `最近发现孩子的很多好朋友也开始上魔力耳朵英语课了，孩子和外教互动的确实很好，自己也很喜欢课堂，也免去了送线下班的来回接送，从听说读写四个方面对于孩子的提升点，整体都很满意。有兴趣的家长扫码免费领取体验课，先给孩子试试。`,
            `自从体验完魔力耳朵的课程，孩子每次上课都很积极，很感兴趣，能够在二十五分钟的时间里，全程配合老师，与外教互动，这让我很欣慰，感谢魔力耳朵！打算让家里小朋友和纯北美的外教学习英语可以扫码先试听一下哦~`,
            `为了给孩子选英语课，我们试听过线下课程（上课时间固定，来回接送费时费力），试过1对1的线上课，不适合我家孩子，尤其是比较腼腆的孩子。试听下来觉得还是魔力耳朵比较适合，小班课，1对4的课程设置特别适合零基础的孩子，就算一时没听懂，通过其他小伙伴回答也能听懂了，孩子擅长模仿，就学会了。老师就是北美外教，发音纯正，上课活泼。上课时间就是自己安排时间随时预约，打开电脑或者iPad就能上课真是太方便了！妞儿上了几节课觉得还不错，慢慢跟着老师开口了，出去玩看见外国人就想上去打招呼。只要想说敢说就是进步！为娘很欣慰了！`,
            `闺女在魔力耳朵上课有三个月了，刚开始看朋友家的孩子在这学，就赶紧也给孩子报上了，但没想到效果竟然这么好，闺女每次上课都特别认真，跟着外教老师大声读大声说，这段时间下来英语进步不是一般的大啊，关键她居然不会腻烦，还天天叨唠着要上兔子课,感兴趣的妈妈也可以带娃试试呀，扫描二维码，就能获得一节免费试听课~`,
            `现在的英语学习平台多而不精，为了不错过孩子学英语的黄金时期，试听了好几家最后选了魔力耳朵，看上他家一对四小班课，感觉对我家这低龄孩子来说，学习最重要的是有兴趣有动力，四个小同伴一起学习，有竞争力，孩子学习的那股劲儿迸发出来了，良心推荐圈里的老母亲带娃来试试，真不错`,
            `魔力耳朵的课件越来越丰富了，儿歌预热、内容生动，各种小游戏，老师也特别热情幽默，妥妥的吸引住了我家哥哥，玩着玩着就学会了好多英语单词和句子，课后还会时不时的蹦出一句，当妈的都没办法接话了，哈哈！现在扫码就能免费试听一节北美外教课哈`,
            `在魔力耳朵学习半年了，孩子的进步是看得见的，从上学期期末考试80多分提高到了98分，孩子说：妈妈，我在魔力耳朵学习还真的挺有用的！我现在学英语一点儿都不犯怵，还越来越喜欢了！魔力耳朵，真的没选错，现在扫图片里的二维码，能免费领一节北美外教1对4小班课，好不好试试才知道！`,
        ]
    };

    /**
     * @description 切换分享内容
     */
    handleChangeIndex = () => {
        const {textIndex, list} = this.state;
        let len = list.length;
        let next;
        if (textIndex + 1 >= len) {
            next = 0;
        } else {
            next = textIndex + 1;
        }
        this.setState({
            textIndex: next
        });
    };

    renderCopyButton = () => {
        const {list, textIndex} = this.state;
        return (
            <div>
                <CopyToClipboard
                    text={list[textIndex].replace(/<br\/>/g, '\n')}
                    onCopy={() => message.info('复制成功!')}
                >
                    <button className={classNames(styles.makeBtn, styles.copy)}>复制分享内容发朋友圈</button>
                </CopyToClipboard>
            </div>
        );
    }

    render() {
        const {list, textIndex} = this.state;
        const {onCancel} = this.props;
        return (
            <div className={styles['cp-modal']}>
                <div className={styles.mask} onClick={onCancel}/>
                <div className={styles.card}>
                    <div className={styles['body-bg']}/>
                    <div className={styles.body}>
                        <p className={styles.title}>用心的分享内容能提高成功率哦~</p>
                        <p className={styles.content} dangerouslySetInnerHTML={{__html: list[textIndex]}}>
                        </p>
                        <div className={styles.refresh} onClick={this.handleChangeIndex}>
                            换一个
                        </div>
                    </div>
                    <div className={styles.mailer}/>
                </div>
                {this.renderCopyButton()}
            </div>
        );
    }
}
