import React, {Component} from 'react';
import {inject, observer} from 'mobx-react';

import styles from './index.less';

import Upload from './upload';

// 模态框展示组件
@inject(({forwardingStore}) => {
    return {
        modal: forwardingStore.state.modal, // 模态框展示
        progress: forwardingStore.state.progress, // 上传进度
    };
})
@observer
export default class ForwardingModal extends Component {
    // 模态框close按钮点击事件
    handleCloseClick = () => {
        let {onCancel} = this.props;
        onCancel && onCancel();
    }

    // 模态框确定按钮点击事件
    handleSubmit = () => {
        let {onOk} = this.props;
        onOk && onOk();
    }

    // 模态框展示
    modalShow = () => {
        let {modal, progress} = this.props;
        let content = null;
        switch (modal) {
            case 'uploading':
                content = (
                    <div className={styles.smallBox}>
                        <div className={styles.outBar}>
                            <div className={styles.innerBar} style={{right: `${100 - progress}%`}}></div>
                        </div>
                        <div className={styles.uploadProgress}>
                            上传中...{progress}%
                        </div>
                    </div>
                );
                break;
            case 'success':
                content = (
                    <div className={styles.bigBox}>
                        <div className={styles.content}>
                            <div className={styles.successLogo}/>
                            <div className={styles.text}>
                                <div className={styles.title}>上传成功</div>
                                <div className={styles.letter}>我们会在3个工作日内完成审核，</div>
                                <div className={styles.letter}>请耐心等待噢～</div>
                                <div className={styles.btn} onClick={this.handleSubmit}>我知道了</div>
                            </div>
                        </div>
                        <div className={styles.close} onClick={this.handleCloseClick}></div>
                    </div>
                );
                break;
            case 'fail':
                content = (
                    <div className={styles.bigBox}>
                        <div className={styles.content}>
                            <div className={styles.failLogo}/>
                            <div className={styles.text}>
                                <div className={styles.letter}>上传失败了，请您重新上传噢～</div>
                                <Upload>
                                    <div className={styles.btn}>重新上传</div>
                                </Upload>
                            </div>
                        </div>
                        <div className={styles.close} onClick={this.handleCloseClick}></div>
                    </div>
                );
                break;
            default:
                content = null;
        }
        return (
            <div className={styles.modalBox}>{content}</div>
        );
    }

    render() {
        return (
            <div>
                {this.modalShow()}
            </div>
        );
    }
}