/**
 * 延迟, 单位毫秒
 * @param delay
 * @returns {Promise<any>}
 */
export const getDelay = (delay) => {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve();
    }, delay);
  });
};

