import React, { PureComponent } from 'react';

import { WechatConfig, Option, DocumentTitle, checkUserAuth } from '../../components';
import { connBasicLayout } from '../layout';

import { message } from '../../utils';
import { settingService } from '../../service';

import styles from './remind.less';

/**
 * 上课提醒类型
 */
const REMIND_TYPE = {
  SMS: '短信', // 短信
  WECHAT: '微信' // 微信
};

/**
 * 提醒设置
 */
@connBasicLayout()
@checkUserAuth({ support: 'all', mode: 'base' })
class Remind extends PureComponent {
  state = {
    remindList: [] // 提醒列表数据
  };

  componentDidMount() {
    this.loadData();
  }

  loadData = async () => {
    try {
      const remindList = await settingService.getMessageRemind();

      if (Array.isArray(remindList) && remindList.length) {
        this.setState({ remindList });
      }

      message.closeLoading();
    } catch (error) {
      message.info(error.msg || '加载失败');
    }
  };

  handelSelectChange = async (id) => {
    const { remindList } = this.state;
    const selectedItem = remindList.find((item) => item.id === id);

    selectedItem.status = !selectedItem.status;

    try {
      const setRemindResult = await settingService.setMessageRemind(selectedItem);

      if (Array.isArray(setRemindResult) && setRemindResult.length) {
        this.setState({ remindList: setRemindResult });
      }
    } catch (error) {
      message.info(error.msg || '操作失败');
    }
  };

  /**
   * 返回提醒列表
   * @returns {any[]}
   */
  renderRemindList = () => {
    const { remindList } = this.state;
    let element = [];

    if (remindList.length) {
      element = remindList.map((item) => {
        const { id, status } = item;
        return (
          <Option
            key={id}
            name={REMIND_TYPE[id]}
            on={status}
            change={this.handelSelectChange.bind(null, id)}
          />
        );
      });
    }
    return element;
  };

  render() {
    return (
      <div className={styles['box']}>
        {this.renderRemindList()}
        <DocumentTitle title="上课提醒" />
        <WechatConfig></WechatConfig>
      </div>
    );
  }
}

export default Remind;
