import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import classNames from 'classnames';
import queryString from 'query-string';

import {appConfig} from '../../config';
import layoutStyles from './layout.less';

import purchaseUrl from '../../public/images/layout/purchase.png';
import subscribeUrl from '../../public/images/layout/subscribe.png';
import subscribeActiveUrl from '../../public/images/layout/subscribe-active.png';
import userCenterUrl from '../../public/images/layout/user-center.png';
import userCenterActiveUrl from '../../public/images/layout/user-center-active.png';

// 商品页面链接
const productUrl = appConfig.resources.productUrl;

// 项目的二级目录
const basename = appConfig.basename;

/**
 * 底部导航
 */
@withRouter
class Footer extends Component {

    constructor(props) {
        super(props);

        this.urlParams = queryString.parse(window.location.search);
    }

    /**
     * 通过修改href跳转
     */
    handleHrefChange = (url) => {
        return () => {
            window.location.href = url;
        };
    };

    render() {
        const {location} = this.props;
        const bookingPath = `${basename}/booking`;
        const homePath = `${basename}/home`;

        const bookingClassName = classNames(layoutStyles.subscribe, {[layoutStyles.activeTab]: location.pathname.toLowerCase() === '/booking'});
        const homeClassName = classNames(layoutStyles.subscribe, {[layoutStyles.activeTab]: location.pathname.toLowerCase() === '/home'});
        return (
            <div id="footer" className={classNames(layoutStyles.footer, 'clearfix')}>
                <div className={layoutStyles.subscribe} onClick={this.handleHrefChange(productUrl)}>
                    <p>
                        <img src={purchaseUrl} alt="购课"/>
                    </p>
                    <p>购课</p>
                </div>
                <div onClick={this.handleHrefChange(bookingPath)} className={bookingClassName}>
                    <p>
                        <img
                            src={location.pathname.toLowerCase() === '/booking' ? subscribeActiveUrl : subscribeUrl}
                            alt="约课"
                        />
                    </p>
                    <p>约课</p>
                </div>
                <div onClick={this.handleHrefChange(homePath)} className={homeClassName}>
                    <p>
                        <img
                            src={location.pathname.toLowerCase() === '/home' ? userCenterActiveUrl : userCenterUrl}
                            alt="个人中心"
                        />
                    </p>
                    <p>个人中心</p>
                </div>

            </div>
        );
    }
}

export default Footer;
