import React, { Component } from 'react';
import classNames from 'classnames';
import { WingBlank, WhiteSpace, ListView } from 'antd-mobile';
import moment from 'moment';
import { inject, observer } from 'mobx-react';
import _ from 'lodash';

import BonusHeader from './bonusHeader';
import BonusSearch from './bonusSearch';
import BonusList from './bonusList';
import BonusModal from './bonusModal';

import { checkUserAuth, DocumentTitle, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';
import { message } from '../../utils';

import styles from './bonusLog.less';

// 魔力币明细容器组件
@inject(({ bonusStore }) => {
  return {
    bonusInfo: bonusStore.bonusInfo, // 魔力币基本信息
    freezePointList: bonusStore.freezePointList, // 冻结魔力币数量
    getBonusPoint: bonusStore.getBonusPoint, // 获取正常魔力币数量 func
    // getBonusFreezePoint: bonusStore.getBonusFreezePoint, // 获取冻结魔力币数量 func
    getBonusPointList: bonusStore.getBonusPointList, // 获取正常魔力币列表数据 func
    getBonusFreezePointList: bonusStore.getBonusFreezePointList // 获取冻结魔力币列表数据 func
  };
})
@observer
@connBasicLayout({ useFooter: true, backgdName: '' })
@checkUserAuth({ support: 'all', mode: 'base' })
class BonusLog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      dataSource: new ListView.DataSource({ rowHasChanged: (r1, r2) => r1 !== r2 }), // 正常魔力币数据源
      freezeDataSource: new ListView.DataSource({ rowHasChanged: (r1, r2) => r1 !== r2 }), // 冻结魔力币数据源
      isLoading: false, // 加载loading
      dateTime: null, // 交易日期
      type: ['ALL'], // 交易类型
      isShow: false, // 是否展示冻结明细
      index: 1, // 正常魔力币列表索引
      freezeIndex: 1, // 冻结魔力币列表索引
      isFreezeLoading: false, // 冻结魔力列表加载loading
      refreshing: false, // 正常魔力币下拉刷新状态
      freezeRefreshing: false, // 冻结魔力币下拉刷新状态
      data: [], // 正常魔力币
      freezeData: [], // 冻结魔力币
      total: 0, // 正常魔力币总数
      freezeTotal: 0 // 冻结魔力币总数
    };
  }

  componentDidMount() {
    // 获取魔力币数量
    this.loadBonusPoint();

    // 获取冻结魔力币数量
    // this.loadBonusFreezePoint();

    // 获取正常魔力币列表数据
    this.loadBonusPointList();
  }

  // 获取正常魔力币的数量
  loadBonusPoint = () => {
    const { getBonusPoint } = this.props;
    getBonusPoint();
  };

  // 获取冻结魔力币的数量
  // loadBonusFreezePoint = () => {
  //     const {getBonusFreezePoint} = this.props;
  //     getBonusFreezePoint();
  // }

  /**
   * 获取正常魔力币列表数据
   */
  loadBonusPointList = () => {
    const { type: tradeType, dateTime, index, data } = this.state;

    // 格式化交易时间
    const tradeDate = dateTime ? moment(dateTime).format('YYYY-MM') : '';
    const { getBonusPointList } = this.props;

    // 如果是第一次加载添加loading
    if (index === 1) {
      message.showLoading();
    }
    getBonusPointList(tradeType, tradeDate, index)
      .then(({ data: res, total }) => {
        message.closeLoading();

        // 同步正常魔力币列表数据
        this.setState({
          isLoading: false,
          refreshing: false,
          index: index + 1,
          total: total,
          data: [...data, ...res],
          dataSource: this.state.dataSource.cloneWithRows([...data, ...res])
        });
      })
      .catch((e) => {
        message.closeLoading();
        this.setState({
          isLoading: false,
          refreshing: false
        });
        console.error(e);
      });
  };

  /**
   * 获取冻结魔力币列表数据
   */
  loadBonusFreezePointList = () => {
    const { freezeIndex, freezeData } = this.state;

    const { getBonusFreezePointList } = this.props;

    // 如果是第一次加载添加loading
    if (freezeIndex === 1) {
      message.showLoading();
    }
    // 加载冻结魔力币列表数据
    getBonusFreezePointList(freezeIndex)
      .then(({ data: res, total }) => {
        message.closeLoading();

        const data = freezeIndex === 1 ? res : [...freezeData, ...res];
        // 同步正常魔力币列表数据
        this.setState({
          isFreezeLoading: false,
          freezeRefreshing: false,
          freezeIndex: freezeIndex + 1,
          freezeTotal: total,
          freezeData: data,
          freezeDataSource: this.state.freezeDataSource.cloneWithRows(data)
        });
      })
      .catch((e) => {
        message.closeLoading();
        this.setState({
          isFreezeLoading: false,
          freezeRefreshing: false
        });
        console.error(e);
      });
  };

  /**
   * 正常魔力币上拉加载数据
   */
  debounceEndReached = () => {
    const { total, data } = this.state;
    if (total === data.length) {
      return false;
    }
    this.setState({ isLoading: true }, () => {
      this.loadBonusPointList();
    });
  };

  // 正常魔力币防抖
  onEndReached = _.debounce(this.debounceEndReached, 500);

  /**
   * 正常魔力币下拉刷新数据
   */
  onRefresh = () => {
    this.setState({ refreshing: true, index: 1, data: [] }, () => {
      this.loadBonusPointList();
    });
  };

  /**
   * 冻结魔力币下拉刷新数据
   */
  debounceFreezeRefresh = () => {
    this.setState({ freezeRefreshing: true, freezeIndex: 1, freezeData: [] }, () => {
      this.loadBonusFreezePointList();
    });
  };

  // 冻结魔力币列表防抖
  onFreezeRefresh = _.debounce(this.debounceFreezeRefresh, 500);

  /**
   * 冻结魔力币上拉加载数据
   */
  onFreezeEndReached = () => {
    const { freezeTotal, freezeData } = this.state;
    if (freezeTotal === freezeData.length) {
      return false;
    }
    this.setState({ isFreezeLoading: true }, () => {
      this.loadBonusFreezePointList();
    });
  };

  /**
   * 查看冻结魔力币详情
   */
  handleClick = () => {
    const { isShow } = this.state;
    this.setState({ isShow: !isShow, freezeIndex: 1 });

    if (isShow) return;
    // 加载冻结魔力币列表数据
    this.loadBonusFreezePointList();
  };

  /**
   *取消搜索 初始化查询状态
   */
  handleCancelClick = (val, field) => {
    // 状态同步完 重新加载数据
    this.setState({ [field]: val, data: [], index: 1 }, () => {
      // 重新加载数据
      this.loadBonusPointList();
    });
  };

  /**
   * 确认搜索
   */
  handleOkClick = (val, field) => {
    // 状态同步完 重新加载数据
    this.setState({ [field]: val, data: [], index: 1 }, () => {
      // 重新加载数据
      this.loadBonusPointList();
    });
  };

  render() {
    const {
      dataSource,
      isLoading,
      dateTime,
      type,
      isShow,
      freezeDataSource,
      isFreezeLoading,
      refreshing,
      freezeRefreshing
    } = this.state;
    const height = document.documentElement.clientHeight - 250;

    const {
      bonusInfo: { bonusPoint, bonusFreezePoint, bonusIncome, bonusExpend }
    } = this.props;

    return (
      <div className={classNames(styles['bonus-log'])}>
        <WingBlank>
          <BonusHeader
            bonusTotal={bonusPoint}
            bonusFreeze={bonusFreezePoint}
            onClick={this.handleClick}
          />
        </WingBlank>
        <WhiteSpace />
        <WingBlank>
          <BonusSearch
            income={bonusIncome}
            pay={bonusExpend}
            dateTime={dateTime}
            type={type}
            onCancel={this.handleCancelClick}
            onOk={this.handleOkClick}
          />
        </WingBlank>
        <WhiteSpace />
        <WingBlank>
          <BonusList
            id="bonus-list"
            onChange={this.onEndReached}
            data={dataSource}
            loading={isLoading}
            pageSize={10}
            height={height}
            onRefresh={this.onRefresh}
            refreshing={refreshing}
          />
        </WingBlank>
        {isShow && (
          <BonusModal
            onChange={this.onFreezeEndReached}
            data={freezeDataSource}
            onClick={this.handleClick}
            loading={isFreezeLoading}
            pageSize={10}
            onRefresh={this.onFreezeRefresh}
            refreshing={freezeRefreshing}
          />
        )}
        <DocumentTitle title="魔力币明细" />
        <WechatConfig showShare={false} />
      </div>
    );
  }
}

export default BonusLog;
