import React, { Component } from 'react';
import className from 'classnames';

import { checkUserAuth, DocumentTitle } from '../../components';

import { userService } from '../../service';
import { message } from '../../utils';

import styles from './nickname.less';

@checkUserAuth({ support: 'all', mode: 'base' })
class Nickname extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '', // 别名
      enabled: true // 是否可用
    };
  }

  componentDidMount() {
    try {
      message.showLoading();
      userService.getUserBaseInfo().then((userInfo) => {
        const nikeName = userInfo['nikeName'];
        if (nikeName) {
          this.setState({ enabled: false, name: nikeName });
        }
        message.closeLoading();
      });
    } catch (e) {
      message.closeLoading();
      console.error(e);
    }
  }

  // 提交表单
  handleClick = async () => {
    const { name } = this.state;
    // 去除前后空格
    const nickName = name.replace(/^(\s|\xA0)+|(\s|\xA0)+$/g, '');
    // 校验是否合法
    const isTrue = /^[ A-Za-z]*$/.test(nickName);

    if (!isTrue) {
      message.info('昵称不合法');
      return false;
    }
    try {
      message.showLoading();
      await userService.nickName(name).then((res) => {
        console.info(res);
        this.setState({ enabled: false });
        message.closeLoading();
      });
    } catch (e) {
      message.closeLoading();
      console.error(e);
    }
  };

  // 同步表单状态
  handleChange = (e) => {
    this.setState({ name: e.target.value });
  };

  // 快捷设置名字
  handleNicknameChange = (name) => {
    this.setState({ name });
  };

  // 渲染结果
  renderResult = () => {
    const { name } = this.state;
    return (
      <div className={className(styles['nickname-body-form'])}>
        <span className={className(styles['nickname-body-form-result-title'])}>填写成功</span>
        <span className={className(styles['nickname-body-form-result-nickname'])}>
          宝宝的英文名: {name}
        </span>
      </div>
    );
  };

  // 渲染表单
  renderForm = () => {
    const { name } = this.state;
    return (
      <div className={className(styles['nickname-body-form'])}>
        <div className={className(styles['nickname-form-input'])}>
          <input
            placeholder="请输入宝贝英文名字哦~"
            onChange={this.handleChange}
            maxLength="20"
            value={name}
          />
        </div>
        <div className={className(styles['nickname-form-tab'])}>
          <div className={className(styles['nickname-tab-top'])}>
            <span className={className(styles['nickname-tab-title'])}>男宝宝英文名参考</span>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Andy')}
            >
              Andy
            </div>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Peter')}
            >
              Peter
            </div>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Alex')}
            >
              Alex
            </div>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Jimmy')}
            >
              Jimmy
            </div>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Eric')}
            >
              Eric
            </div>
          </div>
          <div className={className(styles['nickname-tab-top'])}>
            <span className={className(styles['nickname-tab-title'])}>女宝宝英文名参考</span>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Amy')}
            >
              Amy
            </div>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Elsa')}
            >
              Elsa
            </div>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Tina')}
            >
              Tina
            </div>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Vicky')}
            >
              Vicky
            </div>
            <div
              className={className(styles['nickname-tab-item'])}
              onClick={() => this.handleNicknameChange('Cindy')}
            >
              Cindy
            </div>
          </div>
        </div>
        <div className={className(styles['nickname-form-btn'])} onClick={this.handleClick}>
          确定
        </div>
      </div>
    );
  };

  render() {
    const { enabled } = this.state;
    return (
      <div className={className(styles['nickname-bg'])}>
        <div className={className(styles['nickname-content'])}>
          <div className={className(styles['nickname-header'])}>
            <span>请输入宝贝英文名字哦~</span>
          </div>
          <div className={className(styles['nickname-body'])}>
            <div className={className(styles['nickname-body-content'])}>
              <div className={className(styles['nickname-body-logo'])}></div>
              <div className={className(styles['nickname-body-link'])}>
                <div className={className(styles['nickname-body-link-left'])} />
                <div className={className(styles['nickname-body-link-right'])} />
              </div>
              {enabled ? this.renderForm() : this.renderResult()}
            </div>
          </div>
        </div>
        <div className={className(styles['nickname-footer'])} />
        <DocumentTitle title="用户昵称" />
      </div>
    );
  }
}

export default Nickname;
