import React from 'react';
import {DatePicker} from 'antd-mobile';
import moment from 'moment';
import PropTypes from 'prop-types';
import {BaseModal} from '../../../../components';
import image_close from '../../../../public/images/special/close.png';

import style from './style.less';

const CustomChildren = ({extra, onClick, children}) => {
    return (
        <div
            onClick={onClick}
            className={style.dataPicker}
        >
            <span>{extra === '请选择' ? '请选择' : moment(extra).format('YYYY-MM')}</span>
        </div>
    );
};

/**
 * 选择生日
 */
class BirthdayModal extends React.Component {
    static propTypes = {
        onCancel: PropTypes.func,
        birthDay: PropTypes.string,
        onOk: PropTypes.func,
    };

    static defaultProps = {
        onCancel: () => {},
        onOk: () => {},
        birthDay: '',
    };

    state = {
        birthDay: ''
    };

    /**
     * @description 点击确定回调是事件
     */
    handleOk = () => {
        const {birthDay} = this.state;
        this.props.onOk(moment(birthDay).year(), moment(birthDay).month() + 1);
    };

    handelCancel = () => {
        this.props.onCancel();
    };

    transferSetState = type => value => {
        this.setState(() => ({[type]: value}));
    };

    render() {
        const {birthDay} = this.state;
        return (
            <BaseModal visible={true}>
                <div className={style.content}>
                    <h3>请选择宝宝的生日</h3>
                    <div className={style['birth-input']}>
                        <DatePicker
                            className={style.picker}
                            maxDate={new Date()}
                            mode={'month'}
                            value={birthDay}
                            format={'YYYY-MM'}
                            onChange={this.transferSetState('birthDay')}
                        >
                            <CustomChildren/>
                        </DatePicker>
                    </div>
                    <button onClick={this.handleOk}>确定</button>
                    <div className={style.close} onClick={this.handelCancel}>
                        <img src={image_close} alt={''}/>
                    </div>
                </div>
            </BaseModal>
        );
    }
}

export default BirthdayModal;
