import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';

import BonusModal from './bonus-modal';
import { bonusService } from '../../service';
import { checkUserAuth, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';
import { message, tools } from '../../utils';

import styles from './styles.less';

const miniWxToken = tools.getMiniWxToken();
const bookingUrl = tools.getMiniWxToken() ? `/minibooking?miniWxToken=${miniWxToken}` : '/booking';

// 奖项类型
const BONUS_TYPE = {
  HardLearning: '勤学奖',
  VeryHardLearning: '学霸奖'
};

@checkUserAuth({ support: 'all', mode: 'base' })
@connBasicLayout()
export default class BonusCourse extends PureComponent {
  state = {
    processTarget: 0, // 达成目标所需的数量
    processDone: 0, // 已达成的数量
    processTodo: 0, // 还需要达成的数量
    courseList: [], // 课程列表
    type: '', // 当前奖项类型
    showModal: false, // 是否展示弹窗
    courseType: '' // 消耗的课时类型
  };

  componentDidMount() {
    // 设置标题
    this.setTitle();

    // 加载数据
    this.loadCourseList();
  }

  /**
   * 设置title
   */
  setTitle = () => {
    const {
      match: { params }
    } = this.props;
    const { bonusType } = params;
    const title = `去赚币 - ${BONUS_TYPE[bonusType]}`;

    window.document.title = title;
  };

  /**
   * 获取课程列表数据
   */
  loadCourseList = async () => {
    message.showLoading();
    try {
      const {
        match: { params }
      } = this.props;
      const { bonusType } = params;

      const {
        processTarget,
        processDone,
        processTodo,
        courseList
      } = await bonusService.getBonusCourseList(bonusType);

      this.setState({
        processTarget,
        processDone,
        processTodo,
        courseList,
        type: bonusType
      });
      message.closeLoading();
    } catch (e) {
      message.closeLoading();
      console.log(e);
    }
  };

  /**
   * 展示弹窗
   * @param courseType valid为false时有值，消耗的课时类型
   */
  showModal = (courseType) => {
    this.setState({
      showModal: !this.state.showModal,
      courseType
    });
  };

  renderTop = () => {
    const { processDone, processTodo, processTarget, type } = this.state;

    return (
      <div className={styles.bonusCourseTop}>
        <div className={styles.bonusCourseProgressBar}>
          <div
            className={styles.bonusCourseProgress}
            style={{ width: `${(processDone / processTarget) * 100}%` }}
          />
        </div>
        <div className={styles.bonusCourseTopContent}>
          截止目前，本月获奖的课程数量已达{processDone}次，仅差{processTodo}次即可获得
          {BONUS_TYPE[type]}
        </div>
        <Link to={bookingUrl}>
          <span className={styles.bonusCourseTopBtn} onClick={this.goEarnCoin}>
            去赚币
          </span>
        </Link>
      </div>
    );
  };

  renderList = () => {
    const { courseList } = this.state;

    return (
      <div className={styles.bonusCourseList}>
        <div className={styles.courseListTitle}>本月获奖的课程明细</div>
        <div className={styles.courseListContent}>
          <div className={styles.contentTop}>
            <span>上课时间</span>
            <span>是否纳入获奖统计</span>
          </div>
          <div className={styles.contentList}>
            {courseList &&
              courseList.map((course) => (
                <div className={styles.item} key={course.classStartTime}>
                  <div className={styles.time}>{course.classStartTime}</div>
                  {course.valid ? (
                    <div className={styles.valid}>
                      <i className={styles.bingo} />
                      <span className={styles.textBingo}>是</span>
                    </div>
                  ) : (
                    <div className={styles.valid} onClick={this.showModal.bind(null, course.type)}>
                      <i className={styles.error} />
                      <span className={styles.textError}>否</span>
                    </div>
                  )}
                </div>
              ))}
          </div>
        </div>
      </div>
    );
  };

  renderBottom = () => {
    const text =
      '勤学奖、学霸奖统计周期每个自然月，但不含新生试听课时、365畅学包课时、正式成为付费老生前的团购课时、请假课时、魔力币兑换课时、赠送的课时，且两个奖项不可同时获得。';
    return (
      <div className={styles.bonusCourseBottom}>
        <div className={styles.bottomTop} />
        <div className={styles.bottomContent}>{text}</div>
      </div>
    );
  };

  render() {
    const { showModal, courseType } = this.state;
    const modalData = {
      courseType
    };

    return (
      <div className={styles.bonusCourse}>
        {this.renderTop()}
        {this.renderList()}
        {this.renderBottom()}
        {showModal && (
          <BonusModal modalKey="error" modalData={modalData} onHideModal={this.showModal} />
        )}

        <WechatConfig showShare={false} />
      </div>
    );
  }
}
