import { observable, action } from 'mobx';
import querystring from 'query-string';
import _ from 'lodash';

import { bookingService } from '../service';
import { localStore, myToast, monitor, logger, message } from '../utils';
import { appConfig } from '../config';

import { COURSE_RESULT_ERROR_CODE, COURSE_STATUS, ClASS_TYPE, ClASS_TYPE_TEXT } from '../constants';

// 引导
const bootstrapKey = appConfig.cookie.bookingBootstrap;
const dataType = 'funnel';
/**
 * 预课数据
 */
class BookingStore {
  /**
   * 数据源
   */
  @observable state = {
    applyLoading: false,
    isShopCar: false,
    isShow: false,
    isFirstBootstrap: !localStore.get(bootstrapKey), // 是否是第一次引导
    bootstrap: false, // 是否显示图层引导
    modalKey: '',
    modalText: '',
    okText: '',
    currentSelectLesson: {},
    classType: ClASS_TYPE.MAIN_1v4,
    updateCourseList: false, // 补充注释: 更新课程列表的标记, 只要发生变化, 课表就需要重新加载数据
    paidStatus: false, // 注册用户是否是已付费用户
    subscribeStatus: false, // 注册用户是否关注了公众号
    qrcode: '', // 二维码url
    surplusPackage: '', // 剩余课时包名字(若没有不展示)
    surplusClassHour: 0, // 剩余课时
    selectClassNum: 0, // 选择课时数量
    courseDataObj: {}, // 当前时间的接口数据，正常约课的时候不获取接口使用该数据
    setLocalData: false // 标记属性, 只要发生变化 就设置本地数据.  当全部约课成功时, 课表数据根据本地数据 courseDataObj 显示
  };

  @observable protocolDetail = '';

  @observable status = '';

  @observable protocol = {
    protocolHeader: '',
    protocolDetail: '',
    status: true
  };

  allow = true;

  /**
   * 清空选中课程
   */
  @action clearCourse = () => {
    this.state = {
      ...this.state,
      isShopCar: false,
      isShow: false,
      modalKey: '',
      currentSelectLesson: {},
      selectClassNum: 0
    };
  };

  /**
   * 设置状态
   */
  @action setStoreState = (nextState) => {
    this.state = {
      ...this.state,
      ...nextState
    };
  };

  @action showClassList = () => {
    this.state = {
      ...this.state,
      isShow: !this.state.isShow,
      isFirstBootstrap: false
    };
  };

  /**
   * 删除预约的课程
   */
  @action removeSelectLesson = (data) => {
    const { currentSelectLesson, isShow } = this.state;
    const next = {};
    _.toPairs(currentSelectLesson).forEach((item) => {
      if (item[0] === data['item']) {
        next[item[0]] = item[1].filter((key) => !_.isEqual(JSON.parse(key)['id'], data['id']));
      } else {
        next[item[0]] = item[1];
      }
    });

    let isShopCar = false;
    _.toPairs(next).forEach((item) => {
      if (item[1].length > 0) {
        isShopCar = true;
      } else {
        delete next[item[0]];
      }
    });

    let selectClassNum = 0;
    Object.values(next).forEach((item) => {
      selectClassNum += item.length;
    });

    this.state = {
      ...this.state,
      currentSelectLesson: next,
      isShopCar: isShopCar,
      isShow: isShopCar ? isShow : false,
      selectClassNum
    };
  };

  /**
   * 获取用户付费状态 / 是否关注公众号
   */
  @action getClassUserStatus = async () => {
    try {
      const data = await bookingService.getClassUserStatus();
      this.state.subscribeStatus = data.subscribeStatus; // 是否关注了公众号
      this.state.paidStatus = data.paidStatus; // 是否付费用户
    } catch (error) {
      myToast.error('获取用户信息失败, 请重试~');
      monitor.log('', `获取用户付费/关注公众号失败: ${JSON.stringify(error)}`);
    }
  };

  /**
   * 提交约课
   */
  @action submitAppointment = async () => {
    logger.info({
      message: '点击立即约课',
      dataType
    });
    // monitor.logMoreParam('', '点击立即约课');
    if (!this.allow || this.getTotal() === 0) return;
    this.state.applyLoading = true;

    const { classType, currentSelectLesson } = this.state;
    const arr = []; // [{ dateStr: '2019-1-11', timeId: [19, 20]}]
    Object.keys(currentSelectLesson).forEach((item) => {
      const ids = [];
      currentSelectLesson[item].forEach((item) => {
        ids.push(JSON.parse(item).id);
      });
      const obj = {
        dateStr: item,
        timeIds: ids
      };
      arr.push(obj);
    });
    this.allow = false;
    try {
      const data = await bookingService.appointmentCourse(arr, classType);
      this.allow = true;
      const { act = '' } = querystring.parse(location.search);
      if (act) {
        monitor.log('', `act=${act}`);
      }
      logger.info({
        message: '约课成功',
        dataType
      });
      // monitor.logMoreParam('', `约课成功`);
      this.state.applyLoading = false;

      // 所选课程是否全部预约成功
      const resultCode = data.every((item) => item.courseStatus === COURSE_STATUS.NORMAL);
      data.forEach((item) => {
        Object.keys(this.state.courseDataObj).map((key) => {
          return (
            this.state.courseDataObj[key].length > 0 &&
            this.state.courseDataObj[key].forEach((courseTime) => {
              if (item.startTime === courseTime.startTime) {
                console.log(this.state.classType);
                courseTime.courseStatus = ClASS_TYPE_TEXT[this.state.classType];
              }
            })
          );
        });
      });
      // 所选课程全部预约成功
      if (resultCode) {
        if (this.state.paidStatus) {
          // 已付费用户
          myToast.success('预约成功');
          // 课程预约成功初始化课程预约状态
          this.clearCourse();
          // 标记属性, 只要发生变化 就设置本地数据.  当全部约课成功时, 课表数据根据本地数据 courseDataObj 显示
          this.state.setLocalData = !this.state.setLocalData;
        } else {
          // 未付费用户
          window.location.href = `${appConfig.domain.mobileDomain}/user/resultBook`;
        }
      } else {
        // 没有全部约课成功, 需要刷新课表数据
        this.state.updateCourseList = !this.state.updateCourseList;
        // 所选课程非全部预约成功
        // 格式化日期
        data.forEach(({ startTime, startLocalTime }) => {
          // 根据时间戳获取日期
          const [tempDate] = new Date(startTime).format('yyyy-MM-dd hh:mm:ss').split(' ');

          // 格式化时间
          const [hh, mm] = startLocalTime.split(':');
          if (currentSelectLesson[tempDate]) {
            // 初始化约课状态
            currentSelectLesson[tempDate].forEach((subLesson_str, index) => {
              const subLesson_obj = JSON.parse(subLesson_str);
              if (subLesson_obj.beginTime === hh + ':' + mm) {
                subLesson_obj.tempCourseStatus = '';
                currentSelectLesson[tempDate][index] = JSON.stringify(subLesson_obj);
              }
            });
          }
        });
        //  约满,已约,已完成,已约1v4主课,已约1v2主课,已约专题课
        const fullBooked = data.filter((item) => item.courseStatus !== COURSE_STATUS.NORMAL);
        // fullBooked.length && this.setCourseStatus(fullBooked, COURSE_STATUS.FULL_BOOKED);
        this.setCourseStatus(fullBooked);
      }
      // 重新获取剩余课时和下一个课时包
      this.getClassPackage(classType);
    } catch (error) {
      // 发生任何异常, 都需要重新刷新课表数据
      this.state.updateCourseList = !this.state.updateCourseList;
      // this.state.needRefresh = !this.state.needRefresh;
      this.allow = true;
      monitor.log('', `约课失败: ${JSON.stringify(error)}`);
      let { modalKey } = this.state;
      switch (error.code) {
        case COURSE_RESULT_ERROR_CODE.BIZ_USERCENTER_NO_GRADING:
          modalKey = 'no_grading';
          break;
        case COURSE_RESULT_ERROR_CODE.BIZ_USERCENTER_NO_CONFIRM_PROTOCOL:
          console.log(error.code, this.protocol.status);
          if (this.protocol.status) {
            this.getProtocolStatus();
          } else {
            modalKey = 'agreement';
          }
          // this.protocol.protocolDetail = error.msg;
          break;
        default:
          modalKey = 'error';
      }
      this.state = {
        ...this.state,
        applyLoading: false,
        modalKey: modalKey,
        modalText: error.msg,
        okText: '我知道了',
        isShow: true,
        follow: false
      };
    }
  };

  /**
   * 获取公众号二维码
   */
  @action getGradingQrcode = async () => {
    try {
      const data = await bookingService.getQrcodeUrl();
      this.state.qrcode = data;
    } catch (error) {
      myToast.error('二维码获取失败, 请重试~');
      monitor.log('', `公众号二维码获取失败: ${JSON.stringify(error)}`);
    }
  };

  /**
   * 定级失败的弹窗
   */
  @action errorModalByGrading = (msg) => {
    this.state = {
      ...this.state,
      modalKey: 'error_by_grading',
      modalText: msg,
      okText: '我知道了'
    };
  };

  /**
   * 修改modalkey
   */
  @action handleSetModalKey = (key) => {
    this.state.modalKey = key;
  };

  /**
   * 修改state
   */
  @action handleSetObs = (key, value) => {
    this.protocol = {
      ...this.protocol,
      [key]: value
    };
  };

  /**
   * 隐藏modal
   */
  @action handleHideModal = () => {
    this.state.modalKey = '';
  };

  /**
   * @description 设置待选中课程的状态
   * @param fullBooked 非法约课数组
   */
  setCourseStatus = (fullBooked) => {
    const { currentSelectLesson } = this.state;

    // 调整约满数据结构
    const bookedData = fullBooked.map(
      ({ courseStatus, endLocalTime, startLocalTime, startTime }) => {
        // 格式化日期
        const [tempDate] = new Date(startTime).format('yyyy-MM-dd hh:mm:ss').split(' ');

        // 格式化时间
        const [hh, mm] = startLocalTime.split(':');

        return {
          courseStatus,
          endLocalTime,
          startLocalTime,
          startTime,
          tempDate,
          tempTime: hh + ':' + mm
        };
      }
    );
    // 匹配 状态 课程
    bookedData.forEach(({ tempDate, tempTime, courseStatus }) => {
      if (currentSelectLesson[tempDate]) {
        currentSelectLesson[tempDate].forEach((subItem, index) => {
          if (JSON.parse(subItem).beginTime === tempTime) {
            const tempSubItem = JSON.parse(subItem);
            currentSelectLesson[tempDate][index] = JSON.stringify({
              ...tempSubItem,
              tempCourseStatus: courseStatus
            });
          }
        });
      }
    });

    // 根据状态渲染列表
    this.state = {
      ...this.state,
      isShow: true,
      applyLoading: false,
      currentSelectLesson,
      modalKey: 'error',
      modalText: '晚了一步，请删除已约满的时间哦~',
      okText: '我知道了'
    };
  };

  /**
   * 获取约课的数量
   */
  @action getTotal = () => {
    const { currentSelectLesson } = this.state;
    let sum = 0;
    const keys = Object.keys(currentSelectLesson);
    keys.forEach((key) => {
      sum += currentSelectLesson[key].length;
    });
    return sum;
  };

  /**
   * 获取下一个课时包和剩余课时
   */
  @action getClassPackage = async (type) => {
    try {
      const { source = '', balance = '' } = await bookingService.queryClassPackage(type);
      this.state = {
        ...this.state,
        surplusPackage: source, // 剩余课时包名字(若没有不展示)
        surplusClassHour: balance // 剩余课时
      };
    } catch (error) {
      monitor.log('', `获取下一个课时包和剩余课时: ${JSON.stringify(error)}`);
    }
  };

  /**
   * 选择课时和取消选择课时改变选中课程数量
   */
  @action changeClassNum = (selectClass) => {
    this.state = {
      ...this.state,
      selectClassNum: selectClass.length
    };
  };

  /**
   *同意协议内容
   */
  @action confirmProtocol = async () => {
    if (!this.allow) return;
    this.allow = false;
    try {
      await bookingService.confirmProtocol();
      message.success('同意成功');
      logger.info({
        account: '',
        message: '开包协议同意',
        eventName: 'client_protocol_agree'
      });
      this.handleSetModalKey('');
      this.protocol = {
        status: true,
        protocolHeader: '',
        protocolDetail: ''
      };
      this.allow = true;
    } catch (error) {
      console.log(error);
      myToast.error((error && error.msg) || '同意开课协议出现错误,稍后重试');
      this.protocol = {
        ...this.protocol,
        status: true
      };
      this.allow = true;
      logger.error({
        message: `用户同意协议接口出错:${error && error.msg}`
      });
    }
  };

  /**
   *获取协议状态以及内容
   */
  @action getProtocolStatus = async () => {
    try {
      const data = await bookingService.protocolStatus();
      if (!data.confirm) {
        this.state.modalKey = 'agreement';
        this.protocol = {
          status: data.confirm,
          protocolHeader: data.protocolHeader,
          protocolDetail: data.protocolContent
        };
        logger.info({
          message: '开包协议弹窗',
          eventName: 'client_protocol_choice'
        });
      } else {
        this.protocol = {
          ...this.protocol,
          status: true
        };
      }
    } catch (error) {
      console.log(error);
      myToast.error((error && error.msg) || '获取协议内容出现错误,稍后重试');
      logger.error({
        message: `获取协议状态以及内容:${error && error.msg}`
      });
    }
  };
}

export default new BookingStore();
