import querystring from 'query-string';

import {fetchUtils, monitor} from '../utils';
import {appConfig} from '../config';

const {baseDomain} = appConfig.domain;

/**
 * 当课表页面是内嵌到WebView时, 获取自定义的请求头: X-WECHAT-TOKEN
 */
const getHeadersWhenInWebView = () => {
    // miniWxToken 是内嵌到WebView时, 传来的参数
    const {miniWxToken} = querystring.parse(location.search);
    if (!miniWxToken) {
        return {};
    }
    // 使用内嵌时传的wxToken作为 X-WECHAT-TOKEN
    return {
        headers: {
            'X-WECHAT-TOKEN': miniWxToken,
        }
    };
};

/**
 * 查询课程日期
 */
export const getdayList = () => {
    const url = baseDomain.concat('/usercenter/getdays');
    monitor.log('', `约课 => 课程日期  => 参数: 无参数`);
    return fetchUtils.get(url, null, getHeadersWhenInWebView()).then(res => {
        monitor.log('', `约课 => 课程日期  => 响应: ${JSON.stringify(res.data)}`);
        return res.data;
    });
};

/**
 * @description 查询用户课程列表
 * @param reserveTime 时间
 * @param specialId 专题课id
 */
export const getCourseList = (reserveTime, classType, specialId) => {
    const url = baseDomain.concat('/usercenter/courseList');
    let monitorText = '约课';
    let params = {
        reserveTime,
        classType
    };

    if (specialId) {
        params.specialId = specialId;
        monitorText = '约专题课';
    }


    monitor.log('', `${monitorText} => 当天课程列表  => 参数: ${JSON.stringify(params)}`);
    return fetchUtils.get(url, params, getHeadersWhenInWebView()).then(res => {
        monitor.log('', `约课 => 当天课程列表  => 响应: ${JSON.stringify(res.data)}`);
        return res.data;
    });
};


/**
 * @description 提交约课
 * @param batchBookCourseDTOS 选中的课程数组
 */
export const appointmentCourse = (batchBookCourseDTOS = [], classType) => {
    const url = baseDomain.concat('/usercenter/book');
    const params = {
        batchBookCourseDTOS,
        classType
    };
    monitor.log('', `约课 => 提交约课  => 参数: ${JSON.stringify(params)}`);
    return fetchUtils.postJson(url, params, getHeadersWhenInWebView()).then(res => {
        monitor.log('', `约课 => 提交约课  => 响应: ${JSON.stringify(res.data)}`);
        return res.data;
    });
};


/**
 * 获取1v2耗课配置
 * @param startTime
 * @returns {self|Promise<T | never>}
 */
export const getCourseConfig = () => {
    const url = baseDomain.concat('/usercenter/get/course/config');
    return fetchUtils.get(url, null, getHeadersWhenInWebView()).then(res => {
        return res.data;
    });
};

/**
 * 查询带参数二维码
 */
export const getQrcodeUrl = () => {
    // 参数是要求写死的固定字段
    const param = {
        flag: 'course_'
    };
    const url = baseDomain.concat('/usercenter/wechat/qrcode/mp/url');
    return fetchUtils.get(url, param).then(res => res.data);
};

