import logHelper from '@mmears/log';
import queryString from 'query-string';

import { localStore } from './index';

const logger = logHelper({
  eventName: 'web_log',
  showLog: false, // process.env.NODE_ENV === 'development'
  getAccount: () => {
    return localStore.getUserId() || '';
  }
});

interface IExtraData {
  [propName: string]: any;
}

interface IMoreParam {
  rk?: string;
  rf?: string;
  act?: string;
}

/**
 * 前端监控, 日志上报
 */
const monitor = {
  /**
   * 在登录和注册成功后，调用方法来标识真实用户
   * @param userId
   */
  login(userId: string) {
    logger.login(userId);
  },
  /**
   * 使用 logout 切换到之前的匿名id
   */
  logout() {
    logger.logout();
  },
  /**
   * 记录日志
   * @param account 账号
   * @param message 信息
   */
  log(account: string, message: string, extraData?: IExtraData) {
    extraData = extraData || {};
    logger.info({
      ...extraData,
      account: localStore.getUserId() || account || 'debug',
      message
    });
  },
  /**
   * 记录错误信息
   * @param account 账号
   * @param message 错误信息
   * @param errCode 默认 ERROR
   */
  error(account: string, message: string, errCode: string = 'ERROR') {
    logger.error({
      account: localStore.getUserId() || account || 'debug',
      errCode,
      message
    });
  },

  /**
   *
   * @param account
   * @param message
   */
  logMoreParam(account: string, message: string) {
    const urlParams = queryString.parse(location.search) as any;
    const moreParam: IMoreParam = {
      rk: '',
      rf: '',
      act: ''
    };

    if (urlParams.rk) {
      moreParam.rk = urlParams.rk;
    }
    if (urlParams.rf) {
      moreParam.rf = urlParams.rf;
    }
    if (urlParams.act) {
      moreParam.act = urlParams.act;
    }
    logger.info({
      account: localStore.getUserId() || account || 'debug',
      message,
      ...moreParam
    });
  }
};

export { logger };

export default monitor;
