import { observable, action } from 'mobx';
import { resultBookService } from '../service';
import { message, logger } from '../utils';
/**
 * 约课结果页面-非付费用户
 */
class ResultBookStore {
  @observable course = {
    // courseName:'',
    // courseTime:[],
    // teacherQR:'',
    // wxNo:''
  };

  @observable week = ['日', '一', '二', '三', '四', '五', '六'];

  @action getTeacherQRCode = async () => {
    try {
      const data = await resultBookService.getQRcode();
      this.course = {
        ...this.course,
        teacherQR: data.url,
        wxNo: data.wxNo
      };
    } catch (error) {
      console.log(error);
      message.closeLoading();
      message.error((error && error.msg) || '获取二维码错误');
      logger.error({
        message: `获取用户课程相关内容出错:${error && error.msg}`
      });
    }
  };

  @action getCourseAbouts = async () => {
    try {
      const data = await resultBookService.getClassAboutNonPayment();
      this.course = {
        ...this.course,
        courseName: data.courseName,
        courseTime: data.startTimeList
      };
    } catch (error) {
      console.log(error);
      message.closeLoading();
      message.error((error && error.msg) || '获取用户课程相关内容出错');
      logger.error({
        message: `获取用户课程相关内容出错:${error && error.msg}`
      });
    }
  };
}
export default new ResultBookStore();
