import React, { Component } from 'react';
import Slider from 'react-slick';
import className from 'classnames';
import moment from 'moment';
import _ from 'lodash';
import querystring from 'query-string';

import { InfoModal, WechatConfig, DocumentTitle, checkUserAuth } from '../../components';
import { connBasicLayout } from '../layout';
import {
  COURSE_RESULT_ERROR_CODE,
  COURSE_STATUS,
  SURPLUS_COURSE,
  HAPPY_NEW_YEAR_DATE,
  ClASS_TYPE
} from '../../constants';

import { bookingService, specialService } from '../../service';
import { message, localStore, myToast, monitor } from '../../utils';
import { appConfig } from '../../config';

import styles from './special-booking.less';
import { ERROR_CODE, MODAL_KEY } from '../../constants/special';
import { BirthdayModal } from './component';

import empty_course from '../../public/images/special/empty_course.png';

import * as specialTools from './tools';

/**
 * 分享标题
 * @type {{title: string}}
 */
const shareOptions = {
  title: '魔力耳朵-约课'
};

/**
 * 预课日期轮播slider配置
 */
const DateSettings = {
  // dots: false,
  infinite: false, // 是否循环
  arrows: true, // 启用上一页下一页
  // autoplay: true, // 自动轮播
  autoplaySpeed: 2000,
  speed: 500,
  slidesToShow: 7, // 一页展示多少张
  slidesToScroll: 7 // 滑动一页多少张
};

const basename = appConfig.basename;
// 用户定级访问地址
const surveyPath = `${basename}/survey`;

const TimeQuantum = { am: '上午', pm: '下午', en: '晚上' };

// 引导
const bootstrapKey = appConfig.cookie.bookingBootstrap;

/**
 * 预约上课
 */
@connBasicLayout()
@checkUserAuth({ support: 'all', mode: 'base' })
class Booking extends Component {
  constructor(props) {
    super(props);

    const spId = querystring.parse(location.search).spId || '';

    if (!spId) {
      // 如果url 里面 没有spid
      location.replace(appConfig.basename + '/special');
    }

    this.state = {
      spId,
      detail: {}, // 专题课顶部banner详情
      loading: false,
      refresh: false,
      daysData: [], // 可以约课的日期列表
      courseData: null, // 选中的日期
      nowDate: moment().format('YYYY-MM-DD'), // 今天
      currentSelect: moment().format('YYYY-MM-DD'), // 当前默认选中当天的
      currentSelectLessonID: '', // 当前选中的课
      currentSelectLesson: {},
      modalKey: '',
      modalText: '', // modal展示的内容
      happyNewYear: false, // 是否显示新年快乐
      isFirstBootstrap: !localStore.get(bootstrapKey), // 是否是第一次引导
      bootstrap: false, // 是否显示图层引导
      classType: ClASS_TYPE.SPECIAL_1v4 // 默认1v4专题课
    };
    this.allow = true;
  }

  componentDidMount() {
    this.loadData();
  }

  /**
   * 加载数据
   */
  loadData = async () => {
    const { spId, classType } = this.state;
    let hasError = false;

    message.showLoading();

    specialService
      .getSpecialDetailById(spId)
      .then((res) => {
        this.setState({
          detail: {
            zhName: res.zhName,
            enName: res.enName,
            courseCnt: res.courseCnt
          }
        });
      })
      .catch((error) => {
        hasError = true;
        message.error(error.msg || '加载失败');
      });

    bookingService
      .getdayList()
      .then((daysData) => {
        this.setState({
          daysData: daysData.days,
          nowDate: daysData.now
        });
        bookingService
          .getCourseList(daysData.now, classType, spId)
          .then((courseData) => {
            // 是否包含在假期中
            const happyNewYear = this.isHappyNewYear(daysData.now);
            this.setState({
              currentSelect: daysData.now,
              courseData,
              happyNewYear
            });

            // 创建数据副本
            this.catchData = courseData;
            const slideIndex = daysData.days.findIndex((t) => t === daysData.now);
            this.dateSlider.slickGoTo(slideIndex > 6 ? 7 : 0);

            !hasError && message.closeLoading();
          })
          .catch((error) => {
            !hasError && message.closeLoading();
            switch (error.code) {
              case ERROR_CODE.BIZ_COURSE_BOOK_AGE_NULL: {
                this.setState({
                  modalKey: MODAL_KEY.BIRTHDAY
                });
                break;
              }
            }
          });
      })
      .catch((error) => {
        message.error(error.msg || '加载失败');
      });
  };

  /**
   * 选择日期获取对应的课时
   */
  handleSelect = (date) => {
    // 是否包含在假期中
    const happyNewYear = this.isHappyNewYear(date);
    monitor.log('', `约专题课 =>  选择约课日期 => : ${JSON.stringify(date)}`);
    const { currentSelect } = this.state;
    if (date !== currentSelect) {
      this.setState({
        currentSelect: date,
        courseData: [],
        happyNewYear
      });
      this.getCourseList(date);
    }
  };

  /**
   * 加载对应日期的课时
   */
  getCourseList = (date) => {
    const { classType, spId } = this.state;
    // 加载课程日期
    this.setState({ getting: true });
    bookingService
      .getCourseList(date, classType, spId)
      .then((courseData) => {
        this.setState({ courseData, refresh: false, getting: false });

        // 创建数据副本
        this.catchData = courseData;
      })
      .then(() => {
        this.settingCourseSelect();
      })
      .catch((error) => {
        this.setState({ getting: false });
        message.error(error.msg || '加载失败');

        if (error.code !== 'BIZ_COURSE_BOOK_MISMATCHING') {
          this.setState({
            refresh: true
          });
        }
      });
  };

  /**
   * @description 判断当前日期是否是新年
   * @param currentDate 当前日期
   *
   */
  isHappyNewYear = (currentDate) => {
    // 当前日期是否是新年
    return !!HAPPY_NEW_YEAR_DATE.find((date) => date === currentDate);
  };

  /**
   * animation 点击约课动画效果
   * cb        回调函数
   */
  getAnimation = (ele, cb) => {
    const { left, top, width, height } = ele.getClientRects()[0];
    const group = ele.children[0];
    group.style.top = top + height / 2 + 'px';
    group.style.left = left + width / 2 + 'px';
    group.style.opacity = 1;
    group.style.transition = 'left 0s linear, top 0s linear, opacity 0s ease-out';
    setTimeout(() => {
      group.style.top = '15.78rem';
      group.style.left = '2.84rem';
      group.style.opacity = 0;
      group.style.transition =
        'left .5s cubic-bezier(0, 0, 0.04, 1.04), top .3s cubic-bezier(1, 0.01, 1, 1), opacity .7s ease-in-out';
      group.style.zIndex = 1000;
      cb && cb();
      setTimeout(() => {
        group.style.left = '-999999999px'; // 解决动画后事件冒泡
      }, 500);
    }, 0);
  };

  /**
   * 设置课程状态
   */
  settingCourseSelect = () => {
    const { currentSelectLesson, currentSelect, courseData } = this.state;

    if (currentSelectLesson[currentSelect]) {
      const data = {};
      const ids = currentSelectLesson[currentSelect].map((item) => {
        return JSON.parse(item).id;
      });
      for (const key in courseData) {
        if (courseData.hasOwnProperty(key) && courseData[key] && courseData[key].length > 0) {
          const tempData = courseData[key].map((subItem) => {
            let temp = { ...subItem };
            if (subItem.courseStatus === 'NORMAL' && ids.indexOf(subItem.id) === -1) {
              temp = { ...subItem, courseStatus: 'FULL_BOOKED' };
            }
            return temp;
          });
          data[key] = tempData;
        }
      }
      this.setState({
        courseData:
          currentSelectLesson[currentSelect].length >= this.surplusCourse ? data : this.catchData
        // isShopCar: true
      });
    }
  };

  /**
   * 点击预约课时
   */
  makeAppointment = (date, e) => {
    // 把选中的课时添加进去 currentSelectLesson
    const { currentSelectLesson, currentSelect, bootstrap } = this.state;
    const isHave = _.toPairs(currentSelectLesson).some((items) => {
      let truly = false;
      if (items[0] === currentSelect) {
        items[1].forEach((key) => {
          if (_.isEqual(JSON.parse(key).id, date.id)) {
            truly = true;
          }
        });
      }
      return truly;
    });

    if (isHave) {
      // 如果已经添加了则取消
      const next = {};
      _.toPairs(currentSelectLesson).forEach((item) => {
        if (item[0] === currentSelect) {
          next[item[0]] = item[1].filter((key) => !_.isEqual(JSON.parse(key).id, date.id));
        } else {
          next[item[0]] = item[1];
        }
      });

      let isShopCar = false;

      _.toPairs(next).forEach((item) => {
        if (item[1].length > 0) {
          isShopCar = true;
        }
      });

      this.setState(() => ({
        courseData: this.catchData,
        currentSelectLesson: next,
        isShopCar
      }));
    } else {
      // 添加选中动画
      this.getAnimation(e.currentTarget, () => {
        if (!bootstrap) {
          this.setState({ bootstrap: true });
          localStore.set([bootstrapKey], 'true');
        }
      });
      if (currentSelectLesson[currentSelect]) {
        const index = currentSelectLesson[currentSelect].indexOf(JSON.stringify(date));
        if (index > -1) {
          currentSelectLesson[currentSelect].splice(index, 1);
        } else {
          currentSelectLesson[currentSelect].push(JSON.stringify(date));
        }
      } else {
        currentSelectLesson[currentSelect] = [JSON.stringify(date)];
      }
      this.setState({
        currentSelectLesson,
        isShopCar: true
      });
      if (
        currentSelectLesson[currentSelect] &&
        currentSelectLesson[currentSelect].length >= this.surplusCourse
      ) {
        this.settingCourseSelect();
      }
    }
  };

  /**
   * 提交约课
   */
  submitMake = () => {
    if (!this.allow || this.getTotal() === 0) return;
    this.setState({ loading: true });
    const { currentSelectLesson, currentSelect, spId } = this.state;
    const arr = []; // [{ dateStr: '2019-1-11', timeId: [19, 20]}]
    Object.keys(currentSelectLesson).forEach((item) => {
      const ids = [];
      currentSelectLesson[item].forEach((item) => {
        ids.push(JSON.parse(item).id);
      });
      const obj = {
        dateStr: item,
        timeIds: ids
      };
      arr.push(obj);
    });
    this.allow = false;
    specialService
      .specialBooking(spId, arr)
      .then((data) => {
        this.allow = true;
        const { act = '' } = querystring.parse(location.search);
        if (act) {
          monitor.log('', `act=${act}`);
        }

        this.setState({
          loading: false
        });

        // 所选课程是否全部预约成功
        const resultCode = data.every((item) => item.courseStatus === COURSE_STATUS.NORMAL);

        // 所选课程全部预约成功
        if (resultCode) {
          myToast.success('预约成功');
          // 重新加载数据
          this.getCourseList(currentSelect);

          // 课程预约成功初始化课程预约状态
          this.clearCourse();
        } else {
          // 所选课程非全部预约成功
          // 格式化日期
          data.forEach(({ startTime, startLocalTime }) => {
            // 根据时间戳获取日期
            const [tempDate] = new Date(startTime).format('yyyy-MM-dd hh:mm:ss').split(' ');

            // 格式化时间
            const [hh, mm] = startLocalTime.split(':');
            if (currentSelectLesson[tempDate]) {
              // 初始化约课状态
              currentSelectLesson[tempDate].forEach((subLesson_str, index) => {
                const subLesson_obj = JSON.parse(subLesson_str);
                if (subLesson_obj.beginTime === hh + ':' + mm) {
                  subLesson_obj.tempCourseStatus = '';
                  currentSelectLesson[tempDate][index] = JSON.stringify(subLesson_obj);
                }
              });
            }
          });
          //  约满
          const fullBooked = data.filter((item) => item.courseStatus === COURSE_STATUS.FULL_BOOKED);
          fullBooked.length && this.setCourseStatus(fullBooked, COURSE_STATUS.FULL_BOOKED);

          // 已约
          const alreadyBooked = data.filter(
            (item) => item.courseStatus === COURSE_STATUS.ALREADY_BOOKED
          );
          alreadyBooked.length && this.setCourseStatus(alreadyBooked, COURSE_STATUS.ALREADY_BOOKED);

          // 已约1v4主课
          const alreadyBooked1v4 = data.filter(
            (item) => item.courseStatus === COURSE_STATUS.ALREADY_BOOKED_1V4
          );
          alreadyBooked1v4.length &&
            this.setCourseStatus(alreadyBooked1v4, COURSE_STATUS.ALREADY_BOOKED_1V4);

          // 已约1v2主课
          const alreadyBooked1v2 = data.filter(
            (item) => item.courseStatus === COURSE_STATUS.ALREADY_BOOKED_1V2
          );
          alreadyBooked1v2.length &&
            this.setCourseStatus(alreadyBooked1v2, COURSE_STATUS.ALREADY_BOOKED_1V2);

          // 已约主课
          const alreadyBookedMain = data.filter(
            (item) => item.courseStatus === COURSE_STATUS.ALREADY_BOOKED_MAIN
          );
          alreadyBooked.length &&
            this.setCourseStatus(alreadyBookedMain, COURSE_STATUS.ALREADY_BOOKED_MAIN);

          // 已约其他课
          const alreadyBookedSpecialOther = data.filter(
            (item) => item.courseStatus === COURSE_STATUS.ALREADY_BOOKED_SPECIAL_OTHER
          );
          alreadyBooked.length &&
            this.setCourseStatus(
              alreadyBookedSpecialOther,
              COURSE_STATUS.ALREADY_BOOKED_SPECIAL_OTHER
            );

          // 已完成
          const finishBooked = data.filter(
            (item) => item.courseStatus === COURSE_STATUS.ALREADY_FINISH
          );
          finishBooked.length && this.setCourseStatus(alreadyBooked, COURSE_STATUS.ALREADY_FINISH);
        }
      })
      .catch((error) => {
        this.allow = true;
        monitor.log('', `约专题课 => 提交约课  => 异常: ${JSON.stringify(error)}`);
        switch (error.code) {
          case COURSE_RESULT_ERROR_CODE.BIZ_USERCENTER_NO_GRADING:
            this.setState({
              loading: false,
              modalKey: 'no_grading',
              modalText: error.msg,
              okText: '我知道了',
              isShow: true
            });
            break;
          default:
            this.setState({
              loading: false,
              modalKey: 'error',
              modalText: error.msg,
              okText: '我知道了',
              isShow: true
            });
        }
      });
  };

  /**
   * @description 设置待选中课程的状态
   * @param arr 非法约课数组
   * @param type 类型
   */
  setCourseStatus = (arr, type) => {
    const { currentSelectLesson } = this.state;

    // 调整约满数据结构
    const bookedData = arr.map(({ courseStatus, endLocalTime, startLocalTime, startTime }) => {
      // 格式化日期
      const [tempDate] = new Date(startTime).format('yyyy-MM-dd hh:mm:ss').split(' ');

      // 格式化时间
      const [hh, mm] = startLocalTime.split(':');

      return {
        courseStatus,
        endLocalTime,
        startLocalTime,
        startTime,
        tempDate,
        tempTime: hh + ':' + mm
      };
    });

    // 匹配 状态 课程
    bookedData.forEach(({ tempDate, tempTime }) => {
      if (currentSelectLesson[tempDate]) {
        currentSelectLesson[tempDate].forEach((subItem, index) => {
          if (JSON.parse(subItem).beginTime === tempTime) {
            const tempSubItem = JSON.parse(subItem);
            currentSelectLesson[tempDate][index] = JSON.stringify({
              ...tempSubItem,
              tempCourseStatus: type
            });
          }
        });
      }
    });

    // 根据状态渲染列表
    this.setState({
      loading: false,
      currentSelectLesson,
      modalKey: 'error',
      isShow: true,
      modalText: '晚了一步，请删除已约满的时间哦~',
      okText: '我知道了'
    });
  };

  /**
   * 跳转事件,通过修改href跳转
   * @param path
   * @returns {Function}
   */
  handleHrefChange = (url) => {
    return () => {
      window.location.href = url;
    };
  };

  /**
   * 渲染日期
   */
  renderDateView = () => {
    const { daysData, currentSelect, nowDate } = this.state;
    if (!daysData) return false;
    const dataList = [];
    daysData.forEach((item, index) => {
      let classname = '';
      if (item === nowDate) {
        // 给当前日期添加样式
        classname = styles.nowActive;
      }
      if (item === currentSelect) {
        classname = styles.active; // 给选中的日期添加样式
      }
      dataList.push(
        <li key={item} className={classname} onClick={this.handleSelect.bind(null, item)}>
          <p>{this.getCurrentWeek(item)}</p>
          <p>{moment(item).format('MM/DD')}</p>
        </li>
      );
    });
    return dataList;
  };

  /**
   * 渲染课程
   */
  renderCourseView = () => {
    const { courseData = {}, refresh, getting } = this.state;

    // 初始化当天约课数量
    this.surplusCourse = SURPLUS_COURSE;

    const courseList = [];
    for (const key in courseData) {
      if (courseData.hasOwnProperty(key) && courseData[key] && courseData[key].length > 0) {
        const tempHtml = (
          <div key={key} className={className(styles.section, 'clearfix')}>
            <div className={styles.left}>{TimeQuantum[key]}</div>
            <div className={styles.right}>
              <ul>{this.renderCourseItem(courseData[key])}</ul>
            </div>
          </div>
        );
        courseList.push(tempHtml);
      }
    }
    if (courseList.length === 0 && !refresh && !getting) {
      return (
        <div className={styles.emptyCourse}>
          <img src={empty_course} alt="empty" />
          <p className={styles.desc}>今天没有课哦~</p>
        </div>
      );
    }
    return courseList;
  };

  /**
   * render课程的每一个单元格
   */
  renderCourseItem = (items) => {
    const { currentSelectLesson, currentSelect, happyNewYear } = this.state;
    const currentSelectList = currentSelectLesson[currentSelect] || [];
    return items.map((item) => {
      // let index = currentSelectList.indexOf(JSON.stringify(item));
      let course = {};
      if (currentSelectList.some((subItem) => JSON.parse(subItem).id === item.id)) {
        course = {
          status: true,
          text: '已选',
          class: styles.already,
          courseStatus: item.courseStatus,
          id: item.id
        };
      } else {
        course = this.getCourseStatus(item);
      }

      // 设置当天可预约的课程数
      if (
        course.courseStatus === COURSE_STATUS.ALREADY_BOOKED ||
        course.courseStatus === COURSE_STATUS.ALREADY_BOOKED_1V2 ||
        course.courseStatus === COURSE_STATUS.ALREADY_BOOKED_1V4 ||
        course.courseStatus === COURSE_STATUS.ALREADY_BOOKED_SPECIAL ||
        course.courseStatus === COURSE_STATUS.ALREADY_FINISH
      ) {
        this.surplusCourse = this.surplusCourse - 1;
      }

      // 新年快乐
      if (happyNewYear) {
        course = {
          ...course,
          courseStatus: COURSE_STATUS.HAPPY_NEW_YEAR, // 状态覆盖
          text: '春节假期', // 修改文案
          status: false // 禁用事件点击
        };
      }

      return (
        <li
          key={item.beginTime}
          onClick={course.status && this.makeAppointment.bind(null, item)}
          className={className(course.class, happyNewYear && styles['happy-new-year'])}
        >
          <em />
          <p className={styles.timer}>{item.beginTime}</p>
          <p className={styles.status}>{course.text}</p>
        </li>
      );
    });
  };

  /**
   * @description 显示引导约课
   */
  renderBootstrap = () => {
    const { bootstrap } = this.state;

    if (bootstrap && !localStore.get(bootstrapKey)) {
      return (
        <div className={styles.bootstrap}>
          <div
            className={styles.iknow}
            onClick={() =>
              this.setState({
                bootstrap: true,
                isShow: true
              })}
          >
            我知道了
          </div>
        </div>
      );
    }
    return null;
  };

  /**
   * 根据课程状态返回
   */
  getCourseStatus = (currentCourse) => {
    let course = {
      status: true,
      text: '点击预约',
      class: styles.cannot,
      courseStatus: currentCourse.courseStatus,
      id: currentCourse.id
    };
    switch (currentCourse.courseStatus) {
      // 已完成
      case COURSE_STATUS.ALREADY_FINISH:
        course = {
          status: false,
          text: '已完成',
          class: styles.success,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 已约 1v2
      case COURSE_STATUS.ALREADY_BOOKED_1V2:
        course = {
          status: false,
          text: '已约2人班',
          class: styles.success,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 已约1v4
      case COURSE_STATUS.ALREADY_BOOKED_1V4:
        course = {
          status: false,
          text: '已约4人班',
          class: styles.success,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 已约
      case COURSE_STATUS.ALREADY_BOOKED:
        course = {
          status: false,
          text: '预约成功',
          class: styles.success,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 已约主课
      case COURSE_STATUS.ALREADY_BOOKED_MAIN:
        course = {
          status: false,
          text: '已约主课',
          class: styles.success,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 已约其他课
      case COURSE_STATUS.ALREADY_BOOKED_SPECIAL_OTHER:
        course = {
          status: false,
          text: '已约其他课',
          class: styles.success,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 约满
      case COURSE_STATUS.FULL_BOOKED:
        course = {
          status: false,
          text: '约满',
          class: styles.cannot,
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
      // 可约
      case COURSE_STATUS.NORMAL:
        course = {
          status: true,
          text: '点击预约',
          class: '',
          courseStatus: currentCourse.courseStatus,
          id: currentCourse.id
        };
        break;
    }
    return course;
  };

  /**
   *根据日期获取星期
   */
  getCurrentWeek = (date) => {
    const nowWeek = moment().format('YYYY-MM-DD');
    if (nowWeek === date) return '今天';
    let sWeek = moment(date).format('dddd');
    switch (sWeek) {
      case 'Monday':
        sWeek = '星期一';
        break;
      case 'Tuesday':
        sWeek = '星期二';
        break;
      case 'Wednesday':
        sWeek = '星期三';
        break;
      case 'Thursday':
        sWeek = '星期四';
        break;
      case 'Friday':
        sWeek = '星期五';
        break;
      case 'Saturday':
        sWeek = '星期六';
        break;
      case 'Sunday':
        sWeek = '星期日';
        break;
      default:
        break;
    }
    return sWeek;
  };

  /**
   *刷新页面
   */
  handleRefresh = () => {
    const { currentSelect } = this.state;
    this.getCourseList(currentSelect);
  };

  /**
   * 关闭modal
   */
  handleHideModal = () => {
    this.setState({
      modalKey: ''
    });
  };

  /**
   * @description 切换上下页的回调
   */
  handleAfterChange = (index) => {
    const { daysData } = this.state;
    if (index > 0) {
      this.handleSelect(daysData[index]);
    } else {
      this.handleSelect(moment().format('YYYY-MM-DD'));
    }
  };

  /**
   * 切换是否显示课程列表
   */
  showClassList = () => {
    const { isShow } = this.state;
    this.setState({
      isShow: !isShow,
      isFirstBootstrap: false
    });
  };

  /**
   * 获取约课的数量
   */
  getTotal = () => {
    const { currentSelectLesson } = this.state;
    let arr = [];
    const keys = Object.keys(currentSelectLesson);
    keys.forEach((key) => {
      arr = [...arr, ...currentSelectLesson[key]];
    });
    return arr.length;
  };

  /**
   * 删除预约的课程
   */
  removecurrentSelectLesson = (data) => {
    const { currentSelectLesson } = this.state;
    if (data) {
      monitor.log('', `约专题课 =>  单课程删除 => 当前单课程: ${JSON.stringify(data)}`);
      const { item, id } = data;
      // 根据指定的课程id删除
      if (currentSelectLesson[item]) {
        currentSelectLesson[item] = currentSelectLesson[item].filter(
          (subItem) => JSON.parse(subItem).id !== id
        );
      }
      let isShow = true;
      let isShopCar = true;
      if (!this.getTotal()) {
        isShow = false;
        isShopCar = false;
      }
      this.setState({
        courseData: this.catchData, // 删除指定的课程
        currentSelectLesson,
        isShow,
        isShopCar
      });
    } else {
      monitor.log(
        '',
        `约专题课 =>  清除按钮 => 当前所选课程: ${JSON.stringify(currentSelectLesson)}`
      );
      this.setState({
        courseData: this.catchData, // 全部清除选中课程
        currentSelectLesson: {},
        isShow: false,
        isShopCar: false,
        modalKey: ''
      });
    }
  };

  /**
   * 课程预约成功 初始化选中的预约课程
   */
  clearCourse = () => {
    this.setState({
      currentSelectLesson: {},
      isShow: false,
      isShopCar: false,
      modalKey: ''
    });
  };

  /**
   * 全部删除预约的课程
   */
  removecurrentSelectLessonAll = (data) => {
    this.setState({
      modalKey: 'info',
      modalText: '你确定要清空吗？',
      okText: '确定'
    });
  };

  /**
   * @param status 课程状态
   */
  renderCourseStatus = (status) => {
    switch (status) {
      case COURSE_STATUS.FULL_BOOKED:
        return <i>（约满）</i>;
      case COURSE_STATUS.ALREADY_BOOKED:
      case COURSE_STATUS.ALREADY_BOOKED_MAIN:
        return <i>（已约）</i>;
      case COURSE_STATUS.ALREADY_BOOKED_1V4:
        return <i>（已约4人班）</i>;
      case COURSE_STATUS.ALREADY_BOOKED_1V2:
        return <i>（已约2人班）</i>;
      case COURSE_STATUS.ALREADY_BOOKED_SPECIAL:
        return <i>（已约专题课）</i>;
      case COURSE_STATUS.ALREADY_FINISH:
        return <i>（已完成）</i>;
      default:
        return null;
    }
  };

  /**
   *  渲染选中的课程列表
   */
  renderClassList = () => {
    const { currentSelectLesson, isShow } = this.state;
    console.log(12333, isShow);
    const keys = Object.keys(currentSelectLesson).sort();
    const weekArr = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
    return (
      <section id="booking-course" className={isShow ? styles.list : ''}>
        <div>
          <h4>
            <span className={styles.selectText}>已选课程</span>
            <span className={styles.delete} onClick={this.removecurrentSelectLessonAll}>
              清空
            </span>
          </h4>
          <ul className={styles['course-item']}>
            {keys.map((item) => {
              const tempArr = [];

              // 数据转换
              currentSelectLesson[item].forEach((subLesson) => {
                tempArr.push(JSON.parse(subLesson));
              });

              // 排序
              tempArr.sort((prev, nex) => prev.id - nex.id);

              return (
                <li key={item}>
                  {tempArr.map((val, key) => {
                    // val = JSON.parse(val);
                    return (
                      <p key={`${item}${val.id}`}>
                        <span className={styles.left}>
                          {moment(item).format('MM/DD')}&nbsp;&nbsp;
                          {weekArr[new Date(item).getDay()]}
                          {this.renderCourseStatus(val.tempCourseStatus)}
                        </span>
                        <span className={styles.right}>
                          {val.beginTime}-{val.endTime}
                          <em
                            onClick={this.removecurrentSelectLesson.bind(null, {
                              item,
                              id: val.id
                            })}
                          />
                        </span>
                      </p>
                    );
                  })}
                </li>
              );
            })}
          </ul>
        </div>
      </section>
    );
  };

  /**
   * @description 清除modalKey
   */
  clearModalKey = () => {
    this.setState({
      modalKey: ''
    });
  };

  renderModal = () => {
    const { modalKey, modalText, loading, okText } = this.state;
    let element = null;
    switch (modalKey) {
      case 'info':
        element = (
          <InfoModal
            okText={okText}
            headerType="happy"
            loading={loading}
            onOk={this.removecurrentSelectLesson}
            onCancel={this.handleHideModal}
          >
            <div className={styles.modalCentent}>{modalText}</div>
          </InfoModal>
        );
        break;
      case 'error':
        element = (
          <InfoModal
            okText={okText}
            headerType="weep"
            cancelVisible={false}
            onOk={this.handleHideModal}
          >
            <div className={styles.modalCentent}>{modalText}</div>
          </InfoModal>
        );
        break;
      case 'no_grading':
        element = (
          <InfoModal
            headerType="weep"
            onCancel={this.handleHideModal}
            onOk={this.handleHrefChange(surveyPath)}
          >
            <div className={styles.modalCentent}>正式上课前，需要宝贝完成课程分级调查哦</div>
          </InfoModal>
        );
        break;
      case MODAL_KEY.BIRTHDAY: {
        element = (
          <BirthdayModal
            onOk={(year, month) => {
              specialTools.handleUpdateBirthDay.call(this, year, month);
            }}
            onCancel={this.clearModalKey}
          />
        );
        break;
      }
    }
    return element;
  };

  render() {
    const { refresh, isShopCar, isShow, isFirstBootstrap, detail } = this.state;
    return (
      <div className={styles.wrapper}>
        <header className={styles.header}>
          <div className={styles['wrap']}>
            <h3>{detail.zhName}</h3>
            <h4>{detail.enName}</h4>
            <p>
              该专题课一共有{detail.courseCnt}节课，建议家长一次性约好{detail.courseCnt}
              节课，以便了解完整的知识体系。
            </p>
          </div>
        </header>
        {this.renderBootstrap()}
        <DocumentTitle title="专题课-约课" />
        <div className={styles.content}>
          <div className={styles.optional}>
            <div className={styles.slideOuter}>
              <div className={styles.slideInner}>
                <ul className={styles.slide}>
                  <Slider
                    ref={(slider) => {
                      this.dateSlider = slider;
                    }}
                    {...DateSettings}
                    afterChange={this.handleAfterChange}
                  >
                    {this.renderDateView()}
                  </Slider>
                </ul>
              </div>
            </div>
          </div>
          <div className={styles.duration}>
            <p>每课时25分钟</p>
          </div>
          <div className={styles.container}>{this.renderCourseView()}</div>
          {refresh ? (
            <div className={styles.nowifiwrapper}>
              <div className={styles.nowifibox} />
              <div className={styles.nowifitext}>数据竟然跑路了，刷新试试看</div>
              <div className={styles.nowifirefreshbutton} onClick={this.handleRefresh}>
                刷新
              </div>
            </div>
          ) : null}
        </div>
        {this.renderClassList()}
        <footer
          id="booking-footer"
          className={className(styles.shoppingCar, {
            [styles.footer]: isShopCar
          })}
        >
          <div className={styles.wrapper}>
            <p onClick={this.showClassList}>
              <span className={styles.images} />
              <span className={styles['course-select']}>已选：</span>
              <span className={styles.number}>{this.getTotal()}节课</span>
            </p>
            <div className={styles.submitButtonWrapper}>
              <div
                className={className(styles.submitButton, {
                  [styles.tada]: isFirstBootstrap && isShow
                })}
                onClick={this.submitMake}
              >
                确认约课
              </div>
            </div>
          </div>
        </footer>
        {isShow ? <p className={styles.mark} onClick={this.showClassList} /> : null}
        {this.renderModal()}
        <WechatConfig showShare={true} shareOptions={shareOptions} />
      </div>
    );
  }
}

export default Booking;
