import {fetchUtils} from '../utils';
import {appConfig} from '../config';

const baseDomain = appConfig.domain.baseDomain;

/**
 * 用户设置-获取上课提示设置
 */
export const getMessageRemind = () => {
    const url = baseDomain.concat('/usercenter/message/getStatus');
    return fetchUtils.get(url).then(res => res.data);
};

/**
 * 用户设置-上课提示设置
 */
export const setMessageRemind = (dara) => {
    const url = baseDomain.concat('/usercenter/message/updateMessageSwitch');
    const param = {
        type: dara.id,
        status: dara.status
    };
    return fetchUtils.post(url, param).then(res => res.data);
};
