import {inject, observer} from 'mobx-react';
import React, {Component} from 'react';

import styles from './index.less';

import ForwardingModal from './forwarding-modal';
import Upload from './upload';
import {SCREENSHOTSTATUSENUM} from '../../constants';
import {DocumentTitle} from '../../components';

// 上传朋友圈截图组件
@inject(({forwardingStore}) => {
    let {timelineImg, status, refuseReason, magicCoinAmount} = forwardingStore.shareInfo;
    return {
        // 数据
        timelineImg, // 上传的朋友圈截图
        status, // 审核进度
        refuseReason, // 审核失败原因
        magicCoinAmount, // 获取的魔力币
        modal: forwardingStore.state.modal, // 模态框
        // 函数
        showModal: forwardingStore.showModal, // 更改模态框
    };
})
@observer
export default class CoinForwarding extends Component {

    state = {
        imgShow: false,
    };

    // 模态框close按钮点击事件
    handleCancel = () => {
        let {showModal} = this.props;
        showModal('');
    }

    // 模态框确定按钮点击事件
    handleSubmit = () => {
        let {showModal} = this.props;
        showModal('');
    }

    // 审核进度样式
    createChecking = () => {
        let {status, refuseReason, magicCoinAmount} = this.props;
        let checkClass = styles[status];
        switch (status) {
            case SCREENSHOTSTATUSENUM['REFUSED'].id:
                return (
                    <div className={checkClass}>
                        <div className={styles.failText}>
                            <div className={styles.failTitle}>{SCREENSHOTSTATUSENUM['REFUSED'].text}</div>
                            <div className={styles.failContent}>{refuseReason}</div>
                            <div className={styles.failBorder}></div>
                            <Upload>
                                <div className={styles.failBtn}>
                                    重新上传
                                </div>
                            </Upload>
                        </div>
                    </div>
                );
            case SCREENSHOTSTATUSENUM['TO_AUDIT'].id:
                return (
                    <div className={checkClass}>
                        {SCREENSHOTSTATUSENUM['TO_AUDIT'].text}
                    </div>
                );
            case SCREENSHOTSTATUSENUM['APPROVED'].id:
                return (
                    <div className={checkClass}>
                        {SCREENSHOTSTATUSENUM['APPROVED'].text + magicCoinAmount + '魔力币'}
                    </div>
                );
            default:
                return null;
        }
    }

    handleImgClick = () => {
        this.setState({
            imgShow: !this.state.imgShow,
        });
    }

    render() {
        let {modal, timelineImg} = this.props;
        let {imgShow} = this.state;
        return (
            <div className={styles.forwarding}>
                <div className={styles.main}>
                    <div className={styles.check}>
                        {this.createChecking()}
                    </div>
                    {timelineImg ? null : <div className={styles.uploadBox}>
                        <div className={styles.uploadBoxMain}>
                            <Upload>
                                <div className={styles.centerBox}>
                                    <div className={styles.upload} onClick={this.uploadClick}>
                                        <div className={styles.uploadImg}></div>
                                        <div>上传本月朋友圈截图</div>
                                    </div>
                                </div>
                            </Upload>
                        </div>
                    </div>}
                    {timelineImg ? <div className={styles.screenShot}>
                        <div className={styles.shotTitle}>
                            <div className={styles.circleImg}></div>
                            <div className={styles.titleText}>我的本月朋友圈截图</div>
                        </div>
                        <img className={styles.shotImg} src={timelineImg} onClick={this.handleImgClick} ref={ref => this.img = ref}></img>
                        {/* {status === SCREENSHOTSTATUSENUM['TO_AUDIT'].id ?
                            <div className={styles.reuploadBtnMain}>
                                <Upload>
                                    <div className={styles.reuploadBtn}>
                                        重新上传
                                    </div>
                                </Upload>
                            </div> : null} */}
                    </div> : null}
                    <div className={styles.require}>
                        <h2>朋友圈截图要求：</h2>
                        <p>（1）发布内容为魔力耳朵品牌相关的正面信息。文字+配图为魔力耳朵二维码海报或注册领课链接，截图到上面指定区域。</p>
                        <p>（2）发布内容有5次互动，不限点赞和评论。</p>
                        <p>（3）当月您参与过外教课或训练营的学习，次月1号就能获得相应的魔力币奖励哦~</p>
                        <p>（4）特别注意：如果当月宝贝没有上课/参加训练营，该转发朋友圈规则无效。</p>
                        <p>（5）审核时间：提交后，我们会在3个工作日内审核完成，审核完成后则进入冻结状态，冻结期过后，您可获得相应魔力币。</p>
                    </div>
                    {modal && <ForwardingModal onCancel={this.handleCancel} onOk={this.handleSubmit}></ForwardingModal>}
                    {imgShow ? (
                        <div className={styles.bg} onClick={this.handleImgClick}>
                            <img className={styles.imgShow} src={timelineImg}></img>
                        </div>
                    ) : null}
                </div>
                <DocumentTitle title={'上传朋友圈截图'}></DocumentTitle>
            </div>
        );
    }
}
