import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import queryString from 'query-string';

import { AccountsType } from '../../constants';
import { localStore, tools } from '../../utils';
import { appConfig } from '../../config';
import { logger } from '../../utils/monitor';

import * as styles from './auth.css';

/**
 * 项目的二级目录
 */
const basename = appConfig.basename;

/**
 * 默认选项
 * @type {{account: string}}
 */
const defaultOpptions = {
  account: AccountsType.MAIN,
  /**
   * 支持环境.
   * wechat: 只支持微信环境, 在其他浏览器里 页面无法打开
   * all: 微信和普通浏览器环境 都可以打开, 微信会检查用户是否授权, 普通浏览器跳过
   */
  support: 'wechat',
  /**
   * mode 区分 用户主动授权 or  静默授权, 默认是 user, 用户主动授权
   * 取值: user | base
   * user: 用户主动授权
   * base: 静默授权
   */
  mode: 'user'
};

/**
 * 检查是否主动授权
 * @param options
 * @returns {function(*): InnerComponent}
 */
export default function checkUserAuth(options = {}) {
  return (WrappedComponent) => {
    @withRouter
    class InnerComponent extends Component {
      constructor(props) {
        super(props);

        console.log('checkUserAuth >>>>> ');

        options = {
          ...defaultOpptions,
          ...options
        };

        const { account, support, mode } = options;

        try {
          const inWebView = tools.checkInWebView();
          console.log('checkUserAuth >> 当前页面是否内嵌在WebView里', inWebView);

          let openIdKey;
          switch (account) {
            case AccountsType.MAIN:
              // 主公众号
              openIdKey = appConfig.cookie.mainOpenIdKey;
              break;
            case AccountsType.ACTIVITY:
              // 活动公众号
              openIdKey = appConfig.cookie.activityOpenIdKey;
              break;
            default:
              openIdKey = appConfig.cookie.mainOpenIdKey;
              break;
          }
          const openId = localStore.get(openIdKey) || '';

          this.account = account;
          this.support = support;
          this.mode = mode;
          this.openId = openId;
          this.inWebView = inWebView; // 是否是内嵌到WebView里的H5页面
          this.isWeChat = tools.isWeChat(); // 判断是否是微信浏览器
          this.authUrl = this.getAuthUrl();

          this.checkAuth();
        } catch (error) {
          logger.error({
            message: `checkUserAuth 授权检查发生错误: ${JSON.stringify(error)}`
          });
          console.error('checkUserAuth 发生错误', error);
        }
      }

      /**
       * 获取统一授权地址
       * @returns {string}
       */
      getAuthUrl = () => {
        // 本地开发环境 不处理
        if (appConfig.env === 'development') return '';

        /* eslint-disable */
        let { from, isappinstalled, ...params } = queryString.parse(window.location.search);
        /* eslint-enable */

        // 当前URL地址
        let search = queryString.stringify(params);
        if (search) {
          search = `?${search}`;
        }

        // 前端业务页面url
        const returnUrl = `${location.protocol}//${location.host}${location.pathname}${search}`;

        // 统一授权地址
        // 需要三个参数: account 使用哪个微信公众号, mode 主动/静默 授权, returnUrl 前端业务页面url
        params = {};
        params.account = this.account;
        params.mode = this.mode;
        params.returnUrl = encodeURIComponent(returnUrl);

        // 统一授权url
        const authUrl = `${location.protocol}//${
          location.host
        }${basename}/auth?${queryString.stringify(params)}`;

        return authUrl;
      };

      /**
       * 检查用户主动授权的openId
       */
      checkAuth = () => {
        console.log('开始检查微信授权...');

        /**
         * !!!本地开发环境, 直接显示 不进行授权
         */
        if (appConfig.env === 'development') {
          console.log('本地开发环境, 不执行授权');
          return;
        }

        /**
         * 内嵌到WebView的页面, 直接显示 不进行授权
         */
        if (this.inWebView) {
          console.log('内嵌到WebView的页面, 不执行授权');
          return;
        }
        /**
         * !!!如果不是微信浏览器 并且 支持在其他浏览器里打开, 直接显示 不进行授权
         */
        if (!this.isWeChat && this.support === 'all') {
          console.log('不是微信浏览器 并且 支持在其他浏览器里打开, 不执行授权');
          return;
        }

        // 如果已经授权过, 则不需要重复授权
        if (this.openId) {
          return;
        }

        window.location.replace(this.authUrl);
      };

      render() {
        // 1. 开发环境, 直接显示 不进行授权
        // 2. 只支持微信环境 并且 已授权 拿到openId
        // 3. 支持其他浏览器打开, 直接显示 不进行授权
        // 4. 内嵌到WebView的页面, 直接显示 不进行授权
        if (
          appConfig.env === 'development' ||
          (this.support === 'wechat' && this.openId) ||
          this.support === 'all' ||
          this.inWebView
        ) {
          return <WrappedComponent {...this.props} authUrl={this.authUrl} />;
        }

        return <div className={styles.auth}>授权中...</div>;
      }
    }

    return InnerComponent;
  };
}
