import {observable, action} from 'mobx';

import {bookingService} from '../service';
import {monitor} from '../utils';

class GlobalStore {
    @observable state = {
        courseConfig: {},
    };

    @observable promotion_file = '';

    /**
     * 获取学员1v2状态信息
     */
    @action getCourseConfig = async () => {
        try {
            const data = await bookingService.getCourseConfig();
            this.state.courseConfig = data;
        } catch (error) {
            monitor.log('', `获取学员2人班配置出错了:${JSON.stringify(error)}`);
        }
    }

    @action set_promotion_file = str => {
        this.promotion_file = str;
    }
}

export default new GlobalStore();
