import appConfig from '../config/app-config';
import OSS from 'ali-oss';
import { fetchUtils, tools, monitor } from '../utils';

const baseDomain = appConfig.domain.baseDomain;
const bannerDomain = appConfig.domain.apiDomain.concat('/activity');

// 获取上传资格和上传明细接口
export function getSubmissionRecord() {
  const url = baseDomain + '/usercenter/api/sharecheck/get/shareinfo';
  return fetchUtils.get(url);
}

// 上传朋友圈截图接口
export function uploadImage(path) {
  const url = baseDomain + '/usercenter/api/sharecheck/save/image';
  const params = {
    url: path
  };
  return fetchUtils.post(url, params);
}

// 图片上传oss
export const choiceImageUpload = async (base64Date) => {
  const url = bannerDomain.concat('/file/get/uploadinfodto');

  // 自定义文件名称
  const fileName = `${new Date().getTime() + String(Math.random())}.png`;

  // 获取oss配置参数
  const param = {
    ossFileType: 'IMAGE',
    fileName: fileName
  };

  try {
    const {
      data: { ossKey, accessKeyId, accessKeySecret, securityToken, region, bucket }
    } = await fetchUtils.post(url, param);

    monitor.log(
      '',
      `上传朋友圈截图 => OSS参数 ${JSON.stringify({
        ossKey,
        accessKeyId,
        accessKeySecret,
        securityToken,
        region,
        bucket
      })}`
    );
    // 初始化oss实例
    const client = new OSS({
      region: region,
      accessKeyId: accessKeyId,
      accessKeySecret: accessKeySecret,
      stsToken: securityToken,
      bucket: bucket,
      timeout: 20000
    });

    // base64转file对象
    const file = tools.dataURLtoFile(base64Date, fileName);
    monitor.log('', '朋友圈截图 => base64转file成功');
    const {
      res: { requestUrls }
    } = await client.put(ossKey, file);

    const [firstUrl] = requestUrls;
    return new Promise((resolve, reject) => {
      if (firstUrl) {
        monitor.log('', `朋友圈截图 => 文件上传成功 ${JSON.stringify(firstUrl)}`);
        resolve({ url: firstUrl });
      } else {
        monitor.log('', `朋友圈截图 => 上传失败`);
        reject('上传朋友圈截图-OSS上传失败');
      }
    });
  } catch (e) {
    monitor.log('', `朋友圈截图 => 文件上传失败，${tools.errorToString(e)}`);
  }
};
