import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import queryString from 'query-string';
import jsCookies from 'js-cookie';
import store from 'store';

import { DocumentTitle, WechatConfig, checkUserAuth } from '../../components';
import { connBasicLayout } from '../layout';

import { userService } from '../../service';
import { localStore, message, monitor, tools } from '../../utils';
import styles from './login.less';
import { appConfig } from '../../config';

const phoneReg = /^1\d{10}$/;

// 默认账号
let defaultAccount = {
  phone: '',
  password: ''
};

/**
 * 本地开发环境默认账号
 */
if (process.env.NODE_ENV === 'development') {
  defaultAccount = {
    phone: '18910210381',
    password: '210381'
  };
}

// 二级目录
const basename = appConfig.basename;

/**
 * 登录页面
 */
@connBasicLayout()
@checkUserAuth({ support: 'all', mode: 'base' })
class Login extends PureComponent {
  constructor(props) {
    super(props);
    this.urlParams = queryString.parse(window.location.search);
    this.allow = true; // 请接口状态
    this.isAndroid = tools.isAndroid(); // 是否是Android系统
    let password = defaultAccount.password;
    if (this.isAndroid) {
      this.password = password; // 记录密码
      password = password.replace(/./g, '*');
    }

    this.state = {
      phone: defaultAccount.phone,
      password
    };

    // 简单节流
    this.throttleHandleLogin = tools.throttle(this.handleLogin, 1000);
  }

  componentDidMount() {
    localStore.clearToken();
    const nowToken = localStore.getToken();
    if (nowToken) {
      monitor.log('', `清除Token失败：${nowToken}`);
    }
  }

  /**
   * 用户名输入change事件回调
   */
  handleTelChange = (e) => {
    this.setState({
      phone: e.target.value
    });
  };

  /**
   * 密码输入change事件回调
   */
  handlePsdChange = (e) => {
    let value = e.target.value;

    if (this.isAndroid) {
      const start = e.target.selectionStart; // input输入光标位置
      if (value.length > this.password.length) {
        // 密码个数增加
        const index = start - 1;
        this.password = this.password.slice(0, index) + value[index] + this.password.slice(index);
        value = value.replace(/./g, '*');
      } else {
        // 密码个数减少
        this.password = this.password.slice(0, start) + this.password.slice(start + 1);
      }
    }

    this.setState({
      password: value
    });
  };

  handleLogin = () => {
    const { userTokenKey } = appConfig.cookie;
    const phone = this.state.phone;
    const password = this.isAndroid ? this.password : this.state.password;

    if (!this.allow) {
      return false;
    }

    if (!this.verifyLogin(phone, password)) {
      return false;
    }

    this.allow = false;

    userService
      .login(phone, password)
      .then(({ userId, passToken }) => {
        monitor.log(phone, `登录成功`);
        localStore.setToken(userId, passToken);
        const userCookieToken = jsCookies.get(userTokenKey);
        const userStoreToken = store.get(userTokenKey);
        if (userCookieToken !== userStoreToken) {
          monitor.log(phone, `token不一致：当前登录token=${passToken}`);
          monitor.log(phone, `token不一致：userStoreToken=${userStoreToken}`);
          monitor.log(phone, `token不一致：userCookieToken=${userCookieToken}`);
        }
        monitor.login();
        // 延迟跳转
        setTimeout(() => {
          this.loginSucceed();
        }, 200);
      })
      .catch((error) => {
        monitor.log(phone, `登录失败:${error.msg}`);
        message.error(error.msg || '登录失败');
        this.allow = true;
      });
  };

  /**
   * 登录成功后执行
   */
  loginSucceed = () => {
    let callback = this.urlParams.callback;
    if (callback) {
      callback = decodeURIComponent(callback);
      window.location.replace(callback);
    } else {
      window.location.replace(`${basename}/home`);
    }
  };

  /**
   * 校验用户输入
   */
  verifyLogin = (phone, password) => {
    if (!phone) {
      message.info('请输入手机号码');
      return false;
    }
    if (!phoneReg.test(phone)) {
      message.info('手机号格式错误');
      return false;
    }
    if (!password) {
      message.info('请输入密码');
      return false;
    }
    if (password.length < 6) {
      message.info('请输入正确的登录密码');
      return false;
    }
    return true;
  };

  handleFocus = (ref) => () => {
    const el = this.refs[ref] || null;
    const ua = navigator.userAgent;
    if (ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1) {
      setTimeout(() => {
        el && el.scrollIntoView(true);
      }, 400);
    }
  };

  render() {
    const { phone, password } = this.state;
    return (
      <div className={styles.loginWrapper}>
        <header className={styles.header}>
          <div className={styles.brand} />
        </header>
        <div className={styles.content}>
          <div className={styles.section}>
            <div className={classNames(styles.setItem, styles.phoneWarp, 'clearfix')}>
              <label />
              <input
                type="tel"
                maxLength="11"
                value={phone}
                onChange={this.handleTelChange}
                placeholder="手机号码"
                autoComplete="off"
                ref="phoneRef"
                onFocus={this.handleFocus('phoneRef')}
              />
            </div>
            <div className={classNames(styles.setItem, styles.passwordWrap, 'clearfix')}>
              <label />
              <input
                type={this.isAndroid ? 'text' : 'password'}
                maxLength="18"
                value={password}
                onChange={this.handlePsdChange}
                placeholder="6-18位数字或字母"
                ref="passwordRef"
                onFocus={this.handleFocus('passwordRef')}
                autoComplete="off"
              />
            </div>
          </div>
          <div className={styles.function}>
            <div className={styles.loginBtn} onClick={this.throttleHandleLogin}>
              登录
            </div>
            <div className={classNames(styles.otherBtn, 'clearfix')}>
              <span className={styles.registerBtn}>
                <Link to={`/register?${tools.getSearchParam(window.location.href)}`}>立即注册</Link>
              </span>
              <span className={styles.resetpasswordsBtn}>
                <Link to="/resetpwd">忘记密码</Link>
              </span>
            </div>
          </div>
        </div>
        <DocumentTitle title="登录页面" />
        <WechatConfig />
      </div>
    );
  }
}

export default Login;
