import React from 'react';
import {Carousel} from 'antd-mobile';
import classNames from 'classnames';

import {resourceService} from '../../service';

import styles from './booking.less';

/**
 * Banner 组件
 * @param data
 */
class Banner extends React.PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            data: [], // banner列表
            loading: false, // 加载loading
        };
    }

    componentDidMount() {
        // 加载banner列表
        this.loadBanners();
    }

    /**
     * 获取banner列表
     */
    loadBanners = async () => {
        this.setState({
            loading: true,
        });
        try {
            // 获取banner数据
            const data = await resourceService.getBannersUrl();
            // 同步状态
            this.setState({data});
        } catch (error) {
            console.error(error);
        }

        this.setState({loading: false});
    };

    /**
     * 通过修改href跳转
     */
    handleHrefChange = (url) => {
        return () => {
            window.location.href = url;
        };
    };

    /**
     * 次级渲染banner图片
     */
    renderBannersImg = () => {
        // banner列表数据
        const {data} = this.state;
        return data.map((banner) => {
            return (<img
                key={`banner-${banner.key}`}
                className={styles.bannerImg}
                onClick={this.handleHrefChange(banner.link)}
                src={banner.imgUrl}
            />);
        });
    }

    /**
     * 渲染banner
     */
    renderBanners = () => {
        // 加载loading状态
        const {loading} = this.state;
        // 渲染加载loading
        if (loading) {
            return (<div className={styles.containers}>
                <div className={styles['m-load2']}>
                    <div className={styles['line']}>
                        <div/>
                        <div/>
                        <div/>
                        <div/>
                        <div/>
                        <div/>
                    </div>
                    <div className={styles['circlebg']}/>
                </div>
            </div>);
        }

        // 渲染banner
        return (
            <div className={classNames(styles.bannerContainer)}>
                <Carousel
                    autoplay={true}
                    infinite={true}
                    autoplayInterval={4000}
                >
                    {this.renderBannersImg()}
                </Carousel>
            </div>
        );
    }

    /**
     * 渲染banner
     */
    render() {
        return this.renderBanners();
    }

}

export default Banner;
