import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import WX from 'weixin-js-sdk';

import styles from './index.less';

import appConfig from '../../config/app-config';
import { message, wechatUtils, monitor, tools } from '../../utils';
import { forwardingService } from '../../service';

// 图片上传接口配置
const options = {
  url: appConfig.domain.baseDomain + '/activity/file/image/upload',
  timeOut: 20000,
  header: {
    // 'Content-Type': 'application/json; charset=UTF-8',
  }
};

// 上传组件
@inject(({ forwardingStore }) => {
  return {
    // 数据
    progress: forwardingStore.state.progress, // 上传进度
    // 函数
    showModal: forwardingStore.showModal, // 模态框修改
    modifyProgress: forwardingStore.modifyProgress, // 上传进度修改
    initData: forwardingStore.initData // 初始化数据
  };
})
@observer
export default class Upload extends Component {
  state = {
    modal: false
  };

  // 选中图片时弹框询问是否上传
  refuseHandle = () => {
    this.setState({
      modal: true
    });
    return new Promise((resolve) => {
      this.handleCheck = resolve;
    });
  };

  createModal = () => {
    const { modal } = this.state;
    return modal ? (
      <div className={styles.uploadModal}>
        <div className={styles.Modalcontent}>
          <div className={styles.successLogo} />
          <div className={styles.title}>是否上传该图片</div>
          <div className={styles.btns}>
            <div onClick={this.handleModalClick.bind(null, false)} className={styles.btn}>
              取消
            </div>
            <div onClick={this.handleModalClick.bind(null, true)} className={styles.btn}>
              确定
            </div>
          </div>
        </div>
      </div>
    ) : null;
  };

  handleModalClick = (isConfirm) => {
    this.setState({
      modal: false
    });
    this.handleCheck({
      isConfirm
    });
  };

  // file的onChange事件
  uploadChange = async (event) => {
    const { showModal, initData, modifyProgress } = this.props;
    event.persist();
    const file = event.target.files[0];
    console.log(event.target.files, file);
    if (!file) return;
    monitor.log('', `朋友圈上传-成功选中图片`);
    const { isConfirm } = await this.refuseHandle();
    // 请求体
    if (!isConfirm) {
      event.target.value = ''; // eslint-disable-line
      return;
    }
    const formData = new FormData();
    formData.append('file', file);
    const xhr = new XMLHttpRequest();
    // 请求方式
    xhr.open('POST', options.url, true);
    // 请求头
    if (options.header && Object.keys(options).length > 0) {
      for (const key in options.header) {
        if (options.header.hasOwnProperty(key)) {
          xhr.setRequestHeader(key, options.header[key]);
        }
      }
    }
    // 设置超时
    xhr.timeout = options.timeOut;

    // 开始发起请求
    xhr.addEventListener('loadstart', (e) => {
      showModal && showModal('uploading');
    });
    // 上传进度
    xhr.upload.addEventListener('progress', (e) => {
      modifyProgress && modifyProgress(Math.floor((e.loaded / e.total) * 100));
    });
    // 请求成功
    xhr.addEventListener(
      'load',
      (e) => {
        const response = JSON.parse(e.currentTarget.response);
        if (xhr.status === 200 && response.code === 'OK') {
          monitor.log('', `朋友圈上传-上传图片成功，${e.currentTarget.respons}`);
          forwardingService
            .uploadImage(response.data)
            .then((res) => {
              monitor.log('', `朋友圈上传-图片成功发送到后台，${JSON.stringify(res)}`);
              message.showLoading();
              initData();
              showModal && showModal('success');
              event.target.value = '';
            })
            .catch((error) => {
              showModal && showModal('fail');
              event.target.value = '';
              monitor.log('', `朋友圈上传-图片发送到后台失败，${JSON.stringify(error)}`);
            });
        } else {
          showModal && showModal('fail');
          event.target.value = '';
          monitor.log('', `朋友圈上传-上传图片过程中错误，${xhr.status + '-' + response.code}`);
        }
      },
      false
    );
    // 请求超时
    xhr.addEventListener('timeout', (e) => {
      showModal && showModal('');
      message.error('上传超时');
      event.target.value = '';
      monitor.log('', `朋友圈上传-上传图片超时，${JSON.stringify(e)}`);
    });
    // 请求错误
    xhr.addEventListener(
      'error',
      (e) => {
        message.error('网络错误');
        showModal && showModal('');
        event.target.value = '';
        monitor.log('', `朋友圈上传-网络错误，${JSON.stringify(e)}`);
      },
      false
    );
    xhr.send(formData);
  };

  /**
   * 微信环境上传
   */
  beginUpload = () => {
    const { showModal, initData } = this.props;
    showModal('');
    monitor.log('', `朋友圈上传-开始点选图片`);
    wechatUtils.chooseImage({
      callback(files) {
        monitor.log('', `朋友圈上传-点选图片成功，fiels：${JSON.stringify(files)}`);
        message.showLoading('上传中...');
        WX.getLocalImgData({
          localId: files[0],
          async success(res) {
            monitor.log('', `朋友圈上传-读取本地图片成功，res：${JSON.stringify(res)}`);
            let localData = res.localData;
            // window.__wxjs_is_wkwebview
            if (localData.indexOf('data:image') !== -1) {
              // IOS
              localData = localData.replace('jpg', 'jpeg');
              monitor.log('', `朋友圈上传-读取到本地图片-IOS`);
            } else {
              // 安卓
              localData = 'data:image/jpeg;base64,' + localData.replace(/\n/g, '');
              monitor.log('', `朋友圈上传-读取到本地图片-安卓`);
            }
            monitor.log(
              '',
              `朋友圈上传-读取到本地图片,是否有base64:${localData.indexOf(
                'data:image/jpeg;base64'
              )}`
            );
            console.log('localData>>', localData);
            try {
              monitor.log('', `朋友圈上传-开始上传本地图片`);
              const { url } = await forwardingService.choiceImageUpload(localData);
              if (url) {
                forwardingService
                  .uploadImage(url)
                  .then((res) => {
                    monitor.log('', `朋友圈上传-图片发送后台成功`);
                    initData();
                    showModal && showModal('success');
                  })
                  .catch((error) => {
                    monitor.log('', `朋友圈上传-图片发送后台失败，error：${JSON.stringify(error)}`);
                    console.log(error);
                    message.closeLoading();
                    if (error.code === 'BIZ_SHARE_TIMELINE_REPEAT_SUBMIT') return;
                    showModal && showModal('fail');
                  });
              } else {
                message.closeLoading();
                showModal && showModal('fail');
                monitor.log('', `朋友圈上传-图片上传失败，未解析到可用的url， url：${url}`);
              }
            } catch (error) {
              message.closeLoading();
              console.log(error);
              showModal && showModal('fail');
              monitor.log('', `朋友圈上传-图片上传或发送失败，error： ${JSON.stringify(error)}`);
            }
          },
          fail(res) {
            monitor.log('', `读取本地图片错误，error：${JSON.stringify(res)}`);
            console.log(res);
            message.error('读取本地文件失败');
          }
        });
      }
    });
  };

  render() {
    return tools.isWeiXin() ? (
      <div className={styles.uploadWrapper} onClick={this.beginUpload}>
        {this.props.children}
        {this.createModal()}
      </div>
    ) : (
      <div className={styles.uploadWrapper}>
        {this.props.children}
        <input
          className={styles.uploadInput}
          type="file"
          accept="image/*"
          onChange={this.uploadChange}
        />
        {this.createModal()}
      </div>
    );
  }
}
