import React, {Component} from 'react';
import classNames from 'classnames';
import {Player} from 'video-react';

import styles from './special-detail.less';

import playIcon from '../../public/images/special/video-play.png';
import {tools} from '../../utils';

export default class SpecialTab extends Component {

    constructor(props) {
        super(props);
        this.isMoving = false;
        this.state = {
            currentTab: 'introduce', // 当前选中的tab页面 rule:活动规则 product:产品介绍
            fixedMenu: false,
            showPlayButton: true,
        };
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        if (this.isMoving) return false;
        const pageY = window.pageYOffset;
        let tabsTop = this.refs.specialtabs.offsetTop - pageY;

        let {fixedMenu} = this.state;
        fixedMenu = tabsTop < 0;
        let currentTab = 'introduce';

        if (pageY + 40 <= this.getPoint(this.introduceRef) + this.introduceRef.offsetHeight) {
            currentTab = 'introduce';
        } else if (pageY + 40 <= this.getPoint(this.catalogueRef) + this.catalogueRef.offsetHeight) {
            currentTab = 'catalogue';
        } else if (pageY + 40 <= this.getPoint(this.noticeRef) + this.noticeRef.offsetHeight) {
            currentTab = 'notice';
        }

        this.setState({
            fixedMenu,
            currentTab
        });
    };

    /**
     * 滚动条运动
     */
    move = iTarget => {
        window.scrollTo(0, iTarget);
    };

    /**
     * 切换tabs
     */
    handleTabsClick = (key, value) => {
        this.setState({
            [key]: value,
            fixedMenu: true,
        }, () => {
            let tabsTop = this.getPoint(this[`${value}Ref`]);
            if (this.state.fixedMenu) {
                this.move(tabsTop - 39);
            }
        });
    };

    /**
     * @description 获取元素距离顶部距离
     * @param obj
     * @return {number}
     */
     getPoint = (obj) => {
         let t = obj.offsetTop;
         while (obj = obj.offsetParent) {
             t += obj.offsetTop;
         }
         return t;
     };

    /**
     * @description 课程详情
     * @return {*}
     */
    renderIntroduce = () => {
        let {spData: {descVideoUrl, descImagesUrl, descVideoTitle}} = this.props;
        const {showPlayButton} = this.state;
        return <div>
            {descVideoUrl && <div className={styles.introduceVideo}>
                <div className={styles.title}>{descVideoTitle}</div>
                <div className={styles.video} onClick={() => {
                    this.videoRef.actions.play();
                    this.setState({
                        showPlayButton: false
                    });
                }}>
                    <Player
                        ref={ref => { this.videoRef = ref }}
                        poster={tools.getOssVideoPoster(descVideoUrl)}
                        src={descVideoUrl}
                    />

                    {showPlayButton && <div className={styles.play}>
                        <img src={playIcon} alt={'play'}/>
                    </div>}
                </div>
            </div>}
            {descImagesUrl.map((item, index) => {
                return <img key={`introduce-${index}`} src={item} alt="课程详情"/>;
            })}
        </div>;
    };


    /**
     * @description 课程目录
     * @return {*}
     */
    renderCatalogue = () => {
        let {spData: {directoryUrl}} = this.props;
        return directoryUrl.map((item, index) => {
            if (item) {
                return <img key={`catalogue-${index}`} src={item} alt="课程目录"/>;
            }
            return null;
        });
    };

    /**
     * @description 约课须知
     */
    renderNotice = () => {
        let {spData: {remarkUrl}} = this.props;
        return remarkUrl.map((item, index) => {
            return <img key={`notice-${index}`} src={item} alt="约课须知"/>;
        });
    };

    render() {
        const {currentTab, fixedMenu} = this.state;
        return (
            <div className={styles['special-tabs']} ref="specialtabs">
                <div
                    className={classNames(styles['special-tabs-nav'], 'clearfix', {
                        [styles['fixedMenu']]: fixedMenu,
                    })}
                >
                    <div
                        onClick={this.handleTabsClick.bind(null, 'currentTab', 'introduce')}
                        className={classNames(
                            styles['special-tabs-tab'],
                            {[styles['special-tabs-tab-active']]: currentTab === 'introduce'})}
                    >
                        课程详情
                    </div>
                    <div
                        onClick={this.handleTabsClick.bind(null, 'currentTab', 'catalogue')}
                        className={classNames(
                            styles['special-tabs-tab'],
                            {[styles['special-tabs-tab-active']]: currentTab === 'catalogue'}
                        )}>
                        课程目录
                    </div>
                    <div onClick={this.handleTabsClick.bind(null, 'currentTab', 'notice')}
                        className={classNames(
                            styles['special-tabs-tab'],
                            {[styles['special-tabs-tab-active']]: currentTab === 'notice'}
                        )}>
                        约课须知
                    </div>
                </div>
                {fixedMenu && <div className={styles.hold}/>}
                <div
                    className={styles['special-tabs-content']}
                >
                    <div className={styles['tabs-content-slide']}>
                        <div
                            className={classNames(
                                styles['special-tabs-tabpane'],
                                {[styles['special-tabs-tab-content-active']]: currentTab === 'introduce'}
                            )}
                            ref={(node) => { this.introduceRef = node }}
                        >
                            {this.renderIntroduce()}
                        </div>
                        <div
                            className={classNames(
                                styles['special-tabs-tabpane'],
                                {[styles['special-tabs-tab-content-active']]: currentTab === 'catalogue'}
                            )}
                            ref={(node) => { this.catalogueRef = node }}>
                            {this.renderCatalogue()}
                        </div>
                        <div
                            className={classNames(
                                styles['special-tabs-tabpane'],
                                {[styles['special-tabs-tab-content-active']]: currentTab === 'notice'}
                            )}
                            ref={(node) => { this.noticeRef = node }}>
                            {this.renderNotice()}
                        </div>
                    </div>
                </div>
                <div className={styles['special-tabs-footer']}/>
            </div >);
    }
}
