import React from 'react';
import { Route, Switch } from 'react-router-dom';

import history from './history';

import WechatAuth from '../views/auth/wechat-auth'; // 微信用户主动授权
import Booking from '../views/booking';
import miniBooking from '../views/minibooking';
import Promotion from '../views/promotion/promotion';
import PromotionDetail from '../views/promotion/promotion-detail';
import Survey from '../views/survey/survey';
import SurveyResult from '../views/surveyResult/survey-result';
import Home from '../views/home/home';
import Login from '../views/login/login';
import Register from '../views/register';
import Resetpwd from '../views/resetpwd';
import Remind from '../views/remind';
import Bonus from '../views/bonus/bonus';
import BonusCourse from '../views/bonus/bonus-course';
import BonusLog from '../views/bonusLog/bonusLog';
import Forwarding from '../views/forwarding/index';
import QRGroup from '../views/qr-wxgroup';

import Special from '../views/special/special';
import SpecialDetail from '../views/special/special-detail';
import SpecialBooking from '../views/special/special-booking';
import Nickname from '../views/nickname/nickname';
import Result from '../views/result';
/**
 * 路由配置
 */
const route = (
  <Switch>
    <Route key="auth" path="/auth" component={WechatAuth} />,
    <Route key="login" path="/login" component={Login} />,
    <Route key="register" path="/register" component={Register} />,
    <Route key="resetpwd" path="/resetpwd" component={Resetpwd} />,
    <Route key="booking" path="/booking" component={Booking} />,
    <Route key="minibooking" path="/minibooking" component={miniBooking} />,
    <Route key="resultBook" path="/resultBook" component={Result} />,
    <Route key="home" path="/home" component={Home} />,
    <Route key="promotion" path="/promotion" component={Promotion} />,
    <Route key="bonus" path="/bonus" component={Bonus} />,
    <Route key="bonus_course" path="/bonus_course/:bonusType" component={BonusCourse} />,
    <Route key="promotion_detail" path="/promotion_detail" component={PromotionDetail} />,
    <Route key="survey" path="/survey" component={Survey} />,
    <Route key="survey-result" path="/survey-result" component={SurveyResult} />,
    <Route key="remind" path="/remind" component={Remind} />,
    <Route key="special" path="/special" component={Special} />,
    <Route key="special_detail" path="/special_detail/:spId" component={SpecialDetail} />,
    <Route key="special_booking" path="/special_booking" component={SpecialBooking} />
    <Route key="logs" path="/logs" component={BonusLog} />,
    <Route key="nickname" path="/nickname" component={Nickname} />,
    <Route key="forwarding" path="/forwarding" component={Forwarding} />,
    <Route key="qr_wxgroup" path="/qr_wxgroup" component={QRGroup}></Route>
    <Route key="notFount" path="*" component={Home} />,
  </Switch>
);

export { history };

export default route;
