import { fetchUtils } from '../utils';
import { appConfig } from '../config';

const { baseDomain } = appConfig.domain;

/**
 * 查询带参数二维码
 */
export const getQRcode = () => {
  const url = baseDomain.concat('/activity/api/prize/course/cc/qrcode');
  return fetchUtils.get(url).then((res) => res.data);
};

/**
 * 获取非付费用户的课程相关内容
 */
export const getClassAboutNonPayment = () => {
  const url = baseDomain.concat('/usercenter/person/h5book/course/list');
  return fetchUtils.get(url).then((res) => res.data);
};
