import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';

import { DocumentTitle, checkUserAuth, WechatConfig } from '../../components';
import { tools } from '../../utils';

import CoinForwarding from './coin-forwarding';
// import appConfig from '../../config/app-config';

import styles from './index.less';
import failRubbit from '../../public/images/failRubbit.png';

const miniWxToken = tools.getMiniWxToken();
const bookingUrl = miniWxToken ? `/minibooking?miniWxToken=${miniWxToken}` : '/booking';

// 上传朋友圈截图首页
@inject(({ forwardingStore }) => {
  const { show } = forwardingStore.shareInfo;
  return {
    // 数据
    show,
    // 函数
    initData: forwardingStore.initData // 初始化数据接口
  };
})
@observer
@checkUserAuth({ support: 'all', mode: 'base' })
export default class Forwarding extends Component {
  componentDidMount() {
    this.props.initData();
  }

  // 点击去约课的跳转
  handleClick = () => {
    this.props.history.push(bookingUrl);
  };

  /**
   *刷新页面
   */
  handleRefresh = () => {
    window.location.reload();
  };

  contentShow = () => {
    const { show } = this.props;
    let content;
    switch (show) {
      case '':
        content = (
          <div className={styles.pageLoading}>
            <img
              src="https://resource.mmears.com/miniprogram/miniapp/fly.gif"
              className={styles.pageLoadingImg}
            ></img>
            <p className={styles.pageLoadingText}>魔力耳朵做孩子的大英雄...</p>
          </div>
        );
        break;
      case 'OK':
        content = <CoinForwarding></CoinForwarding>;
        break;
      case 'noPay':
        content = (
          <div className={styles.promptBox}>
            <div className={styles.promptItem}>
              <img className={styles.itemImg} src={failRubbit}></img>
              <div className={styles.itemText}>
                亲，只有参与过课程学习的正式付费学员才可以参与活动哦～
              </div>
            </div>
            <DocumentTitle title="提示"></DocumentTitle>
          </div>
        );
        break;
      case 'noCourse':
        content = (
          <div className={styles.promptBox}>
            <div className={styles.promptItem}>
              <img className={styles.itemImg} src={failRubbit}></img>
              <div className={styles.itemText}>
                亲，当月宝贝参与外教课学习或参加训练营才可以参与呢～
              </div>
              <div className={styles.itemBtn} onClick={this.handleClick}>
                去约课
              </div>
            </div>
            <DocumentTitle title="提示"></DocumentTitle>
          </div>
        );
        break;
      case 'error':
        content = (
          <div className={styles.nowifiwrapper}>
            <div className={styles.nowifibox} />
            <div className={styles.nowifitext}>数据竟然跑路了，刷新试试看</div>
            <div className={styles.nowifirefreshbutton} onClick={this.handleRefresh}>
              刷新
            </div>
            <DocumentTitle title="提示"></DocumentTitle>
          </div>
        );
        break;
    }
    return content;
  };

  render() {
    return (
      <div className={styles.forwarding}>
        {this.contentShow()}
        <WechatConfig showShare={false}></WechatConfig>
      </div>
    );
  }
}
