import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import classNames from 'classnames';

import SpecialTab from './special-tab';
import Banner from './banner';
import { WeiXinModal, SpecialModal, BirthdayModal } from './component';
import { message } from '../../utils';
import { specialService } from '../../service';

import styles from './special-detail.less';
import { BUTTON_TEXT, MODAL_KEY } from '../../constants/special';
import { checkUserAuth, DocumentTitle } from '../../components';

import * as specialTools from './tools';

@withRouter
@checkUserAuth({ support: 'all', mode: 'base' })
class SpecialDetail extends Component {
  constructor(props) {
    super(props);

    const spId = this.props.match.params.spId;

    this.state = {
      spId,
      spData: null,
      spDataId: 0,
      recommend_spid: '',
      currentspId: '', // 当前的专题课id 同上
      judgeText: '',
      ageLevel: '',
      modalKey: ''
    };
  }

  componentDidMount() {
    this.loadData();
  }

  /**
   * @description 隐藏模态框
   */
  clearModalKey = () => {
    this.setState({
      modalKey: ''
    });
  };

  /**
   * @description 渲染 模态框
   */
  renderModal = () => {
    const { judgeText, modalKey, recommendAgeLevel } = this.state;
    // 目前没有适合宝宝年龄的课程哦， 您可在魔力耳朵公众号进行咨询哦
    switch (modalKey) {
      case MODAL_KEY.NO_MATCH_CLASS: {
        return (
          <WeiXinModal
            header={
              <div>
                <p>
                  目前没有适合宝宝年龄的课程哦～请您在【魔力耳朵】公众号后台咨询，或拨打客服电话400-626-1626了解详情，客服时间10:00-22:00
                </p>
              </div>
            }
            onCancel={this.clearModalKey}
          />
        );
      }
      case MODAL_KEY.BIRTHDAY: {
        return (
          <BirthdayModal
            onCancel={this.clearModalKey}
            onOk={specialTools.handleUpdateBirthDay.bind(this)}
          />
        );
      }

      case MODAL_KEY.SPECIAL_CLASS_END: {
        return (
          <WeiXinModal
            header={
              <div>
                <p>专题课限时活动已结束啦，期待下次开放吧~</p>
              </div>
            }
            onCancel={this.clearModalKey}
          />
        );
      }
      case MODAL_KEY.RECOMMEND_CLASS: {
        return (
          <SpecialModal showCancelButton={true} onCancel={this.clearModalKey}>
            <div className={styles.recommendClass}>
              <p>
                啊哦～您的宝宝的年龄过{judgeText}，<br /> 推荐您去选择{recommendAgeLevel}
                岁的课程哦～
              </p>
              <div
                className={styles.btn}
                onClick={specialTools.handleJumpToSpecialDetail.bind(
                  null,
                  this.state.recommend_spid
                )}
              >
                去看看
              </div>
            </div>
          </SpecialModal>
        );
      }
    }
  };

  loadData = async () => {
    const { spId } = this.state;

    message.showLoading();
    Promise.all([
      specialService.getSpecialDetailById(spId),
      specialService.getSepcialButtonStatus()
    ])
      .then((res) => {
        message.closeLoading();
        console.log('res', res);
        this.setState({
          spData: res[0],
          spDataId: res[0].spId,
          buttonText: BUTTON_TEXT[res[1].button],
          buttonUrl: res[1].url
        });
      })
      .catch((error) => {
        message.error(error.msg || '加载失败');
      });
  };

  render() {
    const { spData, spDataId, spId, buttonText } = this.state;
    console.log(this.state);
    if (!spDataId) return null;

    return (
      <div className={styles.container}>
        <DocumentTitle title="专题课-约课" />
        {this.renderModal()}
        <Banner data={spData.courseBanners} />
        <div className={styles['special-info']}>
          <h3>{spData.zhName}</h3>
          <h4>{spData.enName}</h4>
          <p>适合年龄：{spData.ageLevel}岁</p>
        </div>
        <SpecialTab spData={spData} />
        <div className={classNames(styles['footer'])}>
          <button onClick={specialTools.handleJumpTo.bind(this, spId)}>{buttonText}</button>
        </div>
      </div>
    );
  }
}

export default SpecialDetail;
