import React from 'react';
import {BaseModal} from '../../../../components';
import PropTypes from 'prop-types';

import style from './style.less';
import image_close from '../../../../public/images/special/close.png';
import weepRabbit from '../../../../public/images/special/cry_rabbit.png';

/**
 * 专题课约课异常弹窗
 */
function SpecialModal(props) {
    const handelCancel = () => {
        const {onCancel} = props;
        onCancel && onCancel();
    };

    return (
        <BaseModal
            visible={true}
        >
            <div className={style.content}>
                <img src={weepRabbit} alt={''}/>
                {props.children}
                {props.showCancelButton && <div className={style.close} onClick={handelCancel}>
                    <img src={image_close} alt={''}/>
                </div>}
            </div>
        </BaseModal>
    );
}

SpecialModal.defaultProps = {
    onCancel: () => {},
    showCancelButton: false,
    children: null
};

SpecialModal.propTypes = {
    onCancel: PropTypes.func,
    showCancelButton: PropTypes.bool
};

export default SpecialModal;
