/**
 * 根据指定的order获取签名图片
 * @param order 资源序号
 */
export const getImgByOrder = order => {
    const url = mobileDomain.concat('/wx/generater/poster');
    const param = {order};
    return fetchUtils.post(url, param).then(({data: url, msg}) => {
        return {
            url,
            msg,
        };
    });
};

/**
 * 用户选择本地图片进行上传
 */
export const choiceImageUpload = async (base64Date) => {
    const url = bannerDomain.concat('/file/get/uploadinfodto');

    // 自定义文件名称
    const fileName = `${new Date().getTime()}.png`;

    // 获取oss配置参数
    const param = {
        ossFileType: FILE_TYPE.image,
        fileName: fileName
    };

    try {
        // 1. 获取图片上传的目标服务器和秘钥
        const {
            data: {ossKey, accessKeyId, accessKeySecret, securityToken, region, bucket, url: resUrl}
        } = await fetchUtils.post(url, param);

        monitor.log('', `海报 => OSS参数 ${JSON.stringify({
            ossKey,
            accessKeyId,
            accessKeySecret,
            securityToken,
            region,
            bucket
        })}`);
        // 初始化oss实例
        const client = new OSS({
            region: region,
            accessKeyId: accessKeyId,
            accessKeySecret: accessKeySecret,
            stsToken: securityToken,
            bucket: bucket
        });

        // base64转file对象
        const file = tools.dataURLtoFile(base64Date, fileName);
        const {
            res: {requestUrls}
        } = await client.put(ossKey, file);

        const [firstUrl] = requestUrls;
        monitor.log('', `海报 => 文件上传成功 ${JSON.stringify(firstUrl)}`);

        return new Promise((resolve, reject) => {
            // 图片资源加载完成
            let img = new Image();
            img.crossOrigin = 'Anonymous';
            img.src = resUrl;
            img.addEventListener('load', () => {
                monitor.log('', `海报 => 文件加载成功`);
                resolve({
                    url: `${resUrl}?v=${new Date().getTime()}`
                });
            });
            img.addEventListener('error', () => {
                monitor.log('', `海报 => 文件加载失败`);
                reject({
                    msg: '图片加载失败'
                });
            });
        });
    } catch (e) {
        console.error(e);
        monitor.log('', `海报 => 文件上传失败 ${JSON.stringify(e)}`);
    }
};
