import queryString from 'query-string';
import wx from 'weixin-js-sdk';
import atob from 'atob';
import monitor from './monitor';

// 日期格式化
Date.prototype.format = function(format) {
  const o = {
    'M+': this.getMonth() + 1,
    'd+': this.getDate(),
    'h+': this.getHours(),
    'H+': this.getHours(),
    'm+': this.getMinutes(),
    's+': this.getSeconds(),
    'q+': Math.floor((this.getMonth() + 3) / 3),
    S: this.getMilliseconds()
  };
  if (/(y+)/.test(format)) {
    format = format.replace(RegExp.$1, String(this.getFullYear()).substr(4 - RegExp.$1.length));
  }
  for (const k in o) {
    if (new RegExp('(' + k + ')').test(format)) {
      format = format.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(String(o[k]).length)
      );
    }
  }
  return format;
};

/**
 * 获取参数值
 * @param paramKey
 */
export const getQueryString = (paramKey) => {
  const args = {};
  let argsStr = window.location.search; // 获取URL参数字符串
  if (argsStr.length > 0) {
    argsStr = argsStr.substring(1);
    const nameValueArr = argsStr.split('&'); // 多参数
    for (let i = 0; i < nameValueArr.length; i++) {
      const pos = nameValueArr[i].indexOf('=');
      if (pos === -1) continue; // 如果没有找到就跳过
      const argName = nameValueArr[i].substring(0, pos); // 提取name
      const argVal = nameValueArr[i].substring(pos + 1); // 提取value
      args[argName] = unescape(argVal);
    }
    return args[paramKey];
  }
};

/**
 * 更新查询参数
 * @param uri
 * @param key
 * @param value
 * @returns {*}
 */
export const updateParamValue = (uri, key, value) => {
  const re = new RegExp('([?&])' + key + '=.*?(&|$)', 'i');
  const separator = uri.indexOf('?') !== -1 ? '&' : '?';
  if (uri.match(re)) {
    return uri.replace(re, '$1' + key + '=' + value + '$2');
  } else {
    return uri + separator + key + '=' + value;
  }
};

/**
 * 删除参数
 * @param url
 * @param key
 * @returns {*}
 */
export const deleteParam = (url, key) => {
  // 如果不包括此参数
  if (url.indexOf(key) === -1) return url;

  const arr_url = url.split('?');
  const base = arr_url[0];
  const arr_param = arr_url[1].split('&');
  let index = -1;
  for (let i = 0; i < arr_param.length; i++) {
    const paired = arr_param[i].split('=');
    if (paired[0] === key) {
      index = i;
      break;
    }
  }
  if (index === -1) {
    return url;
  } else {
    arr_param.splice(index, 1);
    return base + '?' + arr_param.join('&');
  }
};

/**
 * 判断是否是微信浏览器
 * @returns {boolean}
 */
export const isWeiXin = () => {
  // window.navigator.userAgent属性包含了浏览器类型、版本、操作系统类型、浏览器引擎类型等信息，这个属性可以用来判断浏览器类型
  return /micromessenger/i.test(navigator.userAgent);
};

/**
 * 是否是wap
 * @returns {boolean}
 */
export const isWap = () => {
  return /iphone|android|mobile|windows ce|miui|ipad|ipod/i.test(navigator.userAgent);
};

/**
 * 检查是否支持MpPay
 * MpPay 是什么鬼
 * @returns {boolean}
 */
export const supportMpPay = () => {
  if (/micromessenger\/(\S+)/i.test(navigator.userAgent)) {
    if (parseFloat(RegExp.$1) >= 5.0) {
      return true;
    }
  }
  return false;
};

// 设置标题
export const setTitle = (title) => {
  document.title = `魔力耳朵-${title}`;
};

/**
 * 判断是否是IOS系统
 * @returns {boolean}
 */
export const isIOS = () => {
  const agent = window.navigator.userAgent;
  return !!agent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
};

/**
 * 判断是否是安卓
 * @returns {boolean}
 */
export const isAndroid = () => {
  const agent = window.navigator.userAgent;
  return agent.indexOf('Android') > -1 || agent.indexOf('Adr') > -1;
};

/**
 * 判断是否是微信浏览器
 * @returns {boolean}
 */
export const isWeChat = () => {
  return /micromessenger/i.test(navigator.userAgent);
};

/**
 * 简单函数节流
 * @param action
 * @param delay
 * @returns {Function}
 */
export const throttle = function(action, delay) {
  let last = 0;
  return function() {
    const current = Number(new Date());
    if (current - last > delay) {
      action.apply(this, arguments);
      last = current;
    }
  };
};
/**
 * 简单函数防抖
 * @param action
 * @param delay
 * @returns {Function}
 */
export const debounce = function(action, delay = 300) {
  let timer = null;
  return function() {
    clearTimeout(timer);
    timer = setTimeout(() => {
      action.apply(this, arguments);
    }, delay);
  };
};

/**
 * @description 判断是否输入了表情
 * @param substring
 * @return {boolean}
 */
/* eslint-disable-next-line */
export function isEmojiCharacter(substring) {
  for (let i = 0; i < substring.length; i++) {
    const hs = substring.charCodeAt(i);
    if (0xd800 <= hs && hs <= 0xdbff) {
      if (substring.length > 1) {
        const ls = substring.charCodeAt(i + 1);
        const uc = (hs - 0xd800) * 0x400 + (ls - 0xdc00) + 0x10000;
        if (0x1d000 <= uc && uc <= 0x1f77f) {
          return true;
        }
      }
    } else if (substring.length > 1) {
      const ls = substring.charCodeAt(i + 1);
      if (ls === 0x20e3) {
        return true;
      }
    } else {
      if (0x2100 <= hs && hs <= 0x27ff) {
        return true;
      } else if (0x2b05 <= hs && hs <= 0x2b07) {
        return true;
      } else if (0x2934 <= hs && hs <= 0x2935) {
        return true;
      } else if (0x3297 <= hs && hs <= 0x3299) {
        return true;
      } else if (
        hs === 0xa9 ||
        hs === 0xae ||
        hs === 0x303d ||
        hs === 0x3030 ||
        hs === 0x2b55 ||
        hs === 0x2b1c ||
        hs === 0x2b1b ||
        hs === 0x2b50
      ) {
        return true;
      }
    }
  }
}

function getBase64Image(img) {
  const canvas = document.createElement('canvas');
  canvas.width = img.width;
  canvas.height = img.height;
  const ctx = canvas.getContext('2d');
  ctx.drawImage(img, 0, 0, img.width, img.height);
  const dataURL = canvas.toDataURL('image/png'); // 可选其他值 image/jpeg
  return dataURL;
}

/**
 * @description根据指定的url返回base64
 * @param url
 */
export function getUrlBase64(url) {
  return new Promise((resolve) => {
    const img = new Image();
    // img.crossOrigin = '*';  // 支持跨域图片
    img.setAttribute('crossOrigin', '*');
    img.src = `${url}?v=${Number(new Date())}`;
    img.onload = function() {
      const strBase64 = getBase64Image(img);
      resolve(strBase64);
    };
  });
}

/**
 * 获取url里面的参数
 * */
export function getSearchParam(url) {
  let paramString = '';
  const { query } = queryString.parseUrl(url);
  const { rk, rf, act, callback, miniWxToken } = query;

  if (rk) {
    paramString += `rk=${rk}&`;
  }
  if (rf) {
    paramString += `rf=${rf}&`;
  }
  if (act) {
    paramString += `act=${act}&`;
  }
  if (miniWxToken) {
    paramString += `miniWxToken=${miniWxToken}&`;
  }
  if (callback) {
    paramString += `callback=${encodeURIComponent(callback)}&`;
  }

  if (paramString) {
    paramString = paramString.substr(0, paramString.length - 1);
  }

  return paramString;
}

/**
 * @description 获取oss上的视频封面
 * @param url
 * @return {string}
 */
export function getOssVideoPoster(url) {
  return url + '?x-oss-process=video/snapshot,t_0000,f_jpg,w_0,h_0,m_fast';
}

/**
 * 加密手机号
 * */
export const encryptAccount = (account) => {
  const phoneReg = /^1[0-9]\d{9}$/;
  if (account) {
    if (phoneReg.test(account)) {
      return account.replace(/^(\d{3})\d{4}(\d{4})$/, '$1****$2');
    }
  }
  return account;
};

/**
 * 延迟promise
 * @param timeout 延迟时间, 单位毫秒
 * @returns {Promise}
 */
export function delay(timeout) {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve();
    }, timeout);
  });
}

/**
 * 判断是否小程序中 返回一个promise对象
 */
export function isMiniEnv() {
  const ua = navigator.userAgent.toLowerCase();
  let isMini = false;
  return new Promise(function(resolve, reject) {
    if (ua.match(/MicroMessenger/i) === 'micromessenger') {
      // ios的ua中无miniProgram，但都有MicroMessenger（表示是微信浏览器）
      wx.miniProgram.getEnv((res) => {
        if (res.miniprogram) {
          isMini = true;
          resolve(isMini);
        } else {
          resolve(isMini);
        }
      });
    } else {
      resolve(isMini);
    }
  });
}

/**
 * 检查当前页面是否是 内嵌到魔力耳朵小程序里的WebView
 * @returns {boolean}
 */
export const checkInWebView = () => {
  let inWebView = false;
  // 小程序跳转过来携带的wxtoken
  const { miniWxToken, callback } = queryString.parse(window.location.search);
  if (miniWxToken) {
    inWebView = true; // 当前页面内嵌在WebView
  }
  if (!miniWxToken && callback && callback.indexOf('http') > -1) {
    // 小程序内嵌h5 触发了跳转登录页面
    const { query } = queryString.parseUrl(decodeURIComponent(callback));
    if (query && query['miniWxToken']) {
      inWebView = true; // 当前页面内嵌在WebView
    }
  }
  return inWebView;
};

/**
 * 获取小程序中的url中miniWxtoken
 */
export function getMiniWxToken() {
  const { miniWxToken } = queryString.parse(window.location.search);
  return miniWxToken;
}

/**
 * base64转File
 * @param {*} dataurl
 * @param {*} filename
 */
export const dataURLtoFile = (dataurl, filename) => {
  dataurl = dataurl.replace(/\n/g, ''); // 删除空格/换行
  try {
    // 浏览器不支持File对象时候
    if (typeof File !== 'function') {
      return base64Toblob(dataurl, filename);
    }
    const arr = dataurl.split(',');
    const mime = arr[0].match(/:(.*?);/)[1];
    const bstr = atob(arr[arr.length - 1]);
    let n = bstr.length;
    const u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, { type: mime });
  } catch (error) {
    const errMessage = errorToString(error);
    monitor.log('', `base64转File失败:${errMessage}`);
    if (dataurl.indexOf(' ') > -1) {
      monitor.log('', `base64转File失败data有空格:`);
    }
    throw error;
  }
};
/**
 * base64转Blob
 * @param {*} dataurl
 * @param {*} filename
 */
export const base64Toblob = (dataurl, filename) => {
  const arr = dataurl.split(',');
  const binaryString = atob(arr[arr.length - 1]);
  const arrayBuffer = new ArrayBuffer(binaryString.length);
  const intArray = new Uint8Array(arrayBuffer);
  const mime = arr[0].match(/:(.*?);/)[1];
  for (let i = 0, j = binaryString.length; i < j; i++) {
    intArray[i] = binaryString.charCodeAt(i);
  }
  const data = [intArray];
  let result;
  try {
    result = new Blob(data, { type: mime });
  } catch (error) {
    window.BlobBuilder =
      window.BlobBuilder ||
      window.WebKitBlobBuilder ||
      window.MozBlobBuilder ||
      window.MSBlobBuilder;
    if (error.name === 'TypeError' && window.BlobBuilder) {
      const builder = new window.BlobBuilder();
      builder.append(arrayBuffer);
      result = builder.getBlob(mime);
    } else {
      throw new Error('base转blod失败，浏览器不支持', JSON.stringify(error));
    }
  }
  console.log('handleBase64Toblob', result);
  return result;
};

/**
 * 获取图片的blob格式
 * @param url 图片的url地址
 * @returns {Promise}
 */
export const getImageBlob = (url) => {
  const URL = window.URL || window.webkitURL; // 兼容处理

  return new Promise((resolve, reject) => {
    // 如果浏览器不支持 blob格式, 则使用原图片URL
    if (!URL) {
      // 加载图片
      const img = new Image();
      img.src = url;
      img.addEventListener('load', () => {
        resolve(url);
      });
      img.addEventListener('error', () => {
        reject({
          msg: '图片加载失败'
        });
      });
      return;
    }
    const xhr = new XMLHttpRequest();
    xhr.open('get', url, true);
    xhr.responseType = 'blob';
    xhr.onload = function() {
      if (this.status === 200) {
        const blob = this.response;
        const objectUrl = URL.createObjectURL(blob);
        resolve(objectUrl);
      }
    };
    xhr.onerror = function() {
      resolve(url);
    };
    xhr.send();
  });
};

/**
 * 字符串url解析
 * @param url
 */
export const parseUrl = (urlStr) => {
  if (!urlStr) return;
  const parse_url = /^(?:([A-Za-z]+):)?(\/{0,3})([0-9.\-A-Za-z]+)(?::(\d+))?(?:\/([^?#]*))?(?:\?([^#]*))?(?:#(.*))?$/;
  const result = parse_url.exec(urlStr);
  const names = ['href', 'protocol', 'scheme', 'hostname', 'port', 'pathname'];
  const url = {};
  for (let i = 0; i < names.length; i += 1) {
    url[names[i]] = result[i];
  }
  return url;
};

/**
 * 获取cookie
 * @param name
 * @returns {*}
 */
export const getCookie = (name) => {
  let arr;
  const reg = new RegExp('(^| )' + name + '=([^;]*)(;|$)');
  if ((arr = document.cookie.match(reg))) {
    return decodeURI(arr[2]);
  } else {
    return null;
  }
};

/**
 * 设置cookie
 * @param name
 * @param value
 * @param expireDays
 * @param domain
 */
export const setCookie = (name, value, expireDays, domain = window.location.hostname) => {
  const expiresTime = new Date();
  expiresTime.setTime(expiresTime.getTime() + expireDays * 24 * 60 * 60 * 1000);

  document.cookie = `${name}=${encodeURIComponent(
    value
  )};expires=${expiresTime.toUTCString()};domain=${domain};path=/`;
};

/**
 * error对象转string
 * @param {*} error error对象
 */
export const errorToString = (error) => {
  let errMessage;
  try {
    errMessage = JSON.stringify(error, Object.getOwnPropertyNames(error));
  } catch {
    errMessage = JSON.stringify(error);
  }
  return errMessage;
};
