import React, { Component } from 'react';

import { message } from '../../utils';
import { userService } from '../../service';
import { connBasicLayout } from '../layout';
import { WechatConfig, checkUserAuth } from '../../components';

import styles from './qr-wxgroup.less';

/**
 * 微信群二维码
 */
@connBasicLayout({ useFooter: false, backgdName: '' })
@checkUserAuth({ support: 'all', mode: 'base' })
export default class QRGroup extends Component {
  state = {
    chatRoomQrCode: '', // 二维码
    chatRoomName: '', // 群名称
    roomNameList: [], // 群名称 如果数组为空则取chatRoomName
    chatRoomImageUrl: '', // 群头像

    errorData: null // 异常状态 {code: '异常码', message: '异常信息'}
  };

  componentDidMount() {
    this.loadData();
  }

  loadData = async () => {
    message.showLoading();
    try {
      const {
        chatRoomQrCode,
        chatRoomName,
        chatRoomImageUrl,
        roomNameList
      } = await userService.getWxQrCode();

      this.setState({
        chatRoomQrCode,
        chatRoomName,
        chatRoomImageUrl,
        roomNameList
      });
      message.closeLoading();
    } catch (e) {
      console.log(e);
      const { code, msg } = e;
      const errorData = {
        code,
        message: msg
      };
      this.setState({ errorData });
      message.closeLoading();
    }
  };

  renderContent = () => {
    const { errorData, chatRoomQrCode, chatRoomImageUrl, roomNameList, chatRoomName } = this.state;
    if (!errorData && chatRoomQrCode === '') return null;

    let element = null;
    if (errorData) {
      element = (
        <div className={styles.qrError}>
          <p>{errorData.message}</p>
        </div>
      );
    } else {
      element = (
        <div className={styles.qrContent}>
          <div className={styles.content}>
            <div className={styles.title}>
              <div className={styles.avatar}>
                <img src={chatRoomImageUrl} />
              </div>
              <div className={styles.chatName}>
                {roomNameList && roomNameList.length ? (
                  <>
                    <p>
                      {roomNameList[0]} {roomNameList[1]}
                    </p>
                    <p>{roomNameList[2]}</p>
                  </>
                ) : (
                  <p>{chatRoomName}</p>
                )}
              </div>
            </div>
            <div className={styles.qrCode}>
              <img src={chatRoomQrCode} />
            </div>
            <p>长按识别二维码进入班级</p>
          </div>
        </div>
      );
    }
    return element;
  };

  render() {
    return (
      <div id={styles.qrWrap}>
        <div className={styles.qrBackground}>{this.renderContent()}</div>
        <div className={styles.qrBottom} />

        <WechatConfig showShare={false} />
      </div>
    );
  }
}
