import { fetchUtils } from '../utils';
import { appConfig } from '../config';

const mobileDomain = appConfig.domain.mobileDomain;
const baseDomain = appConfig.domain.baseDomain;

/**
 * 获取正常魔力币数量
 */
export const getBonusPoint = () => {
  const url = mobileDomain.concat('/api/bonus/point/value');
  return fetchUtils.get(url).then((res) => res.data);
};

/**
 * 获取冻结魔力币数量
 */
export const getBonusFreezePoint = () => {
  const url = mobileDomain.concat('/api/bonus/freezepoint/value');
  return fetchUtils.get(url).then((res) => res.data);
};

/**
 * 获取正常魔力币列表
 */
export const getBonusPointList = ({ tradeType, tradeDate, pageIndex, pageSize }) => {
  const url = mobileDomain.concat('/api/bonus/point/detail/logs');

  // 转换交易类型数据结构
  const [type] = tradeType.slice();

  const param = {
    tradeType: type,
    tradeDate: `${tradeDate}${tradeDate && '-01'}`,
    _pn: pageIndex,
    _pi: pageSize
  };
  return fetchUtils.post(url, param).then((res) => res.data);
};

/**
 * 获取冻结魔力币列表
 * @param pageIndex 分页索引
 * @param pageSize 每页显示的长度
 */
export const getBonusFreezePointList = (pageIndex, pageSize) => {
  const url = mobileDomain.concat('/api/bonus/freezepoint/logs');
  const param = {
    _pn: pageIndex,
    _pi: pageSize
  };
  return fetchUtils.get(url, param).then((res) => res.data);
};

