import React from 'react';
import {Toast, Modal} from 'antd-mobile';
import rabbitSrc from '../public/images/rabbit.png';

/**
 * 简单封装 Toast
 * 一种轻量级反馈/提示，可以用来显示不会打断用户操作的内容，适合用于页面转场、数据交互的等场景中
 * 有 Icon 的 Toast，字数为 4-6 个；没有 Icon 的 Toast，字数不宜超过 14 个
 *
 * Toast.info(content, duration, onClose, mask)
 * duration = 0 时，onClose 无效，toast 不会消失；隐藏 toast 需要手动调用 hide
 */

const alert = Modal.alert;

// /**
//  * 确认对话框指针, 可以通过该指针主动关闭确认对话框
//  */
// let confirmInstance = null;

/**
 * 兔子Logo
 */
const rabbit = (
    <div className="modal-rabbit-wrapper">
        <img src={rabbitSrc} style={{width: '100%'}} className="modal-rabbit" alt="魔力耳朵"/>
    </div>
);

const message = {
    /**
     *  消息
     */
    info: (content, callback) => {
        Toast.info(content, 1.5, callback);
    },
    /**
     *  成功信息
     */
    success: (content, callback) => {
        Toast.success(content, 1.5, callback);
    },
    /**
     *  错误信息
     */
    error: (content, callback) => {
        Toast.info(content, 1.5, callback);
    },
    /**
     *  离线信息
     */
    offline: (content, callback) => {
        Toast.offline(content, 1.5, callback);
    },

    /**
     * 确认消息框
     * @param message 确认消息
     * @param okFun 确认后的回调函数
     * @param okText 确认按钮文本
     * @param cancelFun 取消后的回调函数
     * @param cancelText 取消按钮文本
     *
     * am-modal-content
     */
    confirm: ({message, okFun, okText = '确认', cancelFun, cancelText = '取消'}) => {
        // confirmInstance =
        alert(rabbit, message, [
            {
                text: cancelText, onPress: () => {
                    cancelFun && cancelFun();
                },
                style: 'default'
            },
            {
                text: okText, onPress: () => {
                    okFun && okFun();
                }
            },
        ]);
    },
    /**
     *  打开Loading, 打开之后需要手动关闭
     */
    showLoading: (content = '加载中') => {
        Toast.loading(content, 0);
    },
    /**
     *  关闭Loading
     */
    closeLoading: () => {
        Toast.hide();
    },
};

export default message;
