import React from 'react';
import classNames from 'classnames';
import {ListView, List, PullToRefresh} from 'antd-mobile';
import PropTypes from 'prop-types';
// import moment from 'moment';

import {BONUS_LOG_TYPE} from '../../constants';

import styles from './bonusLog.less';

// 魔力币列表组件
const BonusList = (props) => {
    const {data, loading, pageSize, onChange, height, freeze, refreshing, onRefresh} = props;
    /**
     * 列表加载数据loading
     */
    const renderFooterLoading = () => {
        return (
            <div className={classNames(styles['bonus-list-footer'])}>
                <span>数据加载中...</span>
            </div>
        );
    };

    /**
     * 支出数量
     */
    const renderRowDesc = (num) => {
        if (num > 0) {
            return (
                <div className={classNames(styles['bonus-row-desc'])}>
                    <span style={{color: 'rgb(236, 106, 65)'}}>+{num}</span>
                </div>
            );
        }
        return (
            <div className={classNames(styles['bonus-row-desc'])}>
                <span>{num}</span>
            </div>
        );
    };

    /**
     * 渲染下拉刷新
     */
    const renderPullRefresh = () => {
        return (
            <PullToRefresh
                damping={120}
                refreshing={refreshing}
                onRefresh={onRefresh}
                distanceToRefresh={window.devicePixelRatio * 15}
            />
        );
    };

    /**
     * 渲染每条数据
     */
    const renderRow = (row) => {
        if (freeze) {
            return (
                <div className={classNames(styles['bonus-list-row-detail'])}>
                    <List renderHeader={null} className="list">
                        <List.Item multipleLine>
                            {row['action'] && row['action'].indexOf('微信') !== -1 ? (<span className={classNames(styles['bonus-row-title'])}>
                                {row['action'] ? row['action'] : ''}
                            </span>) :
                                (<span className={classNames(styles['bonus-row-title'])}>
                                    {/* {`推荐\"${row['wxNickName'] ? row['wxNickName'] : '魔力宝贝'}(${row['studentId'] ? row['studentId'] : ''})\"${row['action'] ? row['action'] : ''}`} */}
                                    {row['action']}
                                </span>)}
                            <List.Item.Brief>
                                <span className={classNames(styles['bonus-row-subtitle'])}>{BONUS_LOG_TYPE.INVALID !== row['bonusLogStatus'] && `剩余冻结日期: ${row['remainTime']}天`}</span>
                                {
                                    BONUS_LOG_TYPE.INVALID !== row['bonusLogStatus'] ?
                                        <div className={classNames(styles['subtitle'])}>
                                            <span className={classNames(styles['subtitle-left'])}></span>
                                            <span className={classNames(styles['subtitle-right'])}>{row['bonusNum']}魔力币</span>
                                        </div> :
                                        <div className={classNames(styles['subtitle'])}>
                                            {row['action'] && row['action'].indexOf('微信') !== -1 ? (<span className={classNames(styles['subtitle-right-invalid'])}>已失效</span>)
                                                : (<span className={classNames(styles['subtitle-right-invalid'])}>已退费</span>)}

                                        </div>
                                }
                            </List.Item.Brief>
                        </List.Item>
                    </List>
                </div>
            );
        }

        return (
            <div className={classNames(styles['bonus-list-row'])}>
                <List renderHeader={null} className="list">
                    <List.Item multipleLine extra={renderRowDesc(row['bonusPointNum'])}>
                        <span className={classNames(styles['bonus-row-title'])}>
                            {`${row['action'] ? row['action'] : ''}`}
                        </span>
                        <List.Item.Brief>
                            <span className={classNames(styles['bonus-row-subtitle'])}>{row['recordTime']}</span>
                        </List.Item.Brief>
                    </List.Item>
                </List>
            </div>
        );
    };

    return (
        <div className={classNames(styles['bonus-log-list'])}>
            <ListView
                dataSource={data}
                renderFooter={loading && renderFooterLoading}
                renderRow={renderRow}
                initialListSize={10}
                style={{
                    height: height,
                    overflow: 'auto',
                }}
                pageSize={pageSize}
                pullToRefresh={renderPullRefresh()}
                scrollRenderAheadDistance={300}
                onEndReached={onChange}
                onEndReachedThreshold={10}
            />
        </div>
    );
};

// 设置属性检查
BonusList.propTypes = {
    refreshing: PropTypes.bool, // 是否下拉刷新
    freeze: PropTypes.bool, // 类型标记
    data: PropTypes.any, // 列表数据
    loading: PropTypes.bool, // 加载loading
    pageSize: PropTypes.number, // 每页长度
    height: PropTypes.number.isRequired, // 高度
    onChange: PropTypes.func.isRequired, // 上拉加载
    onRefresh: PropTypes.func.isRequired, // 下拉刷新
};

// 设置默认属性值
BonusList.defaultProps = {
    refreshing: false,
    freeze: false,
    data: [], // 默认为空
    loading: true, // 默认加载中
    pageSize: 10, // 默认20条
};

export default BonusList;
