import React from 'react';
import { DatePicker } from 'antd-mobile';
import moment from 'moment';
import querystring from 'query-string';

import RadioBtnGroup from '../../components/radio/radio-btn-group';

import { checkUserAuth, InfoModal, WechatConfig } from '../../components';
import { connBasicLayout } from '../layout';

import { ENGLISH_LEVEL, GRADE_TYPE, IDENTITY_TYPE } from '../../constants/survey';

import { tools, monitor, logger } from '../../utils';
import { userService } from '../../service';
import styles from './index.less';
import className from 'classnames';

/**
 * 分享标题
 * @type {{title: string}}
 */
const shareOptions = {
  title: '魔力耳朵-课程分级调查',
  desc: '为了宝贝更好融入课堂, 需要进行一个简单调查哦~'
};
const dataType = 'funnel';
const CustomChildren = ({ extra, onClick, children }) => (
  <div
    onClick={onClick}
    className={className(styles.dataPicker, { [styles.active]: extra !== '选择宝贝生日' })}
  >
    {children}
    <span>{extra}</span>
  </div>
);

/**
 * 定级页面
 */
@connBasicLayout()
@checkUserAuth({ support: 'all', mode: 'base' })
export default class Survey extends React.Component {
  constructor(props) {
    super(props);
    this.urlParams = querystring.parse(location.search);
    this.state = {
      identityType: '', // 学员身份
      gradeType: '', // 年级
      proficiencyType: '', // 英语水平
      nickName: '',
      birthDay: '',
      visible: false,
      errorMsg: ''
    };
    this.allow = true;
  }

  componentDidMount() {
    tools.setTitle('课程分级调查');
    // monitor.logMoreParam('', '浏览-课程分级调查');
    logger.info({
      message: '浏览-课程分级调查',
      dataType
    });
  }

  transferSetState = (type) => (value) => {
    let name = '';
    switch (type) {
      case 'identityType':
        name = '学员身份';
        break;
      case 'proficiencyType':
        name = '英语水平';
        break;
      case 'gradeType':
        name = '年级';
        break;
      case 'birthDay':
        name = '生日';
        break;
      case 'nickName':
        name = '英文名';
        break;
      default:
        name = '';
        break;
    }
    monitor.logMoreParam('', `课程分级调查-点击了${name}`);
    this.setState(() => {
      const updateState = {
        [type]: value
      };
      if (type === 'identityType') {
        updateState.gradeType = '';
      }
      return updateState;
    });
  };

  handleSubmit = async () => {
    if (!this.allow) return;
    this.allow = false;
    monitor.logMoreParam('', '点击提交定级信息');
    let isPassing = true;
    const { visible, errorMsg, ...rest } = this.state;
    let errMsg = '';

    for (const item in rest) {
      if (rest[item] === '' || rest[item] === undefined) {
        isPassing = false;
      }
    }

    if (isPassing) {
      if (!/^[a-zA-Z\s]+$/g.test(rest.nickName.trim()) || rest.nickName.trim().length < 2) {
        isPassing = false;
        errMsg = '请输入2-20位英文字母，单词之间请以空格隔开，不要输入汉字、数字、符号、表情哦~';
      }
    }

    if (isPassing) {
      const payload = {
        ...rest,
        nickName: rest.nickName.trim(),
        birthDay: moment(rest.birthDay).format('YYYY-MM-DD')
      };
      userService
        .surveySubmit(payload)
        .then((res) => {
          if (res.code === 'OK') {
            logger.info({
              message: `定级信息提交成功:${JSON.stringify(res)}`,
              dataType
            });
            // monitor.logMoreParam('', `定级信息提交成功:${JSON.stringify(res)}`);
            this.allow = true;
            // this.props.history.replace(`/survey-result?${tools.getSearchParam(window.location.href)}`);
            this.props.history.replace(
              `/booking?from=register${
                tools.getSearchParam(window.location.href)
                  ? '&' + tools.getSearchParam(window.location.href)
                  : ''
              }`
            );
          } else {
            monitor.logMoreParam('', `定级信息提交失败:${JSON.stringify(res)}`);
            this.allow = true;
            // 弹窗提示 错误
            this.setState({
              visible: true,
              errorMsg: res.msg
            });
          }
        })
        .catch((error) => {
          monitor.logMoreParam('', `定级信息提交定级失败:${JSON.stringify(error)}`);
          this.allow = true;
          this.setState({
            visible: true,
            errorMsg: error.msg
          });
        });
    } else {
      monitor.log('', '定级信息未填写完整');
      this.allow = true;
      // document.querySelectorAll('input').forEach(element => element.blur && element.blur());
      // 弹窗提示
      this.setState({
        visible: true,
        errorMsg: errMsg || '您还有未选择或未填写的栏目'
      });
    }
  };

  render() {
    const { identityType, birthDay, gradeType, proficiencyType, visible, errorMsg } = this.state;
    return (
      <div className={this.urlParams.from === 'landing' ? styles.congratulation : styles.survey}>
        <header />
        <InfoModal
          headerType="weep"
          visible={visible}
          cancelVisible={false}
          okText="我知道了"
          onOk={() => this.setState({ visible: false })}
        >
          {errorMsg}
        </InfoModal>
        <div className={styles.container}>
          <div className={styles.card}>
            <div className={styles.title}>宝贝英语情况</div>
            <div className={styles.subtitle}>身&nbsp;份</div>
            <RadioBtnGroup
              dataSource={IDENTITY_TYPE}
              onChange={this.transferSetState('identityType')}
              value={identityType}
            />
            <div className={styles.subtitle}>年&nbsp;级</div>
            <RadioBtnGroup
              dataSource={GRADE_TYPE[identityType] || GRADE_TYPE['Infant']}
              value={gradeType}
              onChange={this.transferSetState('gradeType')}
            />
            <div className={styles.subtitle}>英语水平</div>
            <RadioBtnGroup
              dataSource={ENGLISH_LEVEL}
              value={proficiencyType}
              onChange={this.transferSetState('proficiencyType')}
            />
            <em className={styles.cut} />
            <div className={className(styles.title, styles.titleShort)}>宝贝基本情况</div>
            <div className={styles.flexItem}>
              <p className={styles['subtitle-left']}>生&nbsp;日</p>
              <div className={styles['birth-input']}>
                <DatePicker
                  maxDate={new Date()}
                  mode="date"
                  extra={<span className={styles.choseBirthday}>选择宝贝生日</span>}
                  format="YYYY-MM-DD"
                  value={birthDay}
                  className={styles.datePicker}
                  onChange={this.transferSetState('birthDay')}
                >
                  <CustomChildren />
                </DatePicker>
              </div>
            </div>
            <div className={styles.flexItem}>
              <p className={styles['subtitle-left']}>英文名字</p>
              <div className={styles['name-input']}>
                <input
                  type="text"
                  onChange={(e) => this.transferSetState('nickName')(e.target.value)}
                  maxLength={20}
                  placeholder="请输入名字"
                />
              </div>
            </div>
            <button className={styles.submitBtn} onClick={this.handleSubmit}>
              下一步：选择上课时间
            </button>
          </div>
        </div>
        <WechatConfig showShare={true} shareOptions={shareOptions} />
      </div>
    );
  }
}
