import {fetchUtils} from '../utils';
import {appConfig} from '../config';

const {apiDomain, baseDomain} = appConfig.domain;
const specialDomain = apiDomain.concat('/activity/api/sp/config');

/**
 * 查询专题课列表
 */
export const getSpecialList = () => {
    const url = specialDomain.concat('/courseList');
    return fetchUtils.get(url).then(res => res.data);
};

/**
 * 查询专题课详情
 */
export const getSpecialDetailById = (spCourseId) => {
    const url = specialDomain.concat('/courseDetail');
    const param = {
        spCourseId
    };
    return fetchUtils.get(url, param).then(res => res.data);
};


/**
 * @description 判断列表按钮状态
 */
export const getSepcialButtonStatus = () => {
    const url = specialDomain.concat('/checkRemainder');
    return fetchUtils.get(url).then(res => res.data);
};

/**
 * @description 判断学生是否填写过年龄和 年龄是否符合本专题课
 * EXIST_JUDGE = true 有年龄/false 无年龄
 * AGE_SATISFY_JUDGE = true 在年龄范围内 跳转到约课页面/  false 不在年龄内 应该取查询推荐课程
 * @param spCourseId 专题课id
 */
export const getSepcialStudentStatus = (spCourseId) => {
    const url = specialDomain.concat('/ageFilter');
    const params = {
        spCourseId
    };
    return fetchUtils.get(url, params).then(res => res.data);
};

/**
 * @description 查询学生推荐专题课
 * @param spCourseId 专题课id
 */
export const getRecommendCourse = (spCourseId) => {
    const url = specialDomain.concat('/recommendCourse');
    const params = {
        spCourseId
    };

    return fetchUtils.get(url, params).then(res => res.data);
};

/**
 * @description 保存学生生日
 * @param year 年
 * @param month 月
 */
export const updateBirthday = (year, month) => {
    const url = baseDomain.concat('/usercenter/person/updateAge');
    const params = {
        month,
        year,
    };
    return fetchUtils.get(url, params).then(res => res.data);
};

/**
 * @description 专题课约课
 * @param spId 专题课id
 * @param courseArr 预约的专题课列表
 */
export const specialBooking = (spId, courseArr) => {
    const url = baseDomain.concat('/usercenter/specialbook');
    const params = {
        specialId: spId,
        batchBookCourseDTOS: courseArr
    };
    return fetchUtils.postJson(url, params).then(res => res.data);
};
